import { Injectable } from '@angular/core';
import { MetadataService } from './metadata.service';

@Injectable()
export class RangeMapMetadataService extends MetadataService {

  shapeFile;

  specificJsonMap = {
    product_type: { value: () => this.product_type, property: 'product_type' },

    taxonomic_class: { value: () => this.class, property: 'class' },

    author_display: { value: () => this.author_display, property: 'author_display' },
    citation_formatted: { value: () => this.citation_formatted, property: 'citation_formatted' },
    doi: { value: () => this.doi, property: 'doi' },

    geometry_processing: {value: () => this.geometry_processing, property: 'geometry_processing' },
    geospatial_scope: { value: () => this.geospatial_scope, property: 'geospatial_scope' },
    countries: { value: () => this.countries, property: 'countries' },

    endemic_info: { value: () => this.endemic_info, property: 'endemic_info' },

    isbn: { value: () => this.isbn, property: 'isbn' },
    keywords: { value: () => this.keywords, property: 'keywords' },
    description: { value: () => this.abstract, property: 'abstract' },
    notes: { value: () => this.notes, property: 'notes' }
  };

  countries = '';
  endemic_info = '';
  temporal_unit = '';
  geometry_processing = '';
  geospatial_area_km2 = '';
  geospatial_scope = '';
  geospatial_site_details = '';
  inventory_compilation_both = '';
  product_type = 'range';
  scientific_name = '';
  season = '';

  fromOverview(dataset_id, overview: any) {
    super.fromOverview(dataset_id, overview, this.specificJsonMap);
    if(typeof this.release_date === "string" && this.release_date !== 'null' && this.release_date.length > 0) {
      this.release_policy = this.set_release_date;
    } else {
      this.release_policy = this.no_release_date;
    }
  }

  createEmpty (taxaService, dataset_title) {
    this.dataset_title = dataset_title;
    this.create(taxaService);
    this.release_policy = this.no_release_date;
  }

  requiredMetadataIsReady () : boolean {
    return super.requiredMetadataIsReady();
  }

  isReady() {
    return this.requiredMetadataIsReady()
      && this.releasePolicyReady();
  }

}



