import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { RangeMapService } from '../../services/range-map.service';

interface FinalizeData {
    dataset_id: string;
}

@Component({
  selector: 'mol-range-map-finalize-modal',
  templateUrl: './range-map-finalize-modal.component.html',
  styleUrls: ['./range-map-finalize-modal.component.scss']
})
export class RangeMapFinalizeModalComponent implements OnInit {

    state: 'ready' | 'finalizing' | 'error' = 'ready';
    error: string = null;

  constructor(
    public dialogRef: MatDialogRef<RangeMapFinalizeModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: FinalizeData,
    public rangeMapService: RangeMapService
  ) { }

  ngOnInit() {
  }

  finalize () {
    this.state = 'finalizing';
    this.rangeMapService.done().subscribe(result => {
        if (result[0] && result[0].error) {
            this.state = 'error';
            this.error = result[0].error;
        } else {
            this.dialogRef.close();
        }
    }, error => {
        this.error = JSON.parse(error._body)[0].error;
        this.state = 'error';
    });
  }

  cancel () {
    this.dialogRef.close();
  }

}



