import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material';

import { RangeMapFinalizeModalComponent } from '../../components/range-map-finalize-modal/range-map-finalize-modal.component';

@Component({
  selector: 'mol-range-map-finalize',
  templateUrl: './range-map-finalize.component.html',
  styleUrls: ['./range-map-finalize.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class RangeMapFinalizeComponent implements OnInit {

  dataset_id: string = "";

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public dialog: MatDialog
  ) {
    this.dataset_id = this.route.snapshot.params.dataset_id;
  }

  ngOnInit() {
  }

  backToOverview() {
      this.router.navigate(['dataset', this.dataset_id]);
  }

  editMetadata () {
      this.router.navigate(['range-map/metadata', this.dataset_id]);
  }

  finalizeDataset () {
    this.dialog.open(RangeMapFinalizeModalComponent, {
      width: '400px',
      height: '300px',
      disableClose: true,
      data: {
        dataset_id: this.dataset_id
      }
    });
  }

  toUpdateTaxonomy() {
    this.router.navigate(['taxonomy-update', this.dataset_id]);
  }

}



