import { TCSearch, TaxonName } from '../../services/taxa.service';
import { normalizeSciName } from '../../services/util.service';


export class TaxonConcept {
  verbatimSciName: string = '';   // Scientific name from the original dataset before any edits
  startingSciName: string = '';   // Scientific name currently in the database
  newSciName: string = '';        // The updated scientific name
  exactTcSearch: TCSearch[] = []; // Results of tcSearch API call
  approxTcSearch: TCSearch[];     // Results of approxTcSearch API call
  taxonomyId: number;             // Current taxonomy
  tcid: string;
  isBusy = false;
  isEdited = false;
  isVerified = false;
  isSelected = false;

  constructor(taxonName: TaxonName) {
    this.verbatimSciName = taxonName.verbatim_scientificname;
    this.startingSciName = normalizeSciName(taxonName.scientificname);
    this.newSciName = this.startingSciName;
    this.tcid = taxonName.tc_id;
  }

  get label(): string {
    let label = this.verbatimSciName;
    if (this.verbatimSciName !== this.newSciName && this.newSciName) {
      label = `${this.verbatimSciName} -> ${this.newSciName}`;
    }
    return `${label} (${this.filterMatches().length})`;
  }

  filterMatches(): TCSearch[] {
    return this.filterExactMatches().concat(this.filterApproxMatches());
  }

  filterApproxMatches(): TCSearch[] {
    if (!this.approxTcSearch) { return []; }
    return this.approxTcSearch.filter(match => +match.taxonomyid == this.taxonomyId);
  }

  filterExactMatches(): TCSearch[] {
    return this.exactTcSearch.filter(match => +match.taxonomyid == this.taxonomyId);
  }

  findAuthbyName() {
    let auth = this.filterExactMatches().find(a => a.name === this.newSciName);
    if (!auth) {
      auth = this.filterApproxMatches().find(a => a.name === this.newSciName);
    }
    return Object.assign({tcid: null}, auth);
  }

  update(taxonomyId) {
    this.taxonomyId = +taxonomyId;
    this.isVerified = false;
    this.tcid = null;

    if (this.isExactMatch()) { return; }
    if (this.isApproxMatch()) { return; }

    // No match
    this.isVerified = false;
    this.isSelected = false;
  }

  private isExactMatch() {
    let matches = this.filterExactMatches();
    if (! matches.length) { return false; }

    this.newSciName = matches[0].name;
    this.tcid = matches[0].tcid;
    this.isEdited = false;
    this.isVerified = true;
    this.isSelected = false;

    return true;
  }

  private isApproxMatch() {
    const matches = this.filterApproxMatches();
    if (! matches.length) { return false; }

    let match = matches.find(tc => this.newSciName.startsWith(tc.name)) || matches[0];

    this.newSciName = match.name;
    this.tcid = match.tcid;
    this.isVerified = false;
    this.isSelected = true;
    this.isEdited = false;
    return true;
  }

  harmonize() {
    this.isSelected = false;
    this.isVerified = true;
    this.newSciName = this.newSciName || this.startingSciName;
    this.tcid = this.findAuthbyName().tcid;
  }

  unharmonize() {
    this.isSelected = false;
    this.isVerified = false;
    this.tcid = null;
  }

  approxSearch() {
    if (!this.isVerified && (!this.approxTcSearch || this.isEdited)) {
      return this.newSciName || this.verbatimSciName;
    }
    return null;
  }

}



