import { Component, OnInit, ElementRef, ViewChild, Input, NgZone } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { AuthenticationService } from '../../services/authentication.service';
import { DatasetService } from '../../services/dataset.service';
import { MembersService, Member, ResourceMember } from '../../services/members.service';
import { PermissionsService, Permission } from '../../services/permissions.service';
import { UserService, User } from '../../services/user.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/forkJoin';
import { toggleNgIf } from '../../services/animations.service';
import { SelectUsersModalComponent } from '../select-users-modal/select-users-modal.component';
import { MatDialog } from '@angular/material';


class ResourceMemberEdit {
  static resource_id: string;
  member_id: string;
  name: string;
  permission_id: number;
  oldPermissionId?: number;
  metadataService: any;

  constructor() {}

  fromMember(member: ResourceMember) {
    this.member_id = member.member_id;
    this.name = member.name;
    this.permission_id = member.permission_id;
    this.oldPermissionId = member.permission_id;
    return this;
  }

  build(user: User, permission: Permission) {
    this.member_id = user.user_id;
    this.name = user.name;
    this.permission_id = permission.permission_id;
    return this;
  }
}

@Component({
  selector: 'mol-display-dataset-acl',
  templateUrl: './display-dataset-acl.component.html',
  styleUrls: ['./display-dataset-acl.component.css'],
  animations: toggleNgIf
})
export class DisplayDatasetAclComponent implements OnInit {

  @Input() overview: any;
  @Input() metadataService: any;

  dataset_id = '';
  members: ResourceMemberEdit[];
  permissions: Permission[];
  isReady = false;
  users: User[];
  status: 'busy' | 'error' | 'success' = null;
  disabled = '';

  constructor(
    public dialog: MatDialog,
    private route: ActivatedRoute,
    private authentication: AuthenticationService,
    private router: Router,
    private memberService: MembersService,
    private datasetService: DatasetService,
    private permissionsService: PermissionsService,
    private userService: UserService,
    private zone: NgZone,
  ) { }

  ngOnInit() {
    this.dataset_id = this.route.snapshot.paramMap.get('dataset_id');
    ResourceMemberEdit.resource_id = this.dataset_id;

    this.authentication.isLoggedIn();

    Observable.forkJoin(
      this.memberService.fetchResourceMembers(this.dataset_id),
      this.permissionsService.fetchAllPermissions(this.permissionsService.types.dataset),
      this.userService.fetchUserList(),
    )
    .map(results => {
      this.members = results[0].map(m => (new ResourceMemberEdit()).fromMember(m));
      this.permissions = results[1].filter(p => p.permission_name.indexOf('.') === -1);
      this.users = results[2];
      this.isReady = true;
    })
    .subscribe(
      success => {
      },
      error => {
      },
    );
  }

  filterMembers(permission) {
    return this.members.filter(m => m.permission_id == permission.permission_id);
  }

  remove(member) {
    member.permission_id = '';
  }

  add($event, permission) {
    $event.stopPropagation();

    const usersDialog = this.dialog.open(SelectUsersModalComponent, {
      height: 'auto',
      width: 'auto',
      data: {
        permission: permission,
        title: this.overview.summary.dataset_title,
        users: this.users.filter(u => {
          const member = this.members.find(m => m.member_id == u.user_id);
          return member ? !member.permission_id : true;
        }),
      }
    });

    usersDialog.afterClosed().subscribe(user_ids => {
      if (!user_ids) { return; }
      user_ids.forEach(uid => {
        const member = this.members.find(m => m.member_id == uid);
        if (member) {
          member.permission_id = permission.permission_id;
        } else {
          const user = this.users.find(u => u.user_id == uid);
          this.members.push((new ResourceMemberEdit()).build(user, permission))
        }
      });
    });
  }

  save() {
    this.status = 'busy';
    this.disabled = 'disabled';
    this.datasetService
      .updateMetadata(this.dataset_id, this.metadataService)
      .subscribe(
        success => {
          this.status = 'success'
          this.disabled = '';
          setTimeout(() => {
            this.status = null;
          }, 3000)
        },
        error => {
          this.status = 'error'
          this.disabled = '';
        }
      );
  }

}



