import { Injectable } from '@angular/core';
import { TranslationService } from './translation.service';

export interface TermMatches {
  (header: string): boolean;
}

/**
 * Darwin core terms
 *   name    - Darwin Core term
 *   mapsTo  - column header name
 */
export interface Term {
  readonly name: string;
  mapsTo: string;
  required: boolean;
  optional: boolean;
  termMatches: TermMatches;
}

/**
 * Column headers in user-provided CSV
 *   name   - column header name
 *   mapsTo - Darwin Core term. If empty string, then there's "no match"
 */
export interface Verbatim {
    name: string;
    mapsTo: string;
}

const COMMON_TERMS = [
  { name: 'abundance',          termMatches: t => RegExp(/^abund(ance)?/,       'i').test(t) },
  { name: 'catalog_number',     termMatches: t => RegExp(/^cat.*(num|no)/,      'i').test(t) },
  { name: 'class',              termMatches: t => RegExp(/^class/,              'i').test(t) },
  { name: 'collection_code',    termMatches: t => RegExp(/^coll.*code/,         'i').test(t) },
  { name: 'coordinate_precision', termMatches: t => RegExp(/^(coord.*)?(prec)/, 'i').test(t) },
  { name: 'country_code',       termMatches: t => RegExp(/^country[\s_]?code/,  'i').test(t) },
  { name: 'date',               termMatches: t => RegExp(/^(event)?date/,       'i').test(t) },
  { name: 'day',                termMatches: t => RegExp(/^day/,                'i').test(t) },
  { name: 'decimal_latitude',   termMatches: t => RegExp(/^(dec.*)?(lat)/,      'i').test(t) },
  { name: 'decimal_longitude',  termMatches: t => RegExp(/^(dec.*)?(lon|lng)/,  'i').test(t) },
  { name: 'depth',              termMatches: t => RegExp(/^depth$/,             'i').test(t) },
  { name: 'depth_accuracy',     termMatches: t => RegExp(/^depth[\s_]?(acc|prec|uncert)?/, 'i').test(t) },
  { name: 'elevation',          termMatches: t => RegExp(/^ele(vation)?$/,      'i').test(t) },
  { name: 'elevation_accuracy', termMatches: t => RegExp(/^ele(vation)[\s_]?(acc|prec|uncert)?/, 'i').test(t) },
  { name: 'establishment_means', termMatches: t => RegExp(/^est.*means/,        'i').test(t) },
  { name: 'family',             termMatches: t => RegExp(/^family/,             'i').test(t) },
  { name: 'genus',              termMatches: t => RegExp(/^genus/,              'i').test(t) },
  { name: 'geodetic_datum',     termMatches: t => RegExp(/^(geo.*)?(datum)/,    'i').test(t) },
  { name: 'global_unique_identifier', termMatches: t => RegExp(/^(gu?id|global.*id)/, 'i').test(t) },
  { name: 'identified_by',      termMatches: t => RegExp(/^ident(.*by)?/,       'i').test(t) },
  { name: 'infraspecific_epithet', termMatches: t => RegExp(/^infraspec/,       'i').test(t) },
  { name: 'institution_code',   termMatches: t => RegExp(/^inst.*code/,         'i').test(t) },
  { name: 'kingdom',            termMatches: t => RegExp(/^king/,               'i').test(t) },
  { name: 'license',            termMatches: t => RegExp(/^lic/,                'i').test(t) },
  { name: 'locality',           termMatches: t => RegExp(/^locality/,           'i').test(t) },
  { name: 'month',              termMatches: t => RegExp(/^month/,              'i').test(t) },
  { name: 'order',              termMatches: t => RegExp(/^order/,              'i').test(t) },
  { name: 'photographer',       termMatches: t => RegExp(/^photographer/,       'i').test(t) },
  { name: 'phylum',             termMatches: t => RegExp(/^phylum/,             'i').test(t) },
  { name: 'publishing_org_key', termMatches: t => RegExp(/^publishing[\s_]?(org)?[\s_]?key/, 'i').test(t) },
  { name: 'record_number',      termMatches: t => RegExp(/^rec.*(num|no)/,      'i').test(t) },
  { name: 'record_source',      termMatches: t => RegExp(/^rec(ord)?[\s_]?source/, 'i').test(t) },
  { name: 'recorded_by',        termMatches: t => RegExp(/^(rec|obs|coll)/,     'i').test(t) },
  { name: 'region_id',          termMatches: t => RegExp(/^reg.*id/,            'i').test(t) },
  { name: 'scientificname',     termMatches: t => RegExp(/^(sci|name)/,         'i').test(t) },
  { name: 'species',            termMatches: t => RegExp(/^species/,            'i').test(t) },
  { name: 'taxon_rank',         termMatches: t => RegExp(/^(taxon.*)?rank/,     'i').test(t) },
  { name: 'type_status',        termMatches: t => RegExp(/^type[\s_]?stat/,     'i').test(t) },
  { name: 'uncertainty_m',      termMatches: t => RegExp(/^(coord.*)?(uncert|acc)/, 'i').test(t) },
  { name: 'year',               termMatches: t => RegExp(/^year/,               'i').test(t) },
];

@Injectable()
export class TermsService {
  // Darwin Core terms
  terms: Term[] = [];
  // User / CSV header terms
  verbatims: Verbatim[] = [];

  hasRecordedBy = false;

  missingPrefix = 'Missing required term:';

  constructor(public translationService: TranslationService) {
    this.missingPrefix = this.translationService.getTranslation(['additional', 'missingRequiredTerm']);
    COMMON_TERMS.forEach(term => this.add(term.name, term.termMatches));
  }

  get visibleTerms() {
    return this.terms.filter(term => term.optional || term.required);
  }

  checkForRecordedBy () : boolean {
    this.hasRecordedBy = !!this.find("recorded_by").mapsTo;
    return this.hasRecordedBy;
  }

  // After the verbatims have been modified we need to update the terms
  remapVerbatimsToTerms() {
    this.terms.forEach(term => term.mapsTo = '');
    this.verbatims
      .filter(verbatim => verbatim.mapsTo)
      .forEach(verbatim => this.find(verbatim.mapsTo).mapsTo = verbatim.name);
  }

  add(name: string, termMatches?: TermMatches) {
    this.terms.push(<Term>{
      name: name,
      required: false,
      optional: false,
      termMatches: termMatches,
    });
  }

  initVerbatims(names: string[]): void {
    this.verbatims = names.map(name => <Verbatim>{name: name, mapsTo: ''});
  }

  findDefaultMappings(): void {
    // Map required terms before other other terms
    this._findDefaultMappings(this.required());
    this._findDefaultMappings(this.terms);
  }

  private _findDefaultMappings(termSubset: Term[]): void {
    // Check for exact matches first
    termSubset.filter(term => !term.mapsTo).forEach(term => {
      const verbatims = this.verbatims.filter(verbatim => !verbatim.mapsTo);
      for (const verbatim of verbatims) {
          if (term.name == verbatim.name) {
            term.mapsTo = verbatim.name;
            verbatim.mapsTo = term.name;
            break;
          }
      }
    });
    // Now check for fuzzy matches
    termSubset.filter(term => !term.mapsTo).forEach(term => {
      const verbatims = this.verbatims.filter(verbatim => !verbatim.mapsTo);
      for (const verbatim of verbatims) {
          if (term.termMatches(verbatim.name)) {
            term.mapsTo = verbatim.name;
            verbatim.mapsTo = term.name;
            break;
          }
      }
    });
  }

  required(): Term[] {
    return this.terms.filter(term => term.required);
  }

  reset(): void {
    this.terms.forEach(term => term.mapsTo = '');
    this.verbatims = [];
  }

  find(name: string): Term {
    return this.terms.find(term => term.name == name);
  }

  findVerbatim(name: string): Verbatim {
    return this.verbatims.find(verbatim => verbatim.name == name);
  }

  setRequiredTerms(requiredTerms: string[]): void {
    this.terms.forEach(term => {
      term.required = requiredTerms.indexOf(term.name) > -1;
    });
  }

  setOptionalTerms(optionalTerms: string[]): void {
    this.terms.forEach(term => {
      term.optional = optionalTerms.indexOf(term.name) > -1;
    });
  }

  setTermLevels(requiredTerms: string[], optionalTerms: string[]): void {
    this.setRequiredTerms(requiredTerms);
    this.setOptionalTerms(optionalTerms);
  }

  missingRequired(): string[] {
    return this.terms
      .filter(term => term.required)
      .filter(term => !term.mapsTo)
      .map(term => `${this.missingPrefix} ${term.name}`);
  }

}



