export function UUID() {
  let seed = new Date().getTime();
  if (window.performance && typeof window.performance.now === 'function') {
    seed += performance.now(); // Use high-precision timer if available
  }
  const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(char) {
    const digit = (seed + Math.random() * 16) % 16 | 0;
    seed = Math.floor(seed / 16);
    return (char === 'x' ? digit : (digit & 0x3 | 0x8)).toString(16);
  });
  return uuid;
}

export function isUuid(str) {
  return str.match(/^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i);
}

export function isArray(value) {
  return Object.prototype.toString.call(value) === '[object Array]'
}

export function setCookie(name,value,days) {
    var expires = "";
    if (days) {
        var date = new Date();
        date.setTime(date.getTime() + (days*24*60*60*1000));
        expires = "; expires=" + date.toUTCString();
    }
    document.cookie = name + "=" + (value || "")  + expires + "; path=/";
}

export function getCookie(name) {
    var nameEQ = name + "=";
    var ca = document.cookie.split(';');
    for(var i=0;i < ca.length;i++) {
        var c = ca[i];
        while (c.charAt(0)==' ') c = c.substring(1,c.length);
        if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length,c.length);
    }
    return null;
}

export function eraseCookie(name) {
    document.cookie = name + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
}

export function normalizeSciName(sciName) {
    sciName = sciName.trim().split(/\s+/).join(' ');
    return sciName.slice(0, 1).toUpperCase() + sciName.slice(1).toLowerCase();
  }



