import { Component, OnInit, ViewEncapsulation, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';

import { RangeMapService } from '../../services/range-map.service';
import { AuthenticationService } from '../../services/authentication.service';

import { RangeMapInfoModalComponent } from '../../components/range-map-info-modal/range-map-info-modal.component';


@Component({
  selector: 'mol-range-map-metadata-page',
  templateUrl: './range-map-metadata-page.component.html',
  styleUrls: ['./range-map-metadata-page.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class RangeMapMetadataPageComponent implements OnInit, OnDestroy {

  private errorSubscription = null;
  private currentDialogRef = null;

  dataset_id: string = null;

  constructor(
    private authentication: AuthenticationService,
    public rangeMapService: RangeMapService,
    private router: Router,
   private route: ActivatedRoute,
    public dialog: MatDialog
  ) {
    this.dataset_id = this.route.snapshot.params.dataset_id;
    if(this.dataset_id) {
      this.rangeMapService.getDataset(this.dataset_id);
    }

    this.errorSubscription = this.rangeMapService.errorSubj.subscribe(error => {
      this.openErrorModal(error)
    });
  }

  openErrorModal (error) {
    const createError = () => {
       if (error.type === 'updateMetadata') {
         this.dialog.open(RangeMapInfoModalComponent, {
           width: '300px',
           height: '300px',
           disableClose: true,
           data: {
             title: 'Cannot update metadata',
             type: 'error',
             text: error.error
           }
         });
       }
    }

    /**
     * If a dialog already exists, then the closing of the previous dialog is asynchronous
     * because of the closing animation. Therefore, we have to wait for it to finish closing
     * before we can show the next modal.
     */
    this.dialog.closeAll();
    if(this.currentDialogRef) {
      this.currentDialogRef.afterClosed().subscribe(value => {
        createError();
      });
    } else {
      createError();
    }
  }

  ngOnDestroy () {
    this.dialog.closeAll();
    this.errorSubscription.unsubscribe();
  }

  ngOnInit() {
    this.authentication.isLoggedIn();
  }

  datasets() {
    this.router.navigate(['datasets']);
  }

  save() {
    const dataset_id = this.rangeMapService.dataset_id;
    const router = this.router;
    this.currentDialogRef = this.dialog.open(RangeMapInfoModalComponent, {
      width: '300px',
      height: '300px',
      disableClose: true,
      data: {
        type: 'info',
        text: 'Saving Metadata'
      }
    });

    this.rangeMapService.updateMetadata().subscribe(
      response => {
        this.currentDialogRef.close();
        router.navigate(['dataset', dataset_id]);
      }
    );
  }

}



