import { Component, OnInit, ViewEncapsulation, SecurityContext } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import 'rxjs/add/operator/switchMap';
import { DomSanitizer } from '@angular/platform-browser';

import { MatDialog } from '@angular/material';
import { DeleteDatasetModalComponent } from '../../components/delete-dataset-modal/delete-dataset-modal.component';

import { DatasetService, DatasetRecordItem } from '../../services/dataset.service';
import { PointsMetadataService } from '../../services/metadata/points-metadata.service';
import { InventoryMetadataService } from '../../services/metadata/inventory-metadata.service';
import { RangeMapMetadataService } from '../../services/metadata/range-map-metadata.service';
import { DatasetOverview } from '../../services/range-map.service';
import { AuthenticationService } from '../../services/authentication.service';
import { TranslationService } from '../../services/translation.service';

@Component({
  selector: 'mol-dataset-detail-page',
  templateUrl: './dataset-detail-page.component.html',
  styleUrls: ['./dataset-detail-page.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DatasetDetailPageComponent implements OnInit {

  dataset_type: 'localinv' | 'points' | 'range' = null;
  dataset_id = '';
  overview: DatasetOverview;
  records: DatasetRecordItem[] = null;
  title = '';
  short_citation = '';
  citation = '';
  activeTabIndex: number = 0;
  metadataService: any;
  oldMetadata: any;
  errorCode = null;
  error = '';
  hasFeatures = true;
  finalized = false;
  isDev = false;

  translations: any;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public dialog: MatDialog,
    private pointsService: PointsMetadataService,
    private inventoryService: InventoryMetadataService,
    private rangeMapService: RangeMapMetadataService,
    private datasetService: DatasetService,
    private authentication: AuthenticationService,
    private sanitizer: DomSanitizer,
    public translationService: TranslationService
  ) {
    this.translations = translationService.getTranslations();
  }

  ngOnInit() {
    const pathname = this.sanitizer.sanitize(SecurityContext.URL, window.location.pathname);
    const hostname = this.sanitizer.sanitize(SecurityContext.URL, window.location.hostname);
    this.isDev = pathname.startsWith('/upload-dev/') || hostname === 'localhost';

    if(!this.authentication.loggedIn()) {
      this.router.navigate(['landing']);
    }

    this.dataset_id = this.route.snapshot.paramMap.get('dataset_id');
    this.activeTabIndex = +this.route.snapshot.paramMap.get('tab');

    this.datasetService.fetchOverview(this.dataset_id)
      .subscribe(overview => {
        // see what kind of response we got
        const didReturnValue = !!overview[0];
        const didError = (didReturnValue) ? !!overview[0].error : false;
        const didReturnCode = (didReturnValue) ? !!overview[0].code : false;
        const errorMsg = (didError) ? overview[0].error : false;
        const errorCode = (didReturnCode) ? overview[0].code : false;

        if(didReturnValue && !didError) {
          this.overview = overview[0];
        } else if (didReturnValue && didError && didReturnCode) {
          this.errorCode = errorCode;
          this.error = errorMsg;
          return;
        } else if (!didReturnValue) {
          this.errorCode = 0;
          // Translations: 'We had a problem uploading this dataset.'
          this.error = this.translationService.getTranslation(['dataset-detail-error','problemUploadingDataset']);
        }

        if(didReturnValue && !didError) {
          this.title = this.overview.summary.dataset_title;
          this.dataset_type = this.overview.summary.product_type;

          for(let metadataSection of this.overview.metadata) {
            for (let metadataField of metadataSection.children) {
              if(metadataField.id === 'hc_author_display') {
                this.short_citation = metadataField.value;
              }
              if(metadataField.id === 'hc_citation_formatted') {
                this.citation = metadataField.value;
              }
            }
          }

          // assign metadata service per data type
          switch(this.dataset_type) {
            case 'points':
              this.metadataService = this.pointsService;
            break;
            case 'localinv':
              this.metadataService = this.inventoryService;
            break;
            case 'range':
              this.metadataService = this.rangeMapService;
            break;
            default:
              this.metadataService = this.pointsService;
            break;
          }

          if(!this.overview.summary.counts.records && !this.overview.summary.counts.species) {
            this.hasFeatures = false;
          }

          if(this.overview.summary.status === 'COMPLETED') {
            this.finalized = true;
          }

          this.metadataService.fromOverview(this.dataset_id, this.overview);

          if(this.dataset_type === 'range') {
            this.datasetService.fetchRecords(this.dataset_id)
              .subscribe(records => {
                if(records && records[0] && records[0].records) {
                  this.records = records[0].records.sort((a, b) => {
                    let sciOrder = 0;
                    if (a.scientificname < b.scientificname) { sciOrder = -1 }
                    if (a.scientificname > b.scientificname) { sciOrder = 1 }
                    if (sciOrder) return sciOrder;

                    if (a.recid < b.recid) { return -1 }
                    if (a.recid > b.recid) { return 1 }
                    return 0;
                  });
                } else {
                  this.records = [];
                }
              });
          }
        }

      });
  }

  openPermissionsTab () {
    this.activeTabIndex = 3;
  }

  deleteDataset($event) {
    const self = this;
    $event.stopPropagation();
    const deleteDialog = this.dialog.open(DeleteDatasetModalComponent, {
      height: '250px',
      width: '400px',
      data: {
        dataset_id: this.dataset_id,
        dataset_title: this.title
      }
    });

    deleteDialog.afterClosed().subscribe(reply => {
      if (!reply) { return; }
      this.datasetService.delete(this.dataset_id).subscribe(
        success => {
          this.router.navigate(['datasets']);
        },
        error => {
          const reply = error.json();
          // i18n Translations: 'Could not delete the dataset.'
          let msg = this.translationService.getTranslation(["dataset-detail-error","cannotDeleteDataset"]);
          if (reply.length && reply[0].error) {
            msg += reply[0].error;
          }
        });
    });
  }

  addFeature() {
    this.router.navigate(['range-map/draw', this.dataset_id]);
  }

  finalizeDataset () {
    this.router.navigate(['range-map/finalize', this.dataset_id]);
  }

  toUpdateTaxonomy() {
    if (['completed', 'loaded'].indexOf(this.overview.summary.status.toLowerCase()) > -1) {
      this.router.navigate(['taxonomy-update', this.dataset_id]);
    }
  }

}



