import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import { ApiService } from "../api.service";
import { UploadService } from "./upload.service";
import { TaxaService } from "../taxa.service";
import { FileReaderResults } from "../../components/file-reader/file-reader.component";

import { InventoryMetadataService } from "../metadata/inventory-metadata.service";
import { PointsMetadataService } from "../metadata/points-metadata.service";
import { ShapeFeatureService } from "../feature/shape-feature.service";
import { PointFeatureService } from "../feature/point-feature.service";
import { PointInventoryFeatureService } from "../feature/point-inventory-feature.service";
import { ValidationService } from "../validation.service";
import { TermsService } from "../terms.service";
import { TranslationService } from "../translation.service";

import { Subject } from "rxjs";

@Injectable()
export class PointInventoryUploadService extends UploadService {
  requiredTerms = [];
  optionalTerms = [];

  requiredTermsByNumFiles = {
    "1": [
      "scientificname",
      "region_id",
      "decimal_latitude",
      "decimal_longitude"
    ],
    "2": ["scientificname", "region_id"]
  };

  optionalTermsByNumFiles = {
    "1": [],
    "2": []
  };

  readonly checklistErrorPrefix = "Checklist file error:";

  checklistFile;
  geometryFile;
  combinedFile;

  metadata: InventoryMetadataService;

  coordinateTypeSelections;
  numFiles: "1" | "2" = null;

  geomFeaturesCreated = new Subject();

  constructor(
    protected api: ApiService,
    protected http: Http,
    public taxaService: TaxaService,
    public valid: ValidationService,
    public terms: TermsService,
    public pointFeatureService: PointFeatureService,
    public shapeFeatureService: ShapeFeatureService,
    public pointInventoryFeatureService: PointInventoryFeatureService,
    public pointsMetadataService: PointsMetadataService,
    public inventoryMetadataService: InventoryMetadataService,
    public translationService: TranslationService
  ) {
    super(
      api,
      http,
      taxaService,
      valid,
      terms,
      pointFeatureService,
      shapeFeatureService,
      pointsMetadataService,
      inventoryMetadataService,
      translationService
    );
    this.metadata = inventoryMetadataService;
    this.features = this.pointInventoryFeatureService;
  }

  numFilesChange() {
    this.optionalTerms = this.optionalTermsByNumFiles[this.numFiles];
    this.requiredTerms = this.requiredTermsByNumFiles[this.numFiles];
    this.terms.setTermLevels(this.requiredTerms, this.optionalTerms);
  }

  reset() {
    this.terms.setTermLevels(this.requiredTerms, this.optionalTerms);
    this.resetFiles();
    this.numFiles = null;
    this.coordinateTypeSelections = undefined;
    super.reset();
  }

  filesReady(): boolean {
    return (this.checklistFile && this.geometryFile) || !!this.combinedFile;
  }

  isValid(): boolean {
    return this.filesReady();
  }

  initVerbatims() {
    if (this.combinedFile) {
      this.terms.initVerbatims(this.combinedFile.fileReaderOutput.meta.fields);
    } else {
      this.terms.initVerbatims(this.checklistFile.fileReaderOutput.meta.fields);
    }
  }

  resetFiles() {
    this.checklistFile = null;
    this.geometryFile = null;
    this.combinedFile = null;
    this.resetErrors();
  }

  saveChecklistResults(results: FileReaderResults): void {
    this.checklistFile = results;
  }

  saveGeometryFileResults(results: FileReaderResults): void {
    this.geometryFile = results;
  }

  saveCombinedFileResults(results: FileReaderResults): void {
    this.combinedFile = results;
  }

  contentsAreValid(): boolean {
    // TODO
    return true;
  }

  // checks checklist file
  checklistContentErrors(): string[] {
    let errors = [];

    // If we have two files, make sure the region IDs match
    if (!this.combinedFile) {
      errors = this.pointInventoryFeatureService.regionIdsMatch(
        this.geometryFile.fileReaderOutput.data,
        this.checklistFile.fileReaderOutput.data
      );
    }

    // Make sure each row has the appropriate number of columns
    let fileToCheck = this.combinedFile
      ? this.combinedFile
      : this.checklistFile;

    let noBlankHeaders = this.valid.noBlankHeaders(fileToCheck);

    if (!noBlankHeaders) {
      // i18n Translation: "Checklist file has empty column header."
      let err = this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list","checklistFileEmptyColumnHeader"]);
      errors.push(err);
    }

    const expectedLength = fileToCheck.fileReaderOutput.meta.fields.length;
    fileToCheck.fileReaderOutput.data.forEach((record, r) => {
      const row = r + 2;
      if (!this.valid.completeRecord(record, expectedLength)) {
        errors.push(`Record Row: ${row} is incomplete`);
      }
    });

    this.checklistContentErrorsList = errors;
    return errors;
  }

  setCoordinateTypeSelections(selections) {
    this.coordinateTypeSelections = selections;
  }

  validGeometryFile(results: FileReaderResults) {
    const data = results.fileReaderOutput.data;
    let errors = [];
    let noBlankHeaders = this.valid.noBlankHeaders(results);
    if (!noBlankHeaders) {
      // i18n Translation: "Regions file has empty column header."
      let err = this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "regionsFileEmptyColumnHeader"]);
      errors.push(err);
    }
    for (let i = 0; i < data.length; i++) {
      let row = data[i];
      // Check for Region ID
      if (!row.region_id) {
        // i18n Translation: "Row {row} region_id: is missing"
        let err = this.translationService.getTranslation([
          "files-form:match-columns-step:form:matching-errors-list",
          "regionIdMissing"
        ], {
          row: i
        });
        errors.push(err);
      }
      // Check for Latitude
      let cleanLat = row.decimal_latitude
        ? this.valid.cleanLatitude(row.decimal_latitude)
        : false;
      if (!cleanLat) {
        // // i18n Translation: "Row {row} decimal_latitude: is missing"
        let err = this.translationService.getTranslation(
          [
            "files-form:match-columns-step:form:matching-errors-list",
            "latitudeMissing"
          ],
          {
            row: i
          }
        );
        errors.push(err);
      } else if (cleanLat.error) {
        // i18n Translation: "Row {row} decimal_longitude: {error}"
        let err = this.translationService.getTranslation(
          [
            "files-form:match-columns-step:form:matching-errors-list",
            "latitudeMissingWithError"
          ],
          {
            row: i,
            error: cleanLat.error
          }
        );
        errors.push(err);
      }
      // Check for Longitude
      let cleanLong = row.decimal_longitude
        ? this.valid.cleanLongitude(row.decimal_longitude)
        : false;
      if (!cleanLong) {
        // i18n Translation: "Row {row} decimal_longitude: is missing"
        let err = this.translationService.getTranslation(
          [
            "files-form:match-columns-step:form:matching-errors-list",
            "longitudeMissing"
          ],
          {
            row: i
          }
        );
        errors.push(err);
      } else if (cleanLong.error) {
        // i18n Translation: "Row {row} decimal_longitude: {error}"
        let err = this.translationService.getTranslation(
          [
            "files-form:match-columns-step:form:matching-errors-list",
            "longitudeMissingWithError"
          ],
          {
            row: i,
            error: cleanLong.error
          }
        );
        errors.push(err);
      }
    }
    return errors;
  }

  /**
   * Split Combined File
   *
   * - Initially, point inventories had two separate files (geometry and checklist).
   * - We want to allow users to upload point inventories that have just one file.
   *
   * Therefore, for single-file datasets, we need to:
   *     - Tease out all the individual regions and format for pointInventoryFeatureService.addRecord()
   *     - Then, treat the file as a checklist file and addChecklistRecord() for every row
   */
  splitCombinedFile(combinedData) {
    const verbatimRegionId = this.terms.find("region_id").mapsTo;
    const verbatimDecimalLatitude = this.terms.find("decimal_latitude").mapsTo;
    const verbatimDecimalLongitude = this.terms.find("decimal_longitude")
      .mapsTo;

    const regions = {};
    for (let row of combinedData) {
      // create a truncated version of our data for just the region_id and coordinates
      let newRow: any = {};
      newRow[verbatimRegionId] = row[verbatimRegionId];
      newRow.decimal_latitude = row[verbatimDecimalLatitude];
      newRow.decimal_longitude = row[verbatimDecimalLongitude];

      let region_id = row[verbatimRegionId];
      if (!regions[region_id]) {
        regions[region_id] = newRow;
      }
    }

    for (let region in regions) {
      this.pointInventoryFeatureService.addRegion(
        regions[region],
        this.coordinateTypeSelections,
        true
      );
    }

    for (let row of combinedData) {
      this.pointInventoryFeatureService.addChecklistRecord(
        row,
        this.metadata.dataset_id
      );
    }
  }

  createFeatures() {
    this.features.reset();

    if (this.combinedFile) {
      this.splitCombinedFile(this.combinedFile.fileReaderOutput.data);
    } else {
      // For each region in the geometry file, create a region
      this.geometryFile.fileReaderOutput.data.forEach(record => {
        this.pointInventoryFeatureService.addRegion(
          record,
          this.coordinateTypeSelections,
          false
        );
      });

      this.checklistFile.fileReaderOutput.data.forEach(record => {
        this.pointInventoryFeatureService.addChecklistRecord(
          record,
          this.metadata.dataset_id
        );
      });
    }

    this.geomFeaturesCreated.next(
      this.pointInventoryFeatureService.geomFeatures
    );

    this.checkFeatureSize();
  }
}



