import {
  Injectable
 } from '@angular/core';
import { TermsService } from '../terms.service';
import { TranslationService } from '../translation.service';

@Injectable()
export abstract class FeatureService {
  dataFeatures = [];
  geomFeatures = [];
  attrFeatures = [];
  regionIdCache = {};

  translations = {};

  constructor(
    public terms: TermsService,
    public translationService: TranslationService
  ) {
    this.translations = translationService.getTranslations();
  }

  reset() {
    this.dataFeatures = [];
    this.geomFeatures = [];
    this.attrFeatures = [];
    this.regionIdCache = {};
  }

  attributes(record) {
    return Object.keys(record)
      .filter(key => {
        /**
         * Requirements
         *   - Key isn't equal to 'molid'
         *   - Key length isn't blank
         *   - Value isn't blank
         */
        return key !== "molid" && key && record[key];
      })
      .map(key => {
        const verbatim = this.terms.findVerbatim(key);
        const metadata_field =
          typeof verbatim !== "undefined" && verbatim.mapsTo
            ? verbatim.mapsTo
            : key;
        const verbatim_field = verbatim ? key : "";

        return {
          type: "Feature",
          geometry: null,
          properties: {
            molid: record.molid,
            metadata_field: metadata_field,
            verbatim_field: verbatim_field,
            field_value: String(record[key])
          }
        };
      });
  }
}



