import { Injectable } from '@angular/core';
import { Http, Jsonp } from '@angular/http';
import { ApiService } from './api.service';
import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';
import 'rxjs/add/observable/of';

export interface Taxon {
  description: string;
  groupby: string;
  groupby_sortby: number;
  icon: any;
  sortby: number;
  taxa: string;
  tc_id: string;
  title: string;
}

export interface Taxonomies {
  taxonomy_group: string;
  taxonomies: Taxonomy[][];
}

export interface Taxonomy {
  ismaster: boolean;
  name: string;
  roottcid: string;
  authorstring: string;
  citationstr: string;
  taxonomy_id: number;
}

export interface TCInfo {
  taxonomyid: string;
  name: string;
  authorstring: string;
  citationid: string;
  rank: string;
  parenttcid: string;
  highertaxa: string[];
  children: string[];
  synonyms: string[];
}

export interface ApproxTCSearch {
  tcid: string;
  taxonomyid: string;
  rank: string;
  name: string;
  validname: boolean;
  ismaster: boolean;
}

export interface TCSearches {
  [name: string]: TCSearch[];
}

export interface TCSearch {
  tcid: string;
  taxonomyid: string;
  rank: string;
  name: string;
  validname: boolean;
}

export interface UpdateTaxa {
  tc_id: string;
  newname: string;
  oldname: string;
}

export interface TaxonName {
  scientificname: string;
  tc_id: string;
  verbatim_scientificname: string;
}


@Injectable()
export class TaxaService {

  taxa: Taxon[] = [];
  selected: Taxon;

  constructor(
    protected api: ApiService,
    protected http: Http,
    protected jsonp: Jsonp,
  ) { }

  fetchTaxa() {
    return this.http.get(`${this.api.root}/species/availabletaxa`)
      .map(response => {
        this.taxa = response.json();
        return this.taxa;
      });
  }

  selectTaxon(taxon) {
    this.selected = this.taxa.find(t => t.taxa === taxon);
  }

  getTaxonomies() {
    return this.http.get(`${this.api.root}/taxonomy/taxonomies`)
      .map(response => response.json());
  }

  getTcInfo(tcids: string[]) {
    if (tcids && tcids.length && tcids[0]) {
      const ids = tcids.join(',');
      return this.http.get(`${this.api.root}/taxonomy/tcinfo?tcids=${ids}`)
        .map(response => response.json());
    }
    return Observable.of({});
  }

  getTcSearch(searchstrs: string[]) {
    if (!searchstrs.length) {
      return Observable.of([]);
    }
    let url = `${this.api.root}/taxonomy/tcsearch?`;
    url += 'searchstrs=';
    url += searchstrs.map(str => encodeURIComponent(str) ).join(',');
    url += '&rank=Species';
    url += `&${this.api.callback}`;
    return this.jsonp.request(url)
      .map(response => {
        return response.json();
      });
  }

  getApproxTcSearch(searchstrs: string[]) {
    if (!searchstrs.length) {
      return Observable.of([]);
    }
    let url = `${this.api.root}/taxonomy/approxtcsearch?`;
    url += 'searchstrs=';
    url += searchstrs.map(str => encodeURIComponent(str) ).join(',');
    url += '&rank=Species';
    // url += '&method=qgram';
    url += `&${this.api.callback}`;
    return this.jsonp.request(url)
      .map(response => {
        return response.json();
      });
  }

  getTaxaNames(dataset_id: string) {
    let url = `${this.api.root}/acl/member/dataset/taxanames?dataset_id=${dataset_id}&${this.api.callback}`;
    return this.jsonp.get(url)
      .map(response => {
        return response.json();
      });
  }

  updateTaxa(dataset_id: string, taxa: UpdateTaxa[]) {
    let url = `${this.api.root}/acl/member/dataset/taxa?dataset_id=${dataset_id}`;
    return this.http.post(url, JSON.stringify(taxa), { withCredentials: true });
  }

  public reset () {
    this.taxa = [];
    this.selected = null;
  }
}



