import { Injectable } from '@angular/core';
import { FeatureService } from './feature.service';
import { UUID } from '../util.service';

@Injectable()
export class ShapeFeatureService extends FeatureService {

  datum(record) {
    const verbatimSciName = this.terms.find('scientificname').mapsTo;
    return {
      geometry: null,
      type: 'Feature',
      properties: {
        dataset_id: record.dataset_id,
        molid: record.molid,
        geom_id: record.geom_id,
        product_type: 'localinv',
        scientificname: record[verbatimSciName],
        date: null,
        uncertainty: null
      }
    };
  }

  addShapeRecord(feature) {
    let updatedFeature = {
      ...feature
    }
    const verbatimRegionId = this.terms.find('region_id').mapsTo;
    const region_id = updatedFeature.properties[verbatimRegionId];
    this.regionIdCache[region_id] = this.regionIdCache[region_id] || UUID();
    updatedFeature.properties.geom_id = this.regionIdCache[region_id];
    this.geomFeatures.push(updatedFeature);
  }

  addChecklistRecord(record, datasetId) {
    let updatedRecord = {
      ...record
    };
    const verbatimSciName = this.terms.find('scientificname').mapsTo;
    const verbatimRegionId = this.terms.find('region_id').mapsTo;

    updatedRecord.dataset_id = datasetId;
    updatedRecord.molid = UUID();
    updatedRecord.verbatim_scientificname = updatedRecord[verbatimSciName];
    const region_id = updatedRecord[verbatimRegionId];
    updatedRecord.geom_id = this.regionIdCache[region_id];
    this.dataFeatures.push(this.datum(updatedRecord));
    this.attrFeatures.push(...this.attributes(updatedRecord));
  }

  regionIdsMatch(features, records) {
    const self = this;
    const regionIdCache = {};
    const errors = {};

    // Build the region ID cache
    const verbatimRegionId = this.terms.find('region_id').mapsTo;
    features.forEach(feature => {
      let regionId = feature.properties[verbatimRegionId];
      if(typeof regionId !== "undefined") {
        regionIdCache[regionId] = 1;
      } else {
        /**
          * Error pulled from src/app/interpolation.ts:
          *
          * Region ID: Mismatch between region_id column in CSV and region_id column in shapefile.
          *  Usually this means that the column name in one of the files is mistyped. Check to make
          *  sure there is no whitespace in your column headers.
          *
          */
        let error = this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list","regionIdMismatch"]);
        errors[error] = 1;
      }
    });

    // Get missing region IDs
    records.filter(record => !regionIdCache[record[verbatimRegionId]])
      .forEach(record => {
        // i18n Translation: "Region ID: {region_id} was not found in the shape file.""
        let err = this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list","regionIdNotFoundShapeFile"], {
          region_id: record[verbatimRegionId]
        });
        errors[err] = 1;
      });

    // Output errors
    return Object.keys(errors);
  }

}



