import { Component, ViewEncapsulation } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { LOCALE_LIST } from '../locale/localeList';
import { TranslationService } from './services/translation.service';

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent {
  isWWW = false;
  isEmbedded = false;

  constructor(
    private router: Router,
    public translationService: TranslationService
  ) {
    this.isWWW = window.location.hostname.startsWith("www");
    if (this.getUrlVar("embed")) {
      this.isEmbedded = true;
    }
    this.router.events.subscribe((event: NavigationEnd) => {
      if (event instanceof NavigationEnd) {
        window.scrollTo(0, 0);
      }
    });
  }

  getUrlVar(name) {
    name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
    var regex = new RegExp("[\\?&]" + name + "=([^&#]*)");
    var results = regex.exec(location.search);
    return results === null
      ? ""
      : decodeURIComponent(results[1].replace(/\+/g, " "));
  }

  changeLocale(localeId) {
    this.translationService.changeLocale(localeId);
  }

  headerDoneLoading() {
    let currentLocale = this.translationService.getCurrentLocale();
    // put some stuff in the i18n component
    let i81nNode = document.getElementsByTagName("mol-i18n")[0];
    for (let locale of LOCALE_LIST) {
      let localeElement = document.createElement("span");
      localeElement.innerText = locale;
      localeElement.classList.add("locale");
      if(locale === currentLocale) {
        localeElement.classList.add("selected");
      }
      localeElement.addEventListener("click", () => {
        this.changeLocale(locale);
      });
      i81nNode.appendChild(localeElement);
    }
  }
}



