import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { getCookie, setCookie, eraseCookie } from '../../services/util.service';

import { AuthenticationService } from '../../services/authentication.service';
import { RangeMapService } from '../../services/range-map.service';
import { MapService } from '../../services/map.service';
import { SpeciesService, SpeciesName } from '../../services/species.service';
import { SpeciesDatasetService, SpeciesDataset, DatasetType } from '../../services/species-dataset.service';
import { OverlayMapTypeService } from '../../services/overlay-map-type.service';

// modals
import { RangeMapInfoModalComponent } from '../../components/range-map-info-modal/range-map-info-modal.component';
import { SubmitRangeMapModalComponent } from '../../components/submit-range-map-modal/submit-range-map-modal.component';

import { FeatureCtPipe } from '../../pipes/feature-ct.pipe';

import { accordian } from './animations';


function speciesNameValidator(control: FormControl) {
  let speciesName = control.value;
  if(speciesName !== null) {
    if(typeof speciesName === 'object') {
      return null;
    }
    if(typeof speciesName === "string" && speciesName.match(/\w+\s\w+/)) {
      return null;
    }
  }
  return {
    speciesName: "You must either pick a species from the dropdown, or enter a binomial species name (two words only)."
  };
}

@Component({
  selector: 'mol-range-map-draw-page',
  templateUrl: './range-map-draw-page.component.html',
  styleUrls: ['./range-map-draw-page.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [FeatureCtPipe]
})
export class RangeMapDrawPageComponent implements OnInit{

  form: FormGroup;
  dataTypes = null;
  public speciesKeyUpSubj = new Subject();
  speciesList: SpeciesName[] = [];
  refLayers = [
    {
      name: 'Hydro',
      visible: false,
      overlay: new OverlayMapTypeService('http://tile.stamen.com/toner/{z}/{x}/{y}.png')
    },{
      name: "Terrain",
      visible: false,
      overlay: new OverlayMapTypeService('http://tile.stamen.com/terrain/{z}/{x}/{y}.jpg')
    }
  ];
  dialogRefs = {
    saveFeature: null,
    postSubmit: null
  };
  speciesPattern = '\w+\s\w+';
  speciesName: string = null;

  constructor(
      public rangeMapService: RangeMapService,
      public mapService: MapService,
      private router: Router,
      private route: ActivatedRoute,
      public speciesDatasetService: SpeciesDatasetService,
      private speciesService: SpeciesService,
      private authentication: AuthenticationService,
      public dialog: MatDialog,
      private changeDetector: ChangeDetectorRef
  ) {
    const self = this;
    if (!this.rangeMapService.hasDatasetId()) {
      // if the user isn't arriving directly from metadata creation, but the url has a dataset_id
      if (this.route.snapshot.params.dataset_id) {
        this.rangeMapService.getDataset(this.route.snapshot.params.dataset_id);
      } else {
        this.router.navigate(['range-map']);
      }
    }

    if(this.route.snapshot.params.feature_id) {
      this.rangeMapService.startFeature(this.route.snapshot.params.feature_id);
    } else {
      this.rangeMapService.startFeature();
    }

    this.speciesDatasetService.dataTypes.subscribe((results) => {
      this.dataTypes = results;
    });

    // in the event there's a dataset error, show a modal
    this.rangeMapService.errorSubj.subscribe(error => {
      if (error.type === 'getDataset') {
        this.createErrorDialog('Can\'t load dataset info', error.error);
      }
      if (error.type === 'datasetFinalized') {
        this.createErrorDialog('Dataset already finalized', 'You can\'t add more features to a finalized dataset.', error.routerLink);
      }
      if (error.type === 'getUser') {
        this.createErrorDialog('Cannot Get User', 'Can\'t find user');
      }
    });

    // while feature is being submitted, show a modal, then submit
    this.rangeMapService.feature.featureBeforeSubmitSubj.subscribe(x => {
      self.dialogRefs.saveFeature = this.dialog.open(RangeMapInfoModalComponent, {
        width: '300px',
        height: '300px',
        disableClose: true,
        data: {
          type: 'info',
          text: 'Saving Feature'
        }
      });
      self.rangeMapService.feature.submitFeature();
    });

    // after feature submitted, close modal, open post-submit modal
    this.rangeMapService.feature.featureSubmitSubj.subscribe(overview => {
      if(self.dialogRefs.saveFeature) {
        self.dialogRefs.saveFeature.close();
        self.dialogRefs.saveFeature = null;
      }
      self.dialogRefs.postSubmit = self.dialog.open(SubmitRangeMapModalComponent, {
        height: 'auto',
        width: '400px'
      });
    });

    // if there's a feature error, show a modal
    this.rangeMapService.feature.errorSubj.subscribe(error => {
      if (error.type === 'submitFeature') {
        if(this.dialogRefs.saveFeature) {
          this.dialogRefs.saveFeature.close();
          self.dialogRefs.saveFeature = null;
        }
        this.createErrorDialog('Can\'t submit feature', error.error);
      }
      if (error.type === 'getFeature') {
        this.createErrorDialog('Can\'t get feature', error.error);
      }
      if (error.type === 'invalidGeoJson') {
        this.createErrorDialog('Invalid Geometry', error.error);
      }
    });

    this.form = new FormGroup({
      speciesName: new FormControl({ value: null, disabled: false }, [
        Validators.required,
        speciesNameValidator
      ])
    });

    const subscription = this.speciesKeyUpSubj.map(event => event)
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe(() => {
        this.fetchSpecies();
    });

    this.mapService.mapLoadObs.subscribe((res) => {
      // instantiate layers
      self.refLayerChange();
    });

    /**
     * I'm sorry.
     * An Angular bug is refusing to update the input display value after updating the internal
     * value. This is a hacky way to fix it.
     */
    function bypassAngularBug (speciesName) {
      setTimeout( () => {
        let x:any = document.querySelector("#speciesNameInput");
        x.value = speciesName;
      }, 1000);
    }

    /**
     * If the user is creating a new feature, see if they've recently created a range map. If yes,
     * reuse the species they selected for the last range map.
     */
    if(this.rangeMapService.feature.mode === 'new') {
      // check the cookie to see if the user is coming from another range map edit session
      let previousSpecies = getCookie('scientificname');
      let previousTcId = getCookie('tc_id');

      // set the species in the services
      if(previousSpecies && previousTcId) {
        this.rangeMapService.feature.setSpecies(previousSpecies, previousTcId);
        this.form.controls.speciesName.setValue(previousSpecies);
        bypassAngularBug(previousSpecies);
        eraseCookie('scientificname');
        eraseCookie('tc_id');
      } else if (previousSpecies) {
        this.rangeMapService.feature.setSpecies(previousSpecies);
        this.form.controls.speciesName.setValue(previousSpecies);
        bypassAngularBug(previousSpecies);
        eraseCookie('scientificname');
      }
    }
  }

  ngOnInit() {
    this.authentication.isLoggedIn();
  }

  displaySpecies(value) {
    if (value) {
      return value.scientificname;
    }
    return null;
  }

  // when the user selects a species from autocomplete
  selectSpecies () {
    this.rangeMapService.setSpecies(this.form.controls.speciesName.value);
  }

  fetchSpecies() {
    const taxonomy = this.rangeMapService.taxa;
    const species = this.form.controls.speciesName.value;
    if (species.length) {
      this.speciesService.fetchSpecies(taxonomy, species)
        .subscribe(listOfSpecies => {
          this.speciesList = listOfSpecies;
        }, error => {
          console.error('Can\'t retrieve species list');
        });
    }
  }

  createErrorDialog (title = '', text = '', routerLink?) {
    const errorDialog = this.dialog.open(RangeMapInfoModalComponent, {
      width: '300px',
      height: 'auto',
      disableClose: true,
      data: {
        title,
        type: 'error',
        text,
        routerLink
      }
    });
  }

  speciesKeyUp($event) {
    this.rangeMapService.feature.setSpecies(this.form.controls.speciesName.value);
    this.speciesKeyUpSubj.next($event);
  }

  reset () {
    this.speciesList = [];
  }

  molLayerChange () {
    this.speciesDatasetService.dataTypesSource.next(this.dataTypes);
  }

  refLayerChange () {
    this.mapService.layersSource.next(this.refLayers);
  }

  fitBounds (datasets: SpeciesDataset[]) {
    const bounds = this.speciesDatasetService.getBounds(datasets);
    this.speciesDatasetService.boundsSource.next(bounds);
  }

}



