import {
  Component,
  OnInit,
  ChangeDetectorRef,
  ViewChild,
  ViewEncapsulation,
  LOCALE_ID,
  Inject
} from "@angular/core";
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material';

// Services
import { TaxaService, Taxon } from '../../services/taxa.service';
import { AuthenticationService, UserInfo } from '../../services/authentication.service';
import { ValidationService } from '../../services/validation.service';
import { TranslationService } from '../../services/translation.service';

// Upload Services
import { PointUploadService } from '../../services/upload/point-upload.service';
import { InventoryUploadService } from '../../services/upload/inventory-upload.service';
import { PointInventoryUploadService } from '../../services/upload/point-inventory-upload.service';

// Components
import { FileReaderResults } from '../../components/file-reader/file-reader.component';
import { ShapeFileReaderComponent } from '../../components/file-reader/shape-file-reader/shape-file-reader.component';
import { CsvFileReaderComponent } from '../../components/file-reader/csv-file-reader/csv-file-reader.component';

// Modals
import { CoordinateTypeModalComponent, CoordinateTypeSelection } from '../../components/coordinate-type-modal/coordinate-type-modal.component';
import { MatchColumnsDialogComponent } from '../../components/match-columns-dialog/match-columns-dialog.component';

import PreviewMap from './PreviewMap';


@Component({
  selector: 'mol-files-form',
  templateUrl: './files-form.component.html',
  styleUrls: ['./files-form.component.scss', './ol.css', './previewMap.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FilesFormComponent implements OnInit {

  @ViewChild('shapeFile')
  private shapeFile: ShapeFileReaderComponent;

  @ViewChild('inventoryFile')
  private inventoryFile: CsvFileReaderComponent;

  @ViewChild('pointFile')
  private pointFile: CsvFileReaderComponent;

  @ViewChild('pointInventoryChecklistFile')
  private pointInventoryChecklistFile: CsvFileReaderComponent;

  @ViewChild('pointInventoryGeometryFile')
  private pointInventoryGeometryFile: CsvFileReaderComponent;

  @ViewChild('pointInventoryCombinedFile')
  private pointInventoryCombinedFile: CsvFileReaderComponent;

  form: FormGroup;
  taxa: Taxon[] = [];
  taxon: Taxon;
  datasetType: string;
  uploadService: InventoryUploadService | PointUploadService | PointInventoryUploadService;
  columnsChecked = false;
  formReady = false;
  userInfo: UserInfo = null;

  currentStep: number = 1;
  pointInventoryNumFiles: '1' | '2' = null;

  /**
   * CSV Errors
   * - errors directly from CSV file reader
   * - incorrect basic CSV format (not a CSV, etc)
   */
  csvErrors = {
    occurrence: [],
    inventory: [],
    pointInventoryChecklist: [],
    pointInventoryGeometry: [],
    pointInventoryCombined: []
  }

  /**
   * Shape Errors
   * - Shapefile-based inventories only
   * - errors directly from Shapefile reader
   * - incorrect basic Shapefile format (not zipped, etc.)
   */
  shapeErrors: string[] = [];

  /**
   * Point Inventory Geometry Errors
   * - Two-file, coordinate-based inventories only
   * - Checks to make sure that the following terms are present:
    *    - region_id
    *    - decimal_latitude
    *    - decimal_longitude
   */
  pointInventoryGeometryErrors: string[] = [];


  /**
   * Terms Errors
   * - Via Terms service, these errors appear if terms are completely missing from CSV
   */
  termsErrors: string[] = [];

  /**
   * Content Errors
   * - Checks actual content of CSV values
   * - Different requirements for different dataset types
   *    - Inventories: checks whether region ids match
   *    - Occurrences: checks fields in CSV to make sure they're the correct format
   *       - e.g. whether 'decimal_latitude' is a number
   *    - Point Inventories: checks whether region id in geometry CSV matches region_id in
   *      checklist csv
   */
  checklistContentErrors: string[] = [];

  selectedGeometryFormat: 'shapefile' | 'coordinates' = null;
  coordinateTypeSelected: boolean = false;

  // This will be true when the interface processing terms
  matchingTerms: boolean = true;

  geometryFormats = [
    {
      display: 'Shapefile',
      id: 'shapefile'
    },
    {
      display: 'CSV',
      id: 'coordinates'
    }
  ];

  map: PreviewMap = new PreviewMap();

  translations: any;

  constructor(
      private detector: ChangeDetectorRef,
      private builder: FormBuilder,
      private taxaService: TaxaService,
      private pointUploadService: PointUploadService,
      private inventoryUploadService: InventoryUploadService,
      private pointInventoryUploadService: PointInventoryUploadService,
      private router: Router,
      public dialog: MatDialog,
      public valid: ValidationService,
      private auth: AuthenticationService,
      public translationService: TranslationService

    ) {
    this.form = new FormGroup({
      taxon: new FormControl(null, Validators.required),
      datasetType: new FormControl({value: null, disabled: true}, Validators.required),
      geometryFormat: new FormControl()
    });
    this.translations = this.translationService.getTranslations();
  }

  ngOnInit() {
    this.taxaService.fetchTaxa()
      .subscribe(taxa => {
        // temporary hack to change "Plants (selection)" to "Plants"
        this.taxa = taxa.map(taxon => {
          if(taxon.taxa === 'plants') {
            taxon.title = 'Plants';
          }
          return taxon;
        })
      });
    this.auth.getUserInfo();
    this.auth.userInfoObs.subscribe(response => {
      this.userInfo = response;
      this.setUserInfo();
    });

    // Listen for created geometries from all the upload services
    this.pointInventoryUploadService.geomFeaturesCreated.subscribe(geomFeatures => {
      this.map.setData(geomFeatures, "point-inventory");
    });
    this.pointUploadService.geomFeaturesCreated.subscribe(geomFeatures => {
      this.map.setData(geomFeatures, "occurrence");
    });
    this.inventoryUploadService.geomFeaturesCreated.subscribe(geomFeatures => {
      this.map.setData(geomFeatures, "inventory");
    });
  }

  setUserInfo () {
    if(this.uploadService) {
      this.uploadService.metadata.setUserInfo(this.userInfo);
    }
    if(this.datasetType === 'Occurrence') {
      this.pointUploadService.recordedByValue = this.userInfo.fullname;
    }
  }

  nextStep() {
    this.currentStep++;
    // if we're on the review step, update the map
    if(this.currentStep === 4) {
      setTimeout(() => {
        this.map.createMap();
      }, 200);
    }
  }

  previousStep () {
    this.currentStep--;
  }

  stepComplete(stepNumber: number) {
    // errors
    const hasErrors = !!this.errors().length;

    // step 1
    const taxonomicGroupComplete = !!this.taxon;
    const datasetTypeComplete = !!this.datasetType;
    const geometryFormatComplete = !!this.selectedGeometryFormat;
    const coordinateTypeSelectionComplete = !!this.coordinateTypeSelected;

    // step 2
    const filesSelected = (this.uploadService) ? this.uploadService.filesReady() : false;
    const step2Errors = [
        ...this.csvErrors.occurrence,
        ...this.csvErrors.inventory,
        ...this.csvErrors.pointInventoryChecklist,
        ...this.csvErrors.pointInventoryGeometry,
        ...this.csvErrors.pointInventoryCombined,
        ...this.shapeErrors,
        ...this.checklistContentErrors
    ].length;

    // step 3
    const columnsChecked = this.columnsChecked;
    const step3Errors = [
        ...this.termsErrors
    ].length;

    // step 4
    const fileSizeCorrect = (this.uploadService && !this.uploadService.fileSizeError);
    const formValid = this.form.valid;
    const noErrors = !this.errors().length;
    const uploadFilesReady = (this.uploadService && this.uploadService.filesReady());

    let stepComplete = false;

    switch (stepNumber) {
      case 1:
      if (this.datasetType === 'Occurrence') {
        stepComplete = (taxonomicGroupComplete && datasetTypeComplete)
      } else {
        if(this.isPointInventory()) {
          stepComplete = (taxonomicGroupComplete && datasetTypeComplete && geometryFormatComplete && coordinateTypeSelectionComplete)
        } else {
          stepComplete = (taxonomicGroupComplete && datasetTypeComplete && geometryFormatComplete)
        }
      }
      break;
      case 2:
        stepComplete = filesSelected && !step2Errors;
      break;
      case 3:
        stepComplete = columnsChecked && !step3Errors;
      break;
      case 4:
        stepComplete = (columnsChecked && fileSizeCorrect && formValid && noErrors && uploadFilesReady && this.currentStep >= 4);
      break;
      case 5:
        stepComplete = (this.uploadService && this.uploadService.metadata && this.uploadService.metadata.requiredMetadataIsReady());
      break;
      default:
        stepComplete = false;
      break;
    }

    return stepComplete;
  }

  stepReady (stepNumber: number) {
    if(stepNumber === 1) {
      return true;
    }
    if(this.stepComplete(stepNumber - 1) && this.currentStep !== stepNumber) {
      return true;
    }
    return false;
  }

  isPointInventory () {
    return (this.datasetType === 'Inventory' && this.selectedGeometryFormat === 'coordinates');
  }


  resetUploadService () {
    if(this.uploadService) {
      this.uploadService.reset();
    }
  }

  resetFiles () {
    if(this.shapeFile) {
      this.shapeFile.reset();
    }
    if(this.inventoryFile) {
      this.inventoryFile.reset();
    }
    if(this.inventoryFile) {
      this.inventoryFile.reset();
    }
    if(this.pointFile) {
      this.pointFile.reset();
    }
    if(this.pointInventoryChecklistFile) {
      this.pointInventoryChecklistFile.reset();
    }
    if(this.pointInventoryGeometryFile) {
      this.pointInventoryGeometryFile.reset();
    }
    if(this.pointInventoryCombinedFile) {
      this.pointInventoryCombinedFile.reset();
    }
  }

  resetErrors () {
    // reset CSV errors for all dataset types
    this.csvErrors.inventory = [];
    this.csvErrors.occurrence = [];
    this.csvErrors.pointInventoryChecklist = [];
    this.csvErrors.pointInventoryGeometry = [];
    this.csvErrors.pointInventoryCombined = [];

    this.pointInventoryGeometryErrors = [];
    this.shapeErrors = [];
    this.termsErrors = [];
    this.checklistContentErrors = [];

    if(this.uploadService) {
      this.uploadService.fileSizeError = false;
    }
  }

  // Resets all items after first step, as well as all errors
  resetAll () {
    this.columnsChecked = false;
    this.matchingTerms = false;
    this.coordinateTypeSelected = false;
    this.resetUploadService();
    this.resetErrors();
    this.resetFiles();
    this.map.clearMap();
    this.detector.detectChanges();
  }

  /**
   * Set this.uploadService depending on:
   *   1. dataset type (occurrences)
   *   2. dataset type + geometry type (inventories)
   *
   * Then, set user info if it's loaded
   */
  selectUploadService () {
    this.uploadService = null;
    if(this.datasetType === 'Occurrence') {
      this.uploadService = this.pointUploadService;
    } else if (this.datasetType === 'Inventory' && this.selectedGeometryFormat) {
      if(this.selectedGeometryFormat === 'coordinates') {
        this.uploadService = this.pointInventoryUploadService;
      } else if (this.selectedGeometryFormat === 'shapefile') {
        this.uploadService = this.inventoryUploadService;
      }
    }
    // set user info
    if(this.uploadService && this.userInfo) {
      this.setUserInfo();
    }

  }

  datasetTypeChange() {
    this.selectedGeometryFormat = null;
    this.resetAll();
    this.selectUploadService();
    this.resetUploadService();
  }

  inventoryGeometryTypeChange () {
    this.resetAll();
    this.selectUploadService();
    this.resetUploadService();
    this.pointInventoryUploadService.metadata.geometryType = this.selectedGeometryFormat;
  }

  pointInventoryNumFileChange() {
    this.csvErrors.pointInventoryCombined = [];
    this.csvErrors.pointInventoryChecklist = [];
    this.csvErrors.pointInventoryGeometry = [];
    this.pointInventoryGeometryErrors = [];
    this.resetFiles();
    this.pointInventoryUploadService.resetFiles();
    this.pointInventoryUploadService.numFilesChange();
  }

  errors() {
    return [
      ...this.csvErrors.inventory,
      ...this.csvErrors.occurrence,
      ...this.csvErrors.pointInventoryChecklist,
      ...this.csvErrors.pointInventoryGeometry,
      ...this.csvErrors.pointInventoryCombined,
      ...this.pointInventoryGeometryErrors,
      ...this.shapeErrors,
      ...this.termsErrors,
      ...this.checklistContentErrors
    ];
  }

  updateTaxonomy() {
    this.taxaService.selectTaxon(this.taxon);
    this.form.controls.datasetType.enable();
  }

  blankHeadersErrors (file) {
    if(!this.valid.noBlankHeaders(file)) {
      let err = this.translationService.getTranslation(["files-form:upload-files:error-text","emptyColumnHeaders"]);
      return [err];
    }
    return [];
  }

  /**
   * File Results Functions
   */

   // fire after any time a new file is selected
   fileResults () {
     // reset file size errors
     if(this.uploadService) {
       this.uploadService.fileSizeError = false;
     }
   }

    /**
     * Inventory file upload results functions
     */
    shapeFileResults(results: FileReaderResults) {
      this.fileResults();
      this.shapeErrors = results.errors;
      if (this.shapeErrors.length) { return; }
      this.inventoryUploadService.getShapeFileResults(results);
      this.detector.detectChanges();
    }

    // Receives results from CsvFileReader, which checks for CSV errors and outputs the results
    inventoryFileResults(results: FileReaderResults) {
      this.fileResults();
      // reset some things
      this.columnsChecked = false;
      this.termsErrors = [];
      this.checklistContentErrors = [];
      this.csvErrors.inventory = [...results.errors, ...this.blankHeadersErrors(results), ...this.valid.noWhiteSpace(results)];
      if (this.csvErrors.inventory.length) { return; }
      this.inventoryUploadService.getChecklistResults(results);
      this.uploadService.terms.reset();
      this.uploadService.initVerbatims();
      this.uploadService.terms.findDefaultMappings();
      this.detector.detectChanges();
    }

    /**
     * Point Inventory file upload results functions
     *
     * 1. For datasets that have their geometry and checklist split, the following two functions
     *   be called:
     *    - pointInventoryChecklistFileResults()
     *    - pointInventoryGeometryFileResults()
     * 2. For datasets that have their geometry and checklist in the same file, then the third
     *  function will be called:
     *    - pointInventoryCombinedFileResults()
     */
    pointInventoryChecklistFileResults(results: FileReaderResults) {
      this.fileResults();
      this.columnsChecked = false;
      this.termsErrors = [];
      this.checklistContentErrors = [];
      this.csvErrors.pointInventoryChecklist = [...results.errors, ...this.blankHeadersErrors(results), ...this.valid.noWhiteSpace(results)];
      if (this.csvErrors.pointInventoryChecklist.length) { return; }
      // set results in point inventory upload service
      this.pointInventoryUploadService.saveChecklistResults(results);
      this.pointInventoryUploadService.terms.reset();
      this.pointInventoryUploadService.initVerbatims();
      this.pointInventoryUploadService.terms.findDefaultMappings();
      this.detector.detectChanges();
    }

    pointInventoryGeometryFileResults(results: FileReaderResults) {
      this.fileResults();
      this.columnsChecked = false;
      this.termsErrors = [];
      this.checklistContentErrors = [];
      this.pointInventoryGeometryErrors = [];
      this.csvErrors.pointInventoryGeometry = [...results.errors, ...this.blankHeadersErrors(results), ...this.valid.noWhiteSpace(results)];
      this.pointInventoryGeometryErrors = this.pointInventoryUploadService.validGeometryFile(results);
      if (this.csvErrors.pointInventoryGeometry.length || this.pointInventoryGeometryErrors.length) { return; }
      // set results in point inventory upload service
      this.pointInventoryUploadService.saveGeometryFileResults(results);
      this.detector.detectChanges();
    }

    pointInventoryCombinedFileResults(results: FileReaderResults) {
      this.fileResults();
      this.columnsChecked = false;
      this.termsErrors = [];
      this.checklistContentErrors = [];
      this.csvErrors.pointInventoryCombined = [...results.errors, ...this.blankHeadersErrors(results), ...this.valid.noWhiteSpace(results)];
      if (this.csvErrors.pointInventoryCombined.length) { return; }
      // set results in point inventory upload service
      this.pointInventoryUploadService.saveCombinedFileResults(results);
      this.pointInventoryUploadService.terms.reset();
      this.pointInventoryUploadService.initVerbatims();
      this.pointInventoryUploadService.terms.findDefaultMappings();
      this.detector.detectChanges();
    }

    /**
     * Occurrence file upload results function
     */
    pointFileResults(results: FileReaderResults) {
      this.fileResults();
      this.columnsChecked = false;
      this.termsErrors = [];
      this.checklistContentErrors = [];
      this.csvErrors.occurrence = [...results.errors, ...this.blankHeadersErrors(results), ...this.valid.noWhiteSpace(results)];
      if (this.csvErrors.occurrence.length) { return; }
      this.pointUploadService.getPointResults(results);
      this.uploadService.terms.reset();
      this.uploadService.initVerbatims();
      this.uploadService.terms.findDefaultMappings();
      this.detector.detectChanges();
    }

  /**
   * End File Results Functions
   */

  matchColumns($event) {
    this.matchingTerms = true;
    this.columnsChecked = false;
    this.termsErrors = [];
    this.checklistContentErrors = [];
    $event.stopPropagation();
    const dialog = this.dialog.open(MatchColumnsDialogComponent, {
      width: '448px',
      disableClose: true,
      data: {
         uploadService: this.uploadService
      }
    });

    dialog.afterClosed().subscribe(reply => {
      if (!reply) {
        this.matchingTerms = false;
        return;
      }
      this.columnsChecked = true;
      this.uploadService.terms.remapVerbatimsToTerms();
      const termsErrors = this.uploadService.termsErrors();
      if (termsErrors.length) {
        this.termsErrors = termsErrors;
      } else {
        this.checklistContentErrors = this.uploadService.checklistContentErrors();
        if(!this.checklistContentErrors.length) {
          this.uploadService.createFeatures();
        }
      }
      this.matchingTerms = false;
    });
  }

  openCoordinateTypeModal() {
    const dialog = this.dialog.open(CoordinateTypeModalComponent, {
      width: '600px'
    });
    dialog.afterClosed().subscribe(reply => {
      if(reply) {
        this.coordinateTypeSelected = true;
        this.saveCoordinateTypeSelections(reply);
      } else {
        this.coordinateTypeSelected = false;
      }
    });
  }

  saveCoordinateTypeSelections (selections: CoordinateTypeSelection) {
    this.map.clearMap();
    this.pointInventoryUploadService.setCoordinateTypeSelections(selections);
    if(this.columnsChecked === true && !this.uploadService.hasErrors()) {
      this.uploadService.createFeatures();
    }
  }

  nextPage() {
    if (this.datasetType === 'Occurrence') {
      this.router.navigate(['points-save']);
    } else if (this.datasetType === 'Inventory') {
      this.router.navigate(['inventory-save']);
    }
  }
}



