import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FileReaderComponent, FileReaderResults } from '../file-reader.component';
import * as Papa from 'papaparse/papaparse.min.js';

@Component({
  selector: 'mol-csv-file-reader',
  templateUrl: './csv-file-reader.component.html',
  styleUrls: ['./csv-file-reader.component.css', '../file-reader.component.css']
})
export class CsvFileReaderComponent extends FileReaderComponent implements OnInit {

  parseFile(file) {
    const self = this;

    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      // worker: true,   // We need this for large files

      complete: function(output) {
        let errors = {};

        // Remove blank (or partially blank) rows
        const headerLen = output.meta.fields.length;
        output.data = output.data
          .filter(rec => Object.keys(rec).length >= headerLen);

        // Format errors and remove errors caused by blank lines
        output.errors = output.errors
          .filter(err => err.code !== 'TooFewFields');

        // Empty file is an error
        if (!output.data.length) {
          output.errors.push('No data records found.');
        }

        output.errors.forEach(error => errors[error.message] = 1);

        self.results.emit({
          controlName: self.name,
          isValid: !Object.keys(errors).length,
          fileName: self.fileName,
          fileReaderOutput: output,
          errors: Object.keys(errors),
        });
      }
    });
  }

}



