import {
  Component,
  OnInit,
  Input,
  ChangeDetectorRef,
  ViewEncapsulation,
  OnDestroy
} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MapService } from '../../services/map.service';
import { ActivatedRoute } from '@angular/router';
import { OverlayMapTypeService } from '../../services/overlay-map-type.service'
import { Subject } from 'rxjs/Subject';

interface RegionInfo {
  region: string,
  counts: number,
  display: string,
  color_index: number
}

@Component({
  selector: 'mol-display-dataset-map',
  templateUrl: './display-dataset-map.component.html',
  styleUrls: ['./display-dataset-map.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DisplayDatasetMapComponent implements OnInit, OnDestroy {
  private tileUrlSrc = new Subject<string>();
  private tileUrl = this.tileUrlSrc.asObservable();

  private _activeTabIndex: number = 0;
  private _mapInitialised: boolean = false;

  species_list_sorted = [];
  regions: RegionInfo[] = [];

  currentlySelectedSpecies: string = 'all';
  currentlySelectedRegion: string = 'all';
  currentlySelectedERMFeature: string = 'all';
  inventoryColorBySelection: string = 'region';

  urls = {
    range: 'https://carto.mol.org/user/mol/api/v1/map/named/dataset-overview-map-range',
    points: 'https://carto.mol.org/user/mol/api/v1/map/named/dataset-overview-map-points',
    localinv:  {
      region: 'https://carto.mol.org/user/mol/api/v1/map/named/dataset-overview-map-localinv',
      species: 'https://carto.mol.org/user/mol/api/v1/map/named/dataset-overview-map-localinv-numspecies'
    }
  }

  requests = {
    overlays: null,
    popup: null
  };

  @Input() overview: any;
  @Input() records: any;

  @Input('activeTabIndex')
  set activeTabIndex(val: number) {
    this._activeTabIndex = val;
    if (this._activeTabIndex == 1 && !this._mapInitialised) {
      this.initMapView();
    }
  }

  constructor(
    public http: HttpClient,
    private route: ActivatedRoute,
    private mapState: MapService,
    private changeDetector: ChangeDetectorRef
  ) {}

  ngOnInit() {
    const self = this;

    if(this.overview.species_list) {
      this.species_list_sorted = this.overview.species_list.map((val, index, arr) => {
        return val.scientificname;
      }).sort();
    }

    if(this.overview.sites_list) {
      this.regions = this.overview.sites_list.map((value,index,array) => {
        const regionTrim = value.region.replace(/ /g, '');
        const regionInt = parseInt(regionTrim);
        if(regionInt) {
          value.display = 'Region ' + regionInt;
        } else {
          value.display = value.region;
        }
        return value;
      });
      /**
        * The following is a hack to get the colors to sync with the features returned by Carto
        *   1. Sort region by region name to get the appropriate color index
        *   2. Add the index (+ 1) to the array of region objects
        *   3. Re-sort to display properly in map dropdown
        */
      this.regions.sort((a, b) => {
        if (a.region < b.region) { return -1 }
        if (a.region > b.region) { return 1 }
        return 0;
      });
      for(let i = 0; i < this.regions.length; i++) {
        this.regions[i].color_index = i + 1;
      }
      this.regions.sort((a, b) => {
        const aTrim = a.region.replace(/ /g, '');
        const bTrim = b.region.replace(/ /g, '');
        const aInt = parseInt(aTrim);
        const bInt = parseInt(bTrim);
        if(aInt && bInt) {
          return aInt - bInt;
        } else {
          if (a.region < b.region) { return -1 }
          if (a.region > b.region) { return 1 }
          return 0;
        }
      });
    }

    this.getOverlays();
  }

  getRegionInfo (region) {
    let returnRegion = this.regions.filter(value => value.region === region);
    return returnRegion[0];
  }

  ngOnDestroy () {
    for(let request in this.requests) {
      if(this.requests[request]) {
        this.requests[request].unsubscribe();
      }
    }
  }

  getOverlays () {
    const self = this;
    let dataset_type = this.overview.summary.product_type;
    let dataset_id = this.route.snapshot.paramMap.get('dataset_id');
    let scientificname = (this.currentlySelectedSpecies === 'all') ? '%' : this.currentlySelectedSpecies;

    let CARTO_MAP_API = "";

    switch (dataset_type) {
      case 'range':
        CARTO_MAP_API = this.urls[dataset_type];
      break;
      case 'points':
        CARTO_MAP_API = this.urls[dataset_type];
      break;
      case 'localinv':
        if(this.inventoryColorBySelection === 'region') {
          CARTO_MAP_API = this.urls[dataset_type].region;
        } else if (this.inventoryColorBySelection === 'species') {
          CARTO_MAP_API = this.urls[dataset_type].species;
        }
      break;
    }

    let body = {
      dataset_id,
      scientificname,
      region: null,
      geom_id: null,
      molid: null
    };

    if(dataset_type === 'localinv') {
      let region = (this.currentlySelectedRegion === 'all') ? '%' : this.currentlySelectedRegion;
      body.region = region;
    }

    if(dataset_type === 'range') {
      body.molid = (this.currentlySelectedERMFeature === 'all') ? '%' : this.currentlySelectedERMFeature;
    }

    if(this.requests.overlays) {
      this.requests.overlays.unsubscribe();
    }
    this.requests.overlays = this.http.post(CARTO_MAP_API, body)
      .subscribe(result => {
        // Set the map url
        let tile_url = 'https://carto.mol.org/mol/api/v1/map/' + result['layergroupid'] + '/{z}/{x}/{y}.png';
        let grid_url = 'https://carto.mol.org/mol/api/v1/map/' + result['layergroupid'] + '/0/{z}/{x}/{y}.grid.json';

        self.mapState.overlaySub.next(new OverlayMapTypeService(tile_url, 0.8, grid_url, self.http, {
          click: (data) => {
            self.mapClickHandler(data);
          }, move: (data) => {
            self.mapMouseoverHandler(data);
          }
        }));
      }, error => {
        console.error('Error retrieving overlays');
      });
  }

  /* Zoom the map to the extent of the data */
  private initMapView() {

    if(
      this.overview.summary.extents.minx !== null &&
      this.overview.summary.extents.miny !== null &&
      this.overview.summary.extents.maxx !== null &&
      this.overview.summary.extents.maxy !== null
      ) {
      let lowLeft = [this.overview.summary.extents.minx, this.overview.summary.extents.miny],
        topLeft = [this.overview.summary.extents.minx, this.overview.summary.extents.maxy],
        topRight = [this.overview.summary.extents.maxx, this.overview.summary.extents.maxy],
        lowRight = [this.overview.summary.extents.maxx, this.overview.summary.extents.miny];

      let bounds: GeoJSON.Feature<GeoJSON.Polygon> = {
        type: 'Feature',
        geometry: {
          type: 'Polygon',
          coordinates: [[
            lowLeft,
            lowRight,
            topRight,
            topLeft,
            lowLeft
          ]]
        },
        properties: null
      };

      this.mapState.boundsSub.next(bounds);
      this._mapInitialised = true;
    }
  }



  private makePopup (data) {
    const self = this;

    const createSpeciesList = (data) => {
      let scientificnames = [];

      if(data.data && data.data.scientificnames) {
        scientificnames = data.data.scientificnames.split(',');
      } else if (data.data && data.data.scientificname) {
        scientificnames.push(data.data.scientificname);
      }

      scientificnames.sort();

      let content = '';

      if(scientificnames.length > 1) {
        content += `
          <div class='info-window-header species-multiple'>
            <span class='species-count'> ${scientificnames.length} Species </span>
          </div>
          <ul>
        `;

        for(let scientificname of scientificnames) {
          let speciesPageLink = 'https://mol.org/species/map/' + scientificname.replace(' ', '_');
          content += `
            <li>
              <a href='${speciesPageLink}'> ${scientificname} </a>
            </li>
          `;
        }

        content += '</ul>';
      } else if(scientificnames[0]) {
        let speciesNameLink = 'https://mol.org/species/map/' + scientificnames[0].replace(/ /g, '_');
        content += `
          <div class='info-window-header species-single'>
            <img src='//mol.org/static/img/groups/taxa_${this.overview.summary.taxa}.png'/>
            <a href='${speciesNameLink}'>${scientificnames[0]}</a>
          </div>
        `;
      } else {
        content += `
          <div class='info-window-header species-single'>
            <img src='//mol.org/static/img/groups/taxa_${this.overview.summary.taxa}.png'/>
            (No Scientific Name)
          </div>
        `;
      }

      return content;
    }

    const createFeatureMetadataTable = (featureMetadata) => {
      let excludeValues = ['Geom Id', 'Dataset Id', 'Decimallatitude', 'Decimallongitude'];
      let content = `
        <table class='table table-striped table-sm feature-metadata-table'>
          <tbody>
      `;
      for(let row of featureMetadata) {
        let keepRow = (row.title && excludeValues.indexOf(row.title) === -1);
        if(keepRow && row.value) {
          content += `
            <tr>
              <td>${row.title}</td>
              <td>${row.value}</td>
            </tr>
          `;
        }
      }
      content += `
          </tbody>
        </table>
      `;

      return content;
    }

    const createLoadingPopup = () => {
      // show a loading popup while the content loads
      this.mapState.popupSub.next({
        content: `
          <div class='dataset-map-info-window'>
            <div class="refresh-container">
              <i class='fa fa-refresh fa-spin'></i>
            </div>
          </div>
        `,
        position
      });
    }

    const createRangeMapPopup = () =>{
      // get range map feature metadata
      if(this.requests.popup) {
        this.requests.popup.unsubscribe();
      }
      this.requests.popup = this.http.get(`https://api.mol.org/1.x/species/featuremetadata?dataset_id=${data.data.dataset_id}&molid=${data.data.molid}`).subscribe(result => {
        const speciesList = createSpeciesList(data);
        const featureMetadataTable = createFeatureMetadataTable(result);
        const editButton = `
          <div class='info-window-edit'>
            <div>
              <a href='./range-map/draw/${data.data.dataset_id}/${data.data.molid}' target='_blank'>
                <button class='btn btn-primary'>
                  Edit Feature
                </button>
              </a>
            </div>
          </div>
        `;
        const content = [popupHead, speciesList, featureMetadataTable, editButton, popupFoot].join('');
        this.mapState.popupSub.next({content, position});
        this.changeDetector.detectChanges();
      });
    }

    const createPointPopup = () => {
      // get point feature metadata
      if(this.requests.popup) {
        this.requests.popup.unsubscribe();
      }
      this.requests.popup = this.http.get(`https://api.mol.org/1.x/species/featuremetadata?dataset_id=${data.data.dataset_id}&molid=${data.data.molid}`).subscribe(result => {
        const featureMetadataTable = createFeatureMetadataTable(result);
        const content = [popupHead, speciesList, featureMetadataTable, popupFoot].join('');
        self.mapState.popupSub.next({content, position});
        self.changeDetector.detectChanges();
      });
    }

    const createInventoryPopup = () => {
      let region = '';
      if(data.data.region) {
        let regionInfo = this.getRegionInfo(data.data.region);
        region = regionInfo.display;
      }
      const regionDiv = `<div class='info-window-header region'> ${region} </div>`;
      const content = [popupHead, regionDiv, speciesList, popupFoot].join('');
      this.mapState.popupSub.next({content, position});
      this.changeDetector.detectChanges();
    }

    const position = data.coords;
    const dataset_type = this.overview.summary.product_type;
    const canRetrieveFeatureMetadata = (data.data && data.data.dataset_id && data.data.molid);
    const speciesList = createSpeciesList(data);

    // popup HTML
    const popupHead = `
      <div class='dataset-map-info-window'>
        <div class='dataset-map-info-window-wrapper'>
    `;
    const popupFoot = `
        </div>
      </div>
    `;

    // depending on the type of dataset, create different types of popups
    if(dataset_type === 'points' && canRetrieveFeatureMetadata) {
      createLoadingPopup();
      createPointPopup();
    } else if (dataset_type === 'localinv') {
      createInventoryPopup();
    } else if (dataset_type === 'range' && canRetrieveFeatureMetadata) {
      createLoadingPopup();
      createRangeMapPopup();
    }
  }

  private mapClickHandler = function(data) {
    this.makePopup(data);
  }

  // TODO: Add code for custom mouseover handling
  private mapMouseoverHandler = function(data) {}

  public speciesChange (event) {
    this.mapState.popupSub.next(null);
    this.currentlySelectedERMFeature = 'all';
    this.currentlySelectedRegion = 'all';
    this.getOverlays();
  }

  public regionChange (event) {
    this.mapState.popupSub.next(null);
    this.currentlySelectedSpecies = 'all';
    this.currentlySelectedERMFeature = 'all';
    this.getOverlays();
  }

  public rangeFeatureChange (event) {
    this.mapState.popupSub.next(null);
    this.currentlySelectedRegion = 'all';
    this.currentlySelectedSpecies = 'all';
    this.getOverlays();
  }

  public inventoryColorByChange (event) {
    this.getOverlays();
  }

}



