/**
 * A multi-purpose info / error modal for use in the Expert Range Mapper
 * Currently used to show various error and loading information
 */

import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';

interface ModalData {
    type: 'info' | 'error';
    text: string;
    title: string;
    routerLink: string[];
}

@Component({
  selector: 'mol-range-map-info-modal',
  templateUrl: './range-map-info-modal.component.html',
  styleUrls: ['./range-map-info-modal.component.scss']
})
export class RangeMapInfoModalComponent implements OnInit {

  title = '';
  infoText = '';
  type: 'info' | 'error' = 'info';
  routerLink: string[] = null;

  constructor(
    public dialogRef: MatDialogRef<RangeMapInfoModalComponent>,
    public router: Router,
    @Inject(MAT_DIALOG_DATA) public data: ModalData
  ) {
      this.infoText = data.text;
      this.type = data.type;
      this.title = data.title || '';
      if (data.routerLink) {
        this.routerLink = data.routerLink;
      }
  }

  continue () {
    if (this.routerLink) {
      this.router.navigate(this.routerLink);
    }
    this.dialogRef.close();
  }

  ngOnInit() {
  }

}



