import { Pipe, PipeTransform } from '@angular/core';
import { DatasetListItem } from '../services/dataset.service';

@Pipe({
  name: 'filterDatasets'
})
export class FilterDatasetsPipe implements PipeTransform {

  transform(datasets: DatasetListItem[], currentDatasetType: 'all' | 'localinv' | 'points' | 'range'): DatasetListItem[] {
    return datasets.filter((dataset) => {
        if(currentDatasetType === 'all') {
            return true;
        } else {
            if(dataset.product_type === currentDatasetType) {
                return true;
            }
        }
        return false;
    });
  }

}



