import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { RangeMapService } from '../../services/range-map.service';
import { Router } from '@angular/router';

@Component({
  selector: 'mol-submit-range-map-modal',
  templateUrl: './submit-range-map-modal.component.html',
  styleUrls: ['./submit-range-map-modal.component.css']
})
export class SubmitRangeMapModalComponent implements OnInit {

  constructor(
    public rangeMapService: RangeMapService,
    public router: Router,
    public dialogRef: MatDialogRef<SubmitRangeMapModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  addFeature () {
    this.dialogRef.close();
  }

  toDatasets () {
    this.router.navigate(['dataset', this.rangeMapService.dataset_id]);
    this.dialogRef.close();
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {

  }

}



