import { Component, OnInit, Input } from '@angular/core';
import { DatasetService } from '../../services/dataset.service';

@Component({
  selector: 'mol-display-dataset-eml',
  templateUrl: './display-dataset-eml.component.html',
  styleUrls: ['./display-dataset-eml.component.css']
})
export class DisplayDatasetEmlComponent implements OnInit {

  @Input() dataset_id: any;
  @Input() metadataService: any;
  published = false;
  eml;

  constructor(
    private datasetService: DatasetService,
  ) { }

  ngOnInit() {
    if (!this.eml) {
      this.datasetService.fetchEml(this.dataset_id)
      .subscribe(
        success => {
          this.eml = success;
        },
        error => {}
      );
    }

  }

  formatDate(iso:string) {
    return new Date(iso).toDateString();
  }

  getTaxons() {
    return Object.keys(this.eml.taxonomy)
      .map(key => {
        return {
          rank: key[0].toUpperCase() + key.slice(1).toLowerCase(),
          taxon: this.eml.taxonomy[key]};
      });
  }

}



