import { Injectable, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/from';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/retry';
import 'rxjs/add/operator/startWith';
import * as TurfHelpers from '@turf/helpers';
import TurfEnvelope from '@turf/envelope';
import { ApiService } from './api.service';

/**
 * Stolen from MOL/Habitats
 */

export interface SpeciesDescription {
  content: string;
  source: string;
  lang: string;
}

export interface TaxonName {
  name: string;
  lang: string;
}

export interface ImageAsset {
  asset_id: string,
  url: string;
  license: string;
  copyright: string;
}

export interface SpeciesInfo {
  scientificname: string;
  commonname: string;
  info: SpeciesDescription[];
  redlist: string;
  rangemap: string;
  sequenceid: number;
  family: TaxonName[];
  tc_id: string;
  redlist_citation_short: string;
  redlist_citation_full: string;
  image: ImageAsset;
}

export interface SpeciesMapType {
  id: 'localinv'|'range'|'regionalchecklist'|'griddedrangeatlas'|'points'
}

export interface SpeciesDataset {
  product_type: SpeciesMapType['id'],
  type_title: string,
  bounds: {
    northEast: { lat: number, lng: number },
    southWest: { lat: number, lng: number }
  },
  no_rows: number,
  visible: boolean,
  dataset_id: string,
  public: boolean,
  dataset_title: string
}

export interface DatasetType {
  id: string;
  title: string;
  visible?: boolean;
  collapsed?:boolean;
  partial?: boolean;
  datasets: SpeciesDataset[]
}

@Injectable()
export class SpeciesDatasetService {

  private infoSource = new Subject<SpeciesInfo>();
  public info = this.infoSource.asObservable(); //

  private datasetsSource = new Subject<SpeciesDataset[]>();
  public datasets = this.datasetsSource.asObservable(); //

  public dataTypesSource = new Subject<DatasetType[]>();
  public dataTypes = this.dataTypesSource.asObservable(); //

  public boundsSource = new Subject<GeoJSON.Feature<GeoJSON.Polygon>>();
  public bounds = this.boundsSource.asObservable(); //

  public requests: any = {}

  constructor(
    private http: Http,
    private router: Router,
    private route: ActivatedRoute,
    private api: ApiService
    ) {
  }

  clear() {
    /*
     * Clear current species model and resolve api calls.
     */
    this.cancelRequests();
    this.boundsSource.next(null);
    this.dataTypesSource.next(null);
    this.datasetsSource.next(null);
    this.infoSource.next(null);
  }

  private cancelRequests() {
    Object.keys(this.requests).forEach((k,i,a) => {
      this.requests[k].unsubscribe();
      delete(this.requests[k]);
    })
  }

  getBounds(datasets: SpeciesDataset[]) {
    let features = datasets.map(dataset => {
        let sw = [dataset.bounds.southWest.lng,dataset.bounds.southWest.lat],
          se = [dataset.bounds.northEast.lng, dataset.bounds.southWest.lat],
          ne = [dataset.bounds.northEast.lng, dataset.bounds.northEast.lat],
          nw = [dataset.bounds.southWest.lng, dataset.bounds.northEast.lat];
        return TurfHelpers.polygon([[sw,se,ne,nw,sw]]);
    });
    let fc = TurfHelpers.featureCollection(features);
    return TurfEnvelope(fc);
  }

  private getAllBounds(datasets: SpeciesDataset[]) {
    let visibleDatasets = datasets.filter((d) => (['regionalchecklist','points'].indexOf(d.product_type)<0) );
    let bounds = this.getBounds(visibleDatasets);
    this.boundsSource.next(bounds);
  }

  getSpeciesInfo(scientific) {
    this.requests.info =
      this.http.get(
        `${this.api.root}/species/info`,
        { params: { scientificname: scientific } }
      ).retry(3).subscribe(
        (r) => {
          let result = r.json();
          if (result.length > 0) {
            this.infoSource.next(result[0]);
          }
          delete(this.requests.info);
        },
        (error) => {

        }
      );
  }

  getDatasets(scientific: string) {
    this.requests.datasets = this.http.get(
      `${this.api.root}/species/datasets`,
      { params: { scientificname: scientific } }
    ).retry(3).subscribe(
      (r) => {
        let result = r.json(),
        types = {}, typeArr: DatasetType[] = [];

        if(result.length > 0) {
          result.map(d => {
            types[d.product_type] = (types[d.product_type] || {
              title: d.type_title,
              id: d.product_type,
              visible: (d.product_type !== 'regionalchecklist'),
              collapsed: true,
              partial: false,
              datasets: []});
            types[d.product_type].datasets.push(d);
          })

          Object.keys(types).forEach(t => typeArr.push(types[t]));
          this.dataTypesSource.next(typeArr);
          this.datasetsSource.next(result);
          this.getAllBounds(result);
        }

        delete this.requests.datasets;
      },
      (error) => {

      }
    );
  }
}



