import {
  Component,
  ViewEncapsulation,
  ElementRef,
  ViewChild,
  ChangeDetectorRef,
  AfterViewInit,
  AfterViewChecked,
  OnInit,
  Input,
  Output,
  ViewContainerRef,
  ComponentFactoryResolver,
  ComponentFactory,
  ComponentRef,
  EventEmitter
} from '@angular/core';
import { NgStyle } from '@angular/common';
import { Router } from '@angular/router';
import { Http } from '@angular/http';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import TurfEnvelope from '@turf/envelope';
import { featureCollection, feature } from '@turf/helpers';

// Services
import { MapService } from '../../services/map.service';
import { RangeMapService } from '../../services/range-map.service';
import { SpeciesDatasetService } from '../../services/species-dataset.service';
import { OverlayMapTypeService } from '../../services/overlay-map-type.service';

import { MatSpinner } from '@angular/material';
import { MapStyles } from './map-styles';

// Components
import { MapComponent } from '../map/map.component';
import { FeatureAttributesDialogComponent } from '../feature-attributes-dialog/feature-attributes-dialog.component';
import { RangeMapInfoModalComponent } from '../../components/range-map-info-modal/range-map-info-modal.component';

type RangeMapFeature = GeoJSON.FeatureCollection<GeoJSON.Polygon | GeoJSON.MultiPolygon>;

@Component({
  selector: 'mol-range-map',
  templateUrl: './range-map.component.html',
  styleUrls: ['./range-map.component.scss'],
  providers: [MapStyles],
  encapsulation: ViewEncapsulation.None
})
export class RangeMapComponent extends MapComponent {

  drawingManager: google.maps.drawing.DrawingManager;
  mode = 'draw';
  drawnPolygons = [];
  selectedFeature: google.maps.Polygon = null;
  polygonStyles = {
    default: {
      fillOpacity: 0
    },
    selected: {
      fillOpacity: 0.7
    }
  };

  deleteVertexPopup = {
    polygon: null,
    vertex: null,
    visible: false,
    left: 0,
    top: 0,
    width: 50,
    height: 50
  }

  requests = {
    getMOLLayers: null
  }

  @ViewChild('node') node: ElementRef;

  constructor(
    private http: Http,
    private changeDetector: ChangeDetectorRef,
    public rangeMapService: RangeMapService,
    private router: Router,
    private speciesDataset: SpeciesDatasetService,
    public dialog: MatDialog,
    public componentFactoryResolver: ComponentFactoryResolver,
    public viewContainerRef: ViewContainerRef,
    public mapStyles: MapStyles,
    public mapState: MapService
  ) {
    super(componentFactoryResolver, viewContainerRef, mapStyles, mapState);
    const self = this;

    this.speciesDataset.dataTypes.subscribe((results) => {
      this.getMOLLayers(results);
    });

    this.mapState.layersObs.subscribe((layerList) => {
      // subscriptions screw up `this` bindings regardless of arrow function
      this.setLayers.bind(self)(layerList);
    });

    if(this.rangeMapService.feature.mode === 'new') {
      this.speciesDataset.bounds.subscribe(result => {
        this.mapState.boundsSub.next(result);
      });
    } else {
      this.rangeMapService.feature.getFeatureSubj.subscribe((geoJson: RangeMapFeature) => {
        if(geoJson) {
          this.startMapWithGeoJSON(geoJson);
          this.editMode();
        }
      });
    }

    this.rangeMapService.feature.featureSubmitSubj.subscribe(overview => {
      this.reset();
    });
  }

  addMOLOverlay(o) {
    // remove old MOL overlay, if it exists
    if(this.overlayMapType) {
      this.map.overlayMapTypes.forEach((overlay, index) => {
        if(overlay === this.overlayMapType) {
          this.map.overlayMapTypes.removeAt(index);
          return false;
        }
      });
    }

    // find length of overlays array
    let top = this.map.overlayMapTypes.getLength();

    // add new MOL overlay on top of other layers
    this.overlayMapType = o;
    this.map.overlayMapTypes.setAt(top, this.overlayMapType);
  }

  setLayers (layerList) {
    if(layerList) {
      const visibleLayers = layerList.forEach((layer, index) => {
        if(!layer.visible) {
          this.map.overlayMapTypes.removeAt(index);
        } else {
          this.map.overlayMapTypes.setAt(index, layer.overlay);
        }
      });
    }
    // put the MOL overlay on top
    if(this.overlayMapType) {
      this.addMOLOverlay(this.overlayMapType);
    }
  }

  // Take the geoJson returned from the server and transform it to a Google Maps format
  startMapWithGeoJSON (geoJSON) {
    const allowedGeometryTypes = ["Polygon", "MultiPolygon"];

    /**
     *  For some reason, google has two different methods for storing features:
     *   1. google.maps.Polygon
     *   2. google.maps.Data.Polygon
     *
     * We're given a google.maps.Data.Polygon (or a google.maps.Data.MultiPolygon) and
     * have to convert to a regular google.maps.Polygon. To do this, we have to pull the
     * geometry out of the Data.Polygon and instantiate a new  google.maps.Polygon with that
     * geometry.
     *
     */
    const dataPolygonGeometryToPolygon = (geometry: google.maps.Data.Polygon) => {
      const linearRings: google.maps.Data.LinearRing[] = geometry.getArray();
      const ringCoordinates: google.maps.LatLng[][] = linearRings.map(value => {
        return value.getArray();
      });
      // create an empty polygon and set the paths to the ringCoordinates
      const polygon = new google.maps.Polygon();
      polygon.setPaths(ringCoordinates);
      return polygon;
    }

    // Grab geometry out of google.maps.Data.Feature and create a google.maps.Polygon instead
    const convertFeatureToPolygons = (feature: google.maps.Data.Feature) => {

      // first get the geometry of the feature
      const dataPolygonGeometry: google.maps.Data.Geometry = feature.getGeometry();

      // check to make sure the geometry type is Polygon or MultiPolygon
      if(allowedGeometryTypes.indexOf(dataPolygonGeometry.getType()) == -1) {
        console.error("Feature is not one of: " + allowedGeometryTypes.join(", "));
      }

      // If the returned geometry is a Polygon
      if(dataPolygonGeometry instanceof google.maps.Data.Polygon) {
        let polygon = dataPolygonGeometryToPolygon(dataPolygonGeometry);
        return [polygon];

      // If the returned geometry is a MultiPolygon
      } else if (dataPolygonGeometry instanceof google.maps.Data.MultiPolygon) {
        let polygons = [];
        let polygonGeometries = dataPolygonGeometry.getArray();
        for (let polygon of polygonGeometries) {
          polygons.push(dataPolygonGeometryToPolygon(polygon));
        }
        return polygons;
      }

      return false;
    }

    // make sure GeoJson a Feature Collection so it plays nicely with Google Maps
    let fc = geoJSON;
    if(geoJSON.type && geoJSON.type !== "FeatureCollection") {
      if(geoJSON.type === "Feature") {
        fc = featureCollection([
          geoJSON
        ]);
      } else {
        fc = featureCollection([
          feature(geoJSON)
        ]);
      }
    }

    /**
     * Create google.maps.Data container to:
     * 1. Morph feature collection to an array of google.maps.Polygons
     * 2. Set the polygon to editable
     * 3. Add the polygon to the map
     * 4. Add the polygon to the list of polygons (this.drawnPolygons)
     */
    const dataContainer = new google.maps.Data();
    dataContainer.addGeoJson(fc);
    dataContainer.forEach(feature => {
      const polygons = convertFeatureToPolygons(feature);
      if(polygons) {
        for(let polygon of polygons) {
          this.addPolygonToMap(polygon);
        }
      }
    });

    // get bounds
    const bounds = TurfEnvelope(geoJSON);
    this.mapState.boundsSub.next(bounds);
  }

  reset () {
    this.mode = 'draw';
    for (const drawnFeature of this.drawnPolygons) {
      this.removePolygonFromMap(drawnFeature);
    }
    this.drawnPolygons = [];
    this.selectedFeature = null;
    this.rangeMapService.startFeature();
    this.changeDetector.detectChanges();
  }

  getMOLLayers(dataTypes) {

    // if empty, clear datatypes
    if(!dataTypes) {
      this.mapState.overlaySub.next(null);
      return;
    }

    const CARTO_MAP_API = 'https://carto.mol.org/user/mol/api/v1/map/named/detailed-map';

    // show only visible datasets
    const visibleDatasets = [];

    for (const type of dataTypes) {
      if (type.visible) {
        for (const dataset of type.datasets) {
          if (dataset.visible) {
            visibleDatasets.push(dataset.dataset_id);
          }
        }
      }
    }

    const body = {
      datasets: visibleDatasets.join(','),
      scientificname: this.rangeMapService.feature.scientificname
    };

    if(this.requests.getMOLLayers) {
      this.requests.getMOLLayers.unsubscribe();
    }
    this.requests.getMOLLayers = this.http.post(CARTO_MAP_API, body)
      .subscribe(res => {
        const r = res.json();
        // Set the map url
        const tile_url = 'https://carto.mol.org/mol/api/v1/map/' + r['layergroupid'] + '/{z}/{x}/{y}.png';
        const grid_url = 'https://carto.mol.org/mol/api/v1/map/' + r['layergroupid'] + '/0/{z}/{x}/{y}.grid.json';

        this.mapState.overlaySub.next(new OverlayMapTypeService(tile_url, 0.8, grid_url));
      });
  }

  loadMap() {
    super.loadMap();

    this.drawingManager = new google.maps.drawing.DrawingManager({
      drawingMode: null,
      drawingControl: false,
      map: this.map
    });

    google.maps.event.addListener(this.drawingManager, 'polygoncomplete', (polygon) => {
      this.onPolygonComplete(polygon)
    });

    google.maps.event.addListener(this.map, 'click', (e) => {
      this.hideDeleteVertexPopup();
      this.unselectAll();
    });

    this.drawMode();
  }

  onPolygonComplete (polygon) {
    this.addPolygonToMap(polygon);
    // listen for click events on newly created polygon
    google.maps.event.addListener(polygon, 'click', (event) => {
      if (this.mode === 'edit') {
        this.selectFeature(polygon);
      }
    });
    google.maps.event.addListener(polygon, 'rightclick', (event) => {
      if (this.mode === 'edit' && typeof event.vertex !== "undefined") {
        let pixelCoords = this.latLngToPixel(event.latLng);
        this.openDeleteVertexPopup(polygon, event.vertex, pixelCoords);
      }
    });
    this.changeDetector.detectChanges();
  }

  latLngToPixel (latLng) {
    const projection = this.map.getProjection();
    const bounds = this.map.getBounds();
    const topRight = projection.fromLatLngToPoint(bounds.getNorthEast());
    const bottomLeft = projection.fromLatLngToPoint(bounds.getSouthWest());
    const scale = Math.pow(2, this.map.getZoom());
    const worldPoint = projection.fromLatLngToPoint(latLng);
    return [Math.floor((worldPoint.x - bottomLeft.x) * scale), Math.floor((worldPoint.y - topRight.y) * scale)];
  }

  hideDeleteVertexPopup () {
    this.deleteVertexPopup.polygon = null;
    this.deleteVertexPopup.vertex = null;
    if(this.deleteVertexPopup.visible) {
      this.deleteVertexPopup.visible = false;
      this.changeDetector.detectChanges();
    }
  }

  openDeleteVertexPopup (polygon, vertex, pixelCoords) {
    this.deleteVertexPopup.polygon = polygon;
    this.deleteVertexPopup.vertex = vertex;
    const horizontalShift = 10;
    const verticalShift = -18;
    this.deleteVertexPopup.left = pixelCoords[0] + horizontalShift;
    this.deleteVertexPopup.top = pixelCoords[1] + verticalShift;
    this.deleteVertexPopup.visible = true;
    this.changeDetector.detectChanges();
  }

  deleteVertex () {
    this.deleteVertexPopup.polygon.getPath().removeAt(this.deleteVertexPopup.vertex);
    this.hideDeleteVertexPopup();
  }

  addPolygonToMap(polygon) {
    this.drawnPolygons.push(polygon);
    polygon.setMap(this.map);
    this.refreshDataLayerGeoJson();
  }

  removePolygonFromMap(polygon) {
    const indexOfFeature = this.drawnPolygons.indexOf(polygon);
    if (indexOfFeature !== -1) {
      this.drawnPolygons.splice(indexOfFeature, 1);
    }
    polygon.setMap(null);
    this.refreshDataLayerGeoJson();
  }

  drawMode () {
    this.mode = 'draw';
    this.drawingManager.setDrawingMode(google.maps.drawing.OverlayType.POLYGON);
    for (const polygon of this.drawnPolygons) {
      polygon.setEditable(false);
    }
    this.unselectAll();
    this.hideDeleteVertexPopup();
  }

  editMode () {
    this.mode = 'edit';
    this.drawingManager.setDrawingMode(null);
    for (const polygon of this.drawnPolygons) {
      polygon.setEditable(true);
    }
  }

  selectFeature (polygon) {
    if (this.selectedFeature) {
      this.unselectFeature(this.selectedFeature);
    }
    this.selectedFeature = polygon;
    polygon.setOptions({
      ...this.polygonStyles.selected
    });
    this.changeDetector.detectChanges();
  }

  unselectFeature (polygon) {
    polygon.setOptions({
      ...this.polygonStyles.default
    });
    this.selectedFeature = null;
    this.changeDetector.detectChanges();
  }

  unselectAll () {
    for (const polygon of this.drawnPolygons) {
      this.unselectFeature(polygon);
    }
    this.selectedFeature = null;
  }

  deleteFeature () {
    const polygon = this.selectedFeature;
    this.unselectFeature(polygon);
    this.removePolygonFromMap(polygon);
  }

  /**
   * Every time a polygon is created or deleted, re-compute the GeoJSON that will be submitted
   */
  refreshDataLayerGeoJson () {
    const dataLayer = new google.maps.Data();
    for (const polygon of this.drawnPolygons) {
      dataLayer.add(new google.maps.Data.Feature({
        geometry: new google.maps.Data.Polygon([polygon.getPath().getArray()])
      }));
    }
    dataLayer.toGeoJson((geoJson: RangeMapFeature) => {
      if (geoJson.type === "FeatureCollection" && geoJson.features.length > 0) {
        this.rangeMapService.feature.setGeoJson(geoJson);
      } else {
        this.rangeMapService.feature.setGeoJson(null);
      }
      this.changeDetector.detectChanges();
    });
  }

  openFeatureModal() {
    const dialogRef = this.dialog.open(FeatureAttributesDialogComponent, {
      height: 'auto',
      width: 'auto'
    });
  }

}



