import { NativeDateAdapter } from "@angular/material";


export const ISO_DATE_FORMATS = {
  parse: {
    dateInput: 'YYYY-MM-DD',
  },
  display: {
    dateInput: 'YYYY-MM-DD',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'YYYY-MM-DD',
    monthYearA11yLabel: 'MMM YYYY',
  },
};


export function isoFormater(date: Date, displayFormat: Object): string {
  if (displayFormat === 'YYYY-MM-DD') {
    return date.toISOString().slice(0,10);
  }
  return date.toDateString();
}

export class IsoDateAdapter extends NativeDateAdapter {

  format(date: Date, displayFormat: Object): string {
    return isoFormater(date, displayFormat);
  }

}



