/**
 * Translation Service
 *
 * Used to get and set locale, as well as provide translations for dynamic text within Uploader.
 *
 * - On load, sets own locale to either the locale in the cookie or the default locale
 * - Has function `changeLocale()` for changing the locale and reloading the app
 *    - Used by locale switcher in top right corner of page
 * - Has function `getCurrentLocale()` for getting the current locale
 * - Has function `getTranslations()` for getting all translations for a current locale
 * - Has function `getTranslation()` for getting a particular translation, with optional parameters
 *    for injecting dynamic text into the translated message
 */
import { Injectable } from "@angular/core";

import INTERPOLATED_VALUES from "../../locale/interpolated";
import { LOCALE_LIST } from "../../locale/localeList";

interface TranslatedText {
  "en-US": {
    "dataset-list-page:dataset-type-options": {
      localinv: string;
      points: string;
      range: string;
      all: string;
    },
    "files-form:upload-files:error-text": {
      emptyColumnHeaders: string;
      headerHasWhitespace:string;
      yearFormat: string;
      dateOutOfRange: string;
      invalidFloat: string;
      latitudeOutOfRange: string;
      longitudeOutOfRange: string;
    },
    "files-form:match-columns-step:form:matching-errors-list": {
      regionIdMismatch: string;
      regionIdNotFoundShapeFile: string;
      regionIdNotFoundGeometryFile: string;
      pointFileEmptyColumnHeader: string;
      checklistFileError: string;
      shapeFileError: string;
      checklistFileEmptyColumnHeader: string;
      problemUploadingDataset: string;
      missingRequiredTerm: string;
      rowTermMissing: string;
      regionsFileEmptyColumnHeader: string;
      regionIdMissing: string;
      latitudeMissing: string;
      latitudeMissingWithError: string;
      longitudeMissing: string;
      longitudeMissingWithError: string;
      rowTermError: string;
      eventDateError: string;
    },
    "dataset-detail-error": {
      problemUploadingDataset: string;
      cannotDeleteDataset: string;
    },
    'range-map-info-modal:text': {
      datasetFinalized: string;
      noDataReturned: string;
      getUserInfoFailed: string;
      errorCreatingMetadata: string;
      errorSavingMetadata: string;
    }
  }
};

function setCookie(name, value, days?) {
  var expires = "";
  if (days) {
    var date = new Date();
    date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/";
}

function getCookie(name) {
  var nameEQ = name + "=";
  var ca = document.cookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == ' ') c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}

@Injectable()
export class TranslationService {
  messages: any;
  localeId: string;
  defaultLocale = "en-US";

  constructor() {
    let cookieLocale = this.getLocaleIdFromCookie() || this.defaultLocale;
    this.setLocale(cookieLocale);
  }
  setLocale(localeId) {
    if (LOCALE_LIST.indexOf(localeId) > -1) {
      this.localeId = localeId;
      setCookie("lang", localeId);
      this.messages = INTERPOLATED_VALUES[this.localeId];
      if (!this.messages) {
        console.error(
          "Error retrieving interpolated values for language code: " +
            this.localeId +
            ". Make sure that you've added the correct language to interpolated.ts."
        );
      }
    } else {
      console.error("Locale not found " + localeId);
    }
  }
  changeLocale (localeId) {
    this.setLocale(localeId);
    location.reload();
  }
  getLocaleIdFromCookie() {
    return getCookie("lang");
  }
  getCurrentLocale () {
    return this.localeId;
  }
  /**
   * getTranslations
   *
   * Returns all translations from `interpolated.ts` for the current locale
   *
  */
  getTranslations(): TranslatedText {
    return this.messages;
  }
  /**
   * insertValuesIntoText
   *
   * Used by getTranslation to substitute values for in-text variables. See getTranslation
   *
   * @param message - string stored in interpolated.ts
   * @param replaceValues - values to replace in `message`
   */
  insertValuesIntoText(message, replaceValues) {
    if (typeof message === "string" && message.length) {
      const tokensToReplace = Object.keys(replaceValues);
      for (let token of tokensToReplace) {
        const textToInsert = replaceValues[token];
        if (
          typeof textToInsert === "string" ||
          typeof textToInsert === "number"
        ) {
          message = message.replace("{" + token + "}", textToInsert);
        }
      }
      return message;
    }
  }
  /**
   * Usage:
   *
   * this.translationService.getTranslation([
   *  "files-form:match-columns-step:form:matching-errors-list",
   *   "latitudeMissing"
   * ], {
   *   row: "a value to insert for {row}"
   * });
   *
   * @param location string[] - an array of keys that correspond to keys in the interpolation
   *  object (src/app/interpolated.ts)
   * @param replaceValues {} - some translations have pieces of text that need to be dynamically
   *  changed (like a row number, e.g. "{row}"). This object's keys describe the text to be replaced
   *  and what the text will be replaced with. For example:
   *
   * Translation text:
   *
   * "{row} : latitude is out of range = {latitude}"
   *
   * replaceValues:
   *
   * {
   *    row: "row 1",
   *    latitude: "49.85683"
   * }
   *
   * So "{row}" in the translation will be replaced with "row 1" and "{latitude}" in the text will
   * be replaced with "49.85683".
   *
   */
  getTranslation(location: string[], replaceValues?): string {
    let message = this.messages;
    if (this.messages) {
      for (let key of location) {
        if (message[key]) {
          message = message[key];
        }
      }
      if (typeof message === "string" && message.length) {
        if (replaceValues) {
          message = this.insertValuesIntoText(message, replaceValues);
        }
        return message;
      }
    }
    console.error("Translation not found: ", location);
    return "Translation not found";
  }
}



