import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { DatasetOverview } from '../../services/range-map.service';

@Component({
  selector: 'mol-display-dataset-metadata',
  templateUrl: './display-dataset-metadata.component.html',
  styleUrls: ['./display-dataset-metadata.component.css']
})
export class DisplayDatasetMetadataComponent implements OnInit {

  @Input() overview: DatasetOverview;
  @Input() dataset_id: any;
  mappedMetadata: any;
  fromDate = '';
  toDate = '';
  taxa = '';
  creator = '';

  constructor(private router: Router) { }

  ngOnInit() {
    if(this.overview) {
      this.mappedMetadata = this.mapMetadata(this.overview);
    }
    if(this.overview.group_list) {
      this.taxa = this.overview.group_list.map(g => g.taxa).join(', ');
    }
    if(this.overview.records_list) {
     this.overview.records_list.forEach(r => {
       if (!r.displaydate) { return; }
       if ( r.displaydate > this.toDate) {
         this.toDate = r.displaydate;
       }
       if (!this.fromDate || r.displaydate < this.fromDate) {
         this.fromDate = r.displaydate;
       }
     });
   }
  }

  mapMetadata (overview: DatasetOverview) {
    // grab the metadata from the `overview` and make some adjustments to DOI fields
    return overview.metadata.map((value) => {
      return {
        ...value,
        children: value.children.map((v) => {
          if(v.id === 'doi_mol') {
            // assumes that the link is in the format
            // http://something.something/[0 - n subdirectories]/DOI
            const doiLink = v.value;
            const lastSlash = doiLink.lastIndexOf("/");
            const doi = doiLink.substr(lastSlash + 1);
            return {
              ...v,
              doi
            }
          }
          return v;
        })
      }
    });
  }

  editMetadata () {
    this.router.navigate(['range-map/metadata', this.dataset_id]);
  }

}



