import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { Taxon } from '../../services/taxa.service';

import { RangeMapService } from '../../services/range-map.service';

// import { ShapeFileReaderComponent } from '../../components/file-reader/shape-file-reader/shape-file-reader.component';
import { RangeMapInfoModalComponent } from '../../components/range-map-info-modal/range-map-info-modal.component';

@Component({
  selector: 'mol-range-map-landing-form',
  templateUrl: './range-map-landing-form.component.html',
  styleUrls: ['./range-map-landing-form.component.scss']
})
export class RangeMapLandingFormComponent implements OnInit {

  form: FormGroup;

  taxonomyList: Taxon[] = [];
  taxon: Taxon = null;

  private errorSubscription = null;
  private currentDialogRef = null;

  constructor(
    private rangeMapService: RangeMapService,
    private router: Router,
    public dialog: MatDialog
  ) {
    this.rangeMapService.reset();
    this.form = new FormGroup({
      taxon: new FormControl(null, Validators.required),
      dataset_title: new FormControl(null, [
        Validators.required,
        Validators.minLength(3)
      ])
    });

    this.errorSubscription = this.rangeMapService.errorSubj.subscribe(error => {
      this.openErrorModal(error)
    });
  }

  openErrorModal (error) {
    const createError = () => {
       if (error.type === 'newMetadata') {
         this.dialog.open(RangeMapInfoModalComponent, {
           width: '300px',
           height: '300px',
           disableClose: true,
           data: {
             title: 'Cannot create metadata',
             type: 'error',
             text: error.error
           }
         });
       }
    }

    /**
     * If a dialog already exists, then the closing of the previous dialog is asynchronous
     * because of the closing animation. Therefore, we have to wait for it to finish closing
     * before we can show the next modal.
     */
    this.dialog.closeAll();
    if(this.currentDialogRef) {
      this.currentDialogRef.afterClosed().subscribe(value => {
        createError();
      });
    } else {
      createError();
    }
  }

  ngOnInit() {
    this.rangeMapService.taxaService.fetchTaxa()
      .subscribe(taxa => this.taxonomyList = taxa);
  }

  onTaxonomyChange() {
    this.rangeMapService.selectTaxon(this.form.controls.taxon.value);
  }

  createEmptyDataset() {
    this.rangeMapService.selectTaxon(this.form.controls.taxon.value);
    this.rangeMapService.createNewDataset(this.form.controls.dataset_title.value);
    this.router.navigate(['range-map/draw/' + this.rangeMapService.dataset_id]);
  }

}



