import { Component, OnInit, Inject, ViewEncapsulation } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { TermsService } from '../../services/terms.service';



@Component({
  selector: 'mol-match-columns-dialog',
  templateUrl: './match-columns-dialog.component.html',
  styleUrls: ['./match-columns-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class MatchColumnsDialogComponent implements OnInit {

  uploadService;
  terms: TermsService;
  hasRecordedBy = false;
  recordedByValue = "";

  constructor(
    public dialogRef: MatDialogRef<MatchColumnsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
  }

  getTerms() {
    const visibleTerms = this.terms.visibleTerms;
    const groups = {
      required: [],
      optional: []
    };
    for(let term of visibleTerms) {
      if(term.required) {
        groups.required.push(term);
      } else if(term.optional) {
        groups.optional.push(term);
      }
    }
    return groups;
  }

  alreadySelected(term): boolean {
    return !!this.terms.verbatims.find(v => v.mapsTo == term.name);
  }

  deselectInOthers(verbatim) {
    this.terms.verbatims
      .filter(v => v != verbatim && v.mapsTo && v.mapsTo == verbatim.mapsTo)
      .forEach(v => { v.mapsTo = ''; });
  }

  onClick(value): void {
    this.dialogRef.close(value);
  }

  ngOnInit() {
    this.uploadService = this.data.uploadService;
    this.terms = this.uploadService.terms;
    // when the popup opens, check to see if there's a mtached recorded_by field
    this.termChange();
    this.recordedByValue = this.uploadService.metadata.userInfo.fullname;
  }

  termChange() {
    this.uploadService.terms.remapVerbatimsToTerms();
    this.hasRecordedBy = this.uploadService.terms.checkForRecordedBy();
  }

}



