import {
  Component,
  AfterViewInit,
  AfterViewChecked,
  ElementRef,
  ViewChild,
  Input,
  Output,
  ViewContainerRef,
  ComponentFactoryResolver,
  ComponentFactory,
  ComponentRef,
  EventEmitter
} from '@angular/core';

import TurfBbox from '@turf/bbox';
import BboxPoly from '@turf/bbox-polygon';
import Area from '@turf/area';
import Buffer from '@turf/buffer';

import { GoogleMapsApiService } from '../../services/google-maps-api.service';
import { OverlayMapTypeService } from '../../services/overlay-map-type.service';
import { MapService } from '../../services/map.service';
import { MatSpinner } from '@angular/material';
import { MapStyles } from './map-styles';

//import 'leaflet';
//import 'leaflet-canvaslayer-field';

@Component({
  selector: 'mol-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css'],
  providers: [MapStyles],
  entryComponents: [MatSpinner]
})
export class MapComponent implements AfterViewInit, AfterViewChecked {

  private apiLoaded: boolean;
  map: google.maps.Map;
  tilesLoading = false;
  overlayMapType: OverlayMapTypeService;
  loadingIndicator: ComponentRef<MatSpinner>;
  private ready = false;
  infoWindow = null;

  @Input() set bounds(bounds: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon>) {
    if (bounds && this.map && google) { this.fitBounds(bounds) }
  }

  @Input()
  set overlay(overlay: string) {
    if (this.map && google) {
      this.overlayMapType = new OverlayMapTypeService(overlay, this.opacity);

      this.overlayMapType.onTilesAdded = this.showLoader.bind(this);
      this.overlayMapType.onTilesLoaded = this.hideLoader.bind(this);

      this.map.overlayMapTypes.setAt(
        0, this.overlayMapType
      );
    }
  }

  @Input()
  set opacity(opacity: number) {
    if (this.map && google) {
      this.overlayMapType.opacity = opacity;
      this.map.overlayMapTypes.setAt(0, this.overlayMapType);
    }
  }

  @Output() zoomChanged: EventEmitter<number> = new EventEmitter();
  @Output() tilesLoaded: EventEmitter<boolean> = new EventEmitter();

  @ViewChild('node') node: ElementRef;

  constructor(
    public componentFactoryResolver: ComponentFactoryResolver,
    public viewContainerRef: ViewContainerRef,
    public mapStyles: MapStyles,
    public mapState: MapService
  ) {}

  fitBounds(bounds: GeoJSON.Feature<GeoJSON.MultiPolygon | GeoJSON.Polygon>) {
    let bbox = TurfBbox(bounds as any);
    const bboxPoly = BboxPoly(bbox as any);
    const area = Area(bboxPoly) / 1000000;
    if(area < 100) {
      let buffer = Buffer(bboxPoly as any, 10, {units: 'kilometers'});
      bbox = TurfBbox(buffer);
    }
    const latLngBounds = {
      west: bbox[0],
      south: bbox[1],
      east: bbox[2],
      north: bbox[3]
    };
    this.map.fitBounds(latLngBounds);
  }

  private hideLoader() {
    this.tilesLoaded.emit(true);
  }

  private showLoader() {
    if (!this.tilesLoading) {
      this.tilesLoaded.emit(false);
    }
  }

  private showMousePointer(overFeature: boolean) {
    if (this.map && google) {
      if (overFeature) {
        this.map.setOptions({ draggableCursor: 'pointer' });
      } else {
        this.map.setOptions({ draggableCursor: 'default' });
      }
    }
  }

  public showPopup(content, position) {
    if(this.infoWindow) {
      this.infoWindow.close();
    }
    if(content && position) {
      const anchor = new google.maps.MVCObject();
      anchor.set('position', position);

      this.infoWindow = new google.maps.InfoWindow({
        content: content
      });
      this.infoWindow.open(this.map, anchor);

      google.maps.event.addDomListenerOnce(this.infoWindow, 'closeclick', () => {
        this.infoWindow = null;
      });
    }
  }

  loadMap() {
    let self = this;

    this.map = new google.maps.Map(
      this.node.nativeElement,
      {
        center: new google.maps.LatLng(0, 0),
        zoom: 2,
        streetViewControl: false,
        styles: this.mapStyles.GREY_BASEMAP
      }
    );

    google.maps.event.addDomListener(this.map, 'zoom_changed',
      function(event) {
        self.zoomChanged.emit(self.map.getZoom());
      }
    );

    google.maps.event.addDomListener(this.map, 'click', function (event) {
      if(typeof self.overlayMapType !== 'undefined') {
        self.overlayMapType.onMapClickEvent({
          latLng: event.latLng,
          zoom: self.map.getZoom()
        });
      }
    });

    google.maps.event.addDomListener(this.map, 'mousemove', function (event) {
      if(typeof self.overlayMapType !== 'undefined') {
        let data = self.overlayMapType.onMapMouseoverEvent({
          latLng: event.latLng,
          zoom: self.map.getZoom()
        });
        self.showMousePointer((data !== undefined))
      }
    });

    this.mapState.overlayObs.subscribe(o => {
      self.addMOLOverlay.bind(self)(o);
    });
    this.mapState.zoomObs.subscribe(z => this.map.setZoom(z));
    this.mapState.boundsObs.subscribe(b => this.fitBounds(b));
    this.mapState.basemapObs.subscribe(b => this.map.setMapTypeId(b));
    this.mapState.resizeObs.subscribe(r => google.maps.event.trigger(this.map, 'resize'));
    this.mapState.popupObs.subscribe(d => {
      if(d && d.content && d.position) {
        self.showPopup(d.content, d.position);
      } else if (self.infoWindow) {
        self.infoWindow.close();
      }
    });
    this.ready = true;

    this.mapState.mapLoadSub.next(true);
  }

  addMOLOverlay (o) {
    this.overlayMapType = o;
    this.map.overlayMapTypes.setAt(0, this.overlayMapType);
  }

  ngAfterViewInit() {
    if (!this.ready) { this.loadMap(); }
  }
  ngAfterViewChecked() {
    // google.maps.event.trigger(this.map, 'resize');
  }

}



