import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';

export interface FileReaderResults {
  controlName: string;
  isValid: boolean;
  fileName: string;
  fileReaderOutput: any;
  errors: string[];
}

@Component({
  selector: 'mol-file-reader',
  templateUrl: './file-reader.component.html',
  styleUrls: ['./file-reader.component.css']
})
export class FileReaderComponent implements OnInit {

  @ViewChild('fileInput')
  fileInput;

  @Input() label: string;
  @Input() name: string;
  @Input() hint: string;
  @Output() results: EventEmitter<FileReaderResults> = new EventEmitter();

  fileName = '';

  ngOnInit() { }
  parseFile(file) { }

  reset() {
    this.fileName = '';
    this.fileInput.nativeElement.value = null;
  }

  onChangeListener($event): void {
    const files = $event.target.files;
    if (!files.length) { return; }
    this.fileName = files[0].name;
    this.parseFile(files[0]);
  }

}



