import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable, Subject } from 'rxjs';

export interface UserInfo {
  email: string;
  firstname: string;
  fullname: string;
  id: number;
  lastname: string;
  username: string;
}

@Injectable()
export class AuthenticationService {

  private userInfoSubj: Subject<UserInfo>  = new Subject();
  private userInfoErrorSubj: Subject<UserInfo> = new Subject();
  userInfoObs: Observable<UserInfo> = this.userInfoSubj.asObservable();
  userInfoErrorObs: Observable<UserInfo> = this.userInfoErrorSubj.asObservable();

  constructor(
    private http: Http
  ) { }

  public loggedIn() {
    return document.cookie && document.cookie.match(/muprsns=([^;]+)/);
  }

  public isLoggedIn() {
    if (!this.loggedIn()) {
      window.location.href = `https://auth.mol.org/login?next=${window.location.href}`;
    }
  }

  public getUserInfo () {
    const cookie = document.cookie.match(/muprsns=([^;]+)/);
    const cookieToken = (cookie) ? cookie[0] : false;
    const authToken = (cookieToken) ? cookieToken.split("=")[1] : '';
    this.http.get(`https://auth.mol.org/api/me?auth_token=${authToken}`).map(
        response => response.json()
      ).subscribe(response => {
        this.userInfoSubj.next(response);
      }, error => {
        this.userInfoErrorSubj.next(error);
      });
  }

}



