import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


export interface CoordinateTypeSelection {
    areaShape: 'circle' | 'square';
    referenceCoordinate: 'sw'| 'nw' | 'ne' | 'se' | 'center';
    radius: number;
    edgeLength: number;
    area: number;
}

@Component({
  selector: 'mol-coordinate-type-modal',
  templateUrl: './coordinate-type-modal.component.html',
  styleUrls: ['./coordinate-type-modal.component.scss']
})
export class CoordinateTypeModalComponent implements OnInit {

  selection: CoordinateTypeSelection = {
    areaShape: null,
    referenceCoordinate: 'center',
    radius: null,
    edgeLength: null,
    area: null
  };

  sizeUnit: 'edgeLength' | 'radius' | 'area' = 'area';

  constructor(
      public dialogRef: MatDialogRef<CoordinateTypeModalComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  save(): void {
    this.dialogRef.close(this.selection);
  }

  shapeChange () {
    // reset everything
    this.selection.referenceCoordinate = 'center';
    this.selection.radius = null;
    this.selection.edgeLength = null;
    this.selection.area = null;
    this.sizeUnit = 'area';
  }

  sizeUnitChange() {
    this.selection.radius = null;
    this.selection.edgeLength = null;
    this.selection.area = null;
  }

  squareReferenceClick (referenceCoordinate) {
    this.selection.referenceCoordinate = referenceCoordinate;
  }

  cancel () {
    this.dialogRef.close(false);
  }

  isValid () {
    let validCircle = (this.selection.areaShape === 'circle' && (this.selection.radius | this.selection.area));
    let validSquare = (this.selection.areaShape === 'square' && (this.selection.edgeLength | this.selection.area));
    return (validCircle || validSquare);
  }

  ngOnInit() {

  }

}



