import { Injectable } from '@angular/core';
import { MetadataService } from './metadata.service';

@Injectable()
export class PointsMetadataService extends MetadataService {

  dataset_type = 'occurrence';

  protected specificJsonMap = {
    author_display: { value: () => this.author_display, property: 'author_display' },
    citation_formatted: { value: () => this.citation_formatted, property: 'citation_formatted' },
    countries: { value: () => this.countries, property: 'countries' },
    description: { value: () => this.abstract, property: 'abstract' },
    doi: { value: () => this.doi, property: 'doi' },
    geographic_scope: { value: () => this.geographic_scope, property: 'geographic_scope' },
    isbn: { value: () => this.isbn, property: 'isbn' },
    keywords: { value: () => this.keywords, property: 'keywords' },
    notes: { value: () => this.notes, property: 'notes' },
    product_type: { value: () => this.product_type, property: 'product_type' },
    spatial_unit: { value: () => this.geodetic_datum, property: 'geodetic_datum' },
    specific_taxon_groups: { value: () => this.specific_taxon_groups, property: 'specific_taxon_groups' },
    taxonomic_class: { value: () => this.class, property: 'class' },
    uncertainty: { value: () => this.uncertainty, property: 'uncertainty' },
    quality_control: { value: () => this.quality_control, property: 'quality_control' },
    sampling_description: { value: () => this.sampling_description, property: 'sampling_description' }
  }

  /**
   * Default values
   * - if these are changed, modify reset() to match
   */
  countries = '';
  geodetic_datum = '';
  product_type = 'points';
  uncertainty = '';
  quality_control = '';
  sampling_description = '';

  // Point datasets will be set to public by default
  allow_download = true;
  mol_modeling = true;
  allow_visualization = true;

  /**
   * End Default Values
   */

  public fromOverview(dataset_id, overview: any) {
    super.fromOverview(dataset_id, overview, this.specificJsonMap);
  }

  public reset () {
    super.reset();

    // these should match the default values of the properties above
    this.countries = '';
    this.geodetic_datum = '';

    this.product_type = 'points';
    this.uncertainty = '';
    this.quality_control = '';
    this.sampling_description = '';

    this.allow_download = true;
    this.mol_modeling = true;
    this.allow_visualization = true;
  }

  requiredMetadataIsReady () : boolean {
    let requiredFields = [
      this.dataset_title,
      this.license,
      this.geographic_scope,
      this.abstract
    ];
    // if any of the fields are false, this will be true
    let fieldsMissing = requiredFields.some(element => !element);
    // return true if no fields are missing, false if fields are missing
    return !fieldsMissing;
  }

  isReady() {
    return this.requiredMetadataIsReady() && this.releasePolicyReady();
  }

}



