import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { OverlayMapTypeService } from './overlay-map-type.service'

export interface MapLayer {
    visible: boolean,
    name: string,
    overlay: OverlayMapTypeService
}

@Injectable()
export class MapService {

  basemapSub = new Subject<google.maps.MapTypeId>();
  basemapObs = this.basemapSub.asObservable();

  overlaySub = new Subject<OverlayMapTypeService>();
  overlayObs = this.overlaySub.asObservable();

  utfGridSub = new Subject<any>();
  utfgridObs = this.utfGridSub.asObservable()

  clickSub = new Subject<Function>();
  clickObs = this.clickSub.asObservable();

  mousemoveSub = new Subject<Function>();
  mousemoveObs = this.mousemoveSub.asObservable();

  zoomSub = new Subject<number>();
  zoomObs = this.zoomSub.asObservable();

  boundsSub = new Subject<GeoJSON.Feature<GeoJSON.Polygon>>();
  boundsObs = this.boundsSub.asObservable();

  resizeSub = new Subject<boolean>();
  resizeObs = this.resizeSub.asObservable();

  popupSub = new Subject<any>();
  popupObs = this.popupSub.asObservable();

  // separate overlays / layers subject pair for range map supplemental layers
  layersSource = new Subject<MapLayer[]>();
  layersObs = this.layersSource.asObservable();

  mapLoadSub = new Subject<boolean>();
  mapLoadObs = this.mapLoadSub.asObservable();

  constructor() {}
}



