import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import { ApiService } from "../api.service";
import { UploadService } from "./upload.service";
import { TaxaService } from "../taxa.service";
import { InventoryMetadataService } from "../metadata/inventory-metadata.service";
import { PointsMetadataService } from "../metadata/points-metadata.service";
import { PointFeatureService } from "../feature/point-feature.service";
import { ShapeFeatureService } from "../feature/shape-feature.service";
import { FileReaderResults } from "../../components/file-reader//file-reader.component";
import { ValidationService } from "../validation.service";
import { TermsService } from "../terms.service";
import { TranslationService } from "../translation.service";

import { Subject } from "rxjs";

@Injectable()
export class PointUploadService extends UploadService {
  readonly defaultUncertainty = "25000";
  readonly errorMax = 10;

  recordedByValue = "";

  pointFile: FileReaderResults;

  readonly requiredTerms = [
    "scientificname",
    "date",
    "decimal_latitude",
    "decimal_longitude",
  ];

  readonly optionalTerms = [
    "abundance",
    "catalog_number",
    "class",
    "collection_code",
    "coordinate_precision",
    "country_code",
    "day",
    "depth",
    "depth_accuracy",
    "elevation",
    "elevation_accuracy",
    "establishment_means",
    "family",
    "genus",
    "geodetic_datum",
    "global_unique_identifier",
    "identified_by",
    "infraspecific_epithet",
    "institution_code",
    "kingdom",
    "license",
    "locality",
    "month",
    "order",
    "photographer",
    "phylum",
    "publishing_org_key",
    "record_number",
    "record_source",
    "recorded_by",
    "species",
    "taxon_rank",
    "type_status",
    "uncertainty_m",
    "year",
  ];

  geomFeaturesCreated = new Subject();

  constructor(
    protected api: ApiService,
    protected http: Http,
    public taxaService: TaxaService,
    public valid: ValidationService,
    public terms: TermsService,
    public pointFeatureService: PointFeatureService,
    public shapeFeatureService: ShapeFeatureService,
    public pointsMetadataService: PointsMetadataService,
    public inventoryMetadataService: InventoryMetadataService,
    public translationService: TranslationService
  ) {
    super(
      api,
      http,
      taxaService,
      valid,
      terms,
      pointFeatureService,
      shapeFeatureService,
      pointsMetadataService,
      inventoryMetadataService,
      translationService
    );
    this.metadata = pointsMetadataService;
    this.features = this.pointFeatureService;
  }

  reset() {
    this.terms.setTermLevels(this.requiredTerms, this.optionalTerms);
    this.pointFile = undefined;
    super.reset();
  }

  filesReady(): boolean {
    return !!this.pointFile;
  }

  isValid(): boolean {
    return (
      this.filesReady() &&
      !this.termsErrorsList.length &&
      !this.checklistContentErrorsList.length
    );
  }

  initVerbatims() {
    this.terms.initVerbatims(this.pointFile.fileReaderOutput.meta.fields);
  }

  getPointResults(results: FileReaderResults): void {
    this.pointFile = results;
  }

  // add uncertainty to our geom features just for preview purposes in the "Review Geometry" step
  getGeomFeaturesWithUncertainty(geomFeatures, dataFeatures) {
    let dataFeaturesByGeomId = {};
    let geomFeaturesWithUncertainty = [];

    for (let dataFeature of dataFeatures) {
      let geom_id = dataFeature.properties.geom_id;
      if (!dataFeaturesByGeomId[geom_id]) {
        dataFeaturesByGeomId[geom_id] = dataFeature;
      }
    }

    for (let geomFeature of geomFeatures) {
      let geom_id = geomFeature.properties.geom_id;
      let uncertainty = dataFeaturesByGeomId[geom_id]
        ? dataFeaturesByGeomId[geom_id].properties.uncertainty
        : 0;

      geomFeaturesWithUncertainty.push({
        ...geomFeature,
        geometry: {
          ...geomFeature.geometry
        },
        properties: {
          ...geomFeature.properties,
          uncertainty
        }
      });
    }

    return geomFeaturesWithUncertainty;
  }

  createFeatures() {
    const self = this;
    self.pointFile.fileReaderOutput.data.forEach((record, r) => {
      (<PointFeatureService>self.features).addRecord(
        record,
        self.metadata.dataset_id
      );
    });
    let features = this.getGeomFeaturesWithUncertainty(
      this.pointFeatureService.geomFeatures,
      this.pointFeatureService.dataFeatures
    );
    this.geomFeaturesCreated.next(features);
    this.checkFeatureSize();
  }

  checklistContentErrors(): string[] {
    const self = this;
    const errors: string[] = [];
    const expectedLength = this.pointFile.fileReaderOutput.meta.fields.length;
    self.pointFile.fileReaderOutput.data.forEach((record, r) => {
      const row = r + 2;
      if (self.valid.completeRecord(record, expectedLength)) {
        const error = self.validRecord(record, row);
        if (error) {
          errors.push(error);
        }
      }
    });

    let noBlankHeaders = this.valid.noBlankHeaders(this.pointFile);

    if (!noBlankHeaders) {
      // i18n Translation: "Point file has empty column header."
      let err = this.translationService.getTranslation([
        "files-form:match-columns-step:form:matching-errors-list",
        "pointFileEmptyColumnHeader"
      ]);
      errors.push(err);
    }

    this.checklistContentErrorsList = errors;
    return errors;
  }

  validRecord(record, row) {
    let term = this.terms.find("scientificname");
    if (!record[term.mapsTo]) {
      // i18n Translation: `Row ${row} ${term.name}: is missing`
      return this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "rowTermMissing"], {
        row,
        term: term.name
      });
    }

    term = this.terms.find("recorded_by");
    // if there's no exact "recorded_by" field or matched recorded_by field,
    // set the value for recorded_by to the value specified by the user
    if (!term.mapsTo) {
      record["recorded_by"] = this.recordedByValue;
    }

    term = this.terms.find("decimal_latitude");
    let cleanNum = this.valid.cleanLatitude(record[term.mapsTo]);
    if (cleanNum.error) {
      // i18n Translation: "Row ${row} ${term.name}: ${cleanNum.error}"
      return this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "rowTermError"],
        {
          row,
          term: term.name,
          error: cleanNum.error
        }
      );
    }
    record[term.mapsTo] = cleanNum.value;

    term = this.terms.find("decimal_longitude");
    cleanNum = this.valid.cleanLongitude(record[term.mapsTo]);
    if (cleanNum.error) {
      // i18n Translation: "Row ${row} ${term.name}: ${cleanNum.error}"
      return this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "rowTermError"],
        {
          row,
          term: term.name,
          error: cleanNum.error
        }
      );
    }
    record[term.mapsTo] = cleanNum.value;

    term = this.terms.find("date");
    let cleanDate = this.valid.cleanIsoDate(record[term.mapsTo]);
    if (cleanDate.error) {
      // i18n Translation: "Row ${row} ${term.name}: Event Date ${cleanDate.error}"
      return this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "eventDateError"],
        {
          row,
          term: term.name,
          error: cleanDate.error
        }
      );
    }
    record[term.mapsTo] = cleanDate.value;

    term = this.terms.find("uncertainty_m");
    cleanNum = this.valid.cleanFloat(
      record[term.mapsTo],
      this.defaultUncertainty
    );
    if (cleanNum.error) {
      // i18n Translation: "Row ${row} ${term.name}: ${cleanNum.error}"
      return this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "rowTermError"],
        {
          row,
          term: term.name,
          error: cleanNum.error
        }
      );
    }
    record[term.mapsTo] = cleanNum.value;

    term = this.terms.find("record_source");
    // if there's no exact "record_source" field or matched record_source field,
    // set the value for record_source to 'HumanObservation' by default
    if (!term.mapsTo) {
      record["record_source"] = "HumanObservation";
    } else {
      record[term.mapsTo] = record[term.mapsTo] || 'N/A';
    }

    return "";
  }
}



