import { Component, OnInit, NgZone } from "@angular/core";
import { Router } from '@angular/router';
import { DatasetListItem, DatasetService } from '../../services/dataset.service';
import { AuthenticationService } from '../../services/authentication.service';
import { TranslationService } from '../../services/translation.service';
import { MatDialog } from '@angular/material';
import { DeleteDatasetModalComponent } from '../../components/delete-dataset-modal/delete-dataset-modal.component';

@Component({
  selector: "mol-dataset-list-page",
  templateUrl: "./dataset-list-page.component.html",
  styleUrls: ["./dataset-list-page.component.scss"]
})
export class DatasetListPageComponent implements OnInit {
  datasetListSubscriber = null;
  datasetListErrorCode: number = 0;
  datasetListError: boolean = false;
  datasetList: DatasetListItem[];
  selectedDataset: "all" | "localinv" | "points" | "range" = "all";
  datasetTypeDisplay = {
    localinv: "Local Inventory",
    points: "Occurrence",
    range: "Range Map",
    all: "All"
  };
  userDatasetTypes: { type: string; display: string }[];
  translations = {};

  constructor(
    private router: Router,
    private authentication: AuthenticationService,
    private datasetService: DatasetService,
    public dialog: MatDialog,
    private zone: NgZone,
    public translationService: TranslationService
  ) {
    this.translations = translationService.getTranslations();
  }

  ngOnInit() {
    const self = this;
    if (!this.authentication.loggedIn()) {
      this.router.navigate(["landing"]);
    }

    this.datasetTypeDisplay = this.translations["dataset-list-page:dataset-type-options"];

    const getDatasetList = () => {
      if (!window.location.href.endsWith("/datasets")) {
        return;
      }
      if (this.datasetListSubscriber) {
        this.datasetListSubscriber.unsubscribe();
      }
      this.datasetListSubscriber = this.datasetService
        .fetchList()
        .subscribe(list => {
          this.mapDatasetList(list);
        });
    };
    getDatasetList();
    setInterval(getDatasetList, 30000);
  }

  mapDatasetList(list) {
    const self = this;
    if (list.length && list[0] && !list[0].error) {
      this.datasetList = list.map(this.mapStatus);
      this.userDatasetTypes = this.datasetList
        .map((currentValue, index, array) => {
          return currentValue.product_type;
        })
        .filter((value, index, self) => {
          return self.indexOf(value) === index;
        })
        .map((currentValue, index, array) => {
          return {
            type: currentValue,
            display: self.datasetTypeDisplay[currentValue]
          };
        });
      this.userDatasetTypes.unshift({
        type: "all",
        display: "All"
      });
    } else if (list.length && list[0] && list[0].error) {
      this.datasetListError = list[0].error;
      this.datasetListErrorCode = list[0].code;
    } else {
      this.datasetList = [];
      this.datasetListError = false;
    }
  }

  mapStatus(dataset, index, array) {
    let newDataset = {
      ...dataset,
      view_status: "ready",
      viewable: "yes",
      finalized: false,
      public_private_display: dataset.public ? "public" : "private"
    };

    let lc_status = dataset.status.toLowerCase();
    switch (dataset.product_type) {
      case "localinv":
      case "points":
        newDataset.view_status = lc_status;
        if (lc_status === "queued") {
          newDataset.viewable = "no";
        } else if (lc_status === "loaded") {
          newDataset.view_status = "harmonize";
          newDataset.viewable = "harmonize";
        } else if (lc_status === "completed") {
          newDataset.viewable = "yes";
        } else {
          newDataset.viewable = "error";
        }
        break;
      case "range":
        if (lc_status === "completed") {
          newDataset.finalized = true;
        }
        break;
    }

    return newDataset;
  }

  toDetails(dataset_id) {
    const dataset = this.datasetList.find(d => d.dataset_id == dataset_id);
    if (
      ["completed", "loaded", "error"].indexOf(dataset.status.toLowerCase()) >
      -1
    ) {
      this.router.navigate(["dataset", dataset_id]);
    } else if (dataset.product_type === "range") {
      this.router.navigate(["dataset", dataset_id]);
    }
  }

  deleteDataset($event, dataset_id) {
    const self = this;
    $event.stopPropagation();
    const dataset = this.datasetList.find(d => d.dataset_id == dataset_id);
    const deleteDialog = this.dialog.open(DeleteDatasetModalComponent, {
      height: "400px",
      width: "400px",
      data: {
        dataset_id: dataset_id,
        dataset_title: dataset.dataset_title
      }
    });

    deleteDialog.afterClosed().subscribe(reply => {
      if (reply) {
        this.datasetService.delete(dataset_id).subscribe(
          success => {
            this.reloadPage();
          },
          error => {
            const reply = error.json();
            // i18n Translation: "Could not delete dataset."
            let msg = this.translationService.getTranslation(["dataset-detail-error","cannotDeleteDataset"]);
            if (reply.length && reply[0].error) {
              msg = msg + error.json()[0].error;
            }
          }
        );
      }
    });
  }

  reloadPage() {
    this.zone.runOutsideAngular(() => {
      location.reload();
    });
  }

  goToUpload() {
    this.router.navigate(["files"]);
  }
}



