import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import {HttpParams} from "@angular/common/http";
import { ApiService } from './api.service';
import 'rxjs/add/operator/map';

export interface SpeciesName {
  taxa: string;
  scientificname: string;
  vernacular: string;
  tc_id: string;
}

@Injectable()
export class SpeciesService {

  private species: SpeciesName[] = [];
  public selected: SpeciesName;
  public query: string;

  constructor(
    protected api: ApiService,
    protected http: Http) {
  }

  public fetchSpecies(taxa: string, speciesName: string) {
    const self = this;
    if(taxa) {
      return self.http.get(`${this.api.root}/species/groupsearch?query=${speciesName}&group=${taxa}`)
        .map(response => {
            self.species = response.json();
            return self.species;
        });
    }
    return self.http.get(`${this.api.root}/species/groupsearch?query=${speciesName}`)
      .map(response => {
          self.species = response.json();
          return self.species;
      });
  }
}



