import { Injectable } from '@angular/core';
import { MetadataService } from './metadata.service';

@Injectable()
export class InventoryMetadataService extends MetadataService {

  geometryType: 'shapefile' | 'coordinates';

  dataset_type = 'inventory';

  specificJsonMap = {
    hc_author_display: { value: () => this.author_display, property: 'author_display' },
    hc_citation_formatted: { value: () => this.citation_formatted, property: 'citation_formatted' },
    hc_compilation_type: { value: () => this.inv_compilation_type, property: 'inv_compilation_type' },
    hc_completeness_lower: { value: () => this.inv_completeness_lower, property: 'inv_completeness_lower' },
    hc_completeness_rationale: { value: () => this.inv_completeness_rationale, property: 'inv_completeness_rationale' },
    hc_completeness_upper: { value: () => this.inv_completeness_upper, property: 'inv_completeness_upper' },
    hc_dataset_title: { value: () => this.dataset_title, property: 'dataset_title' },
    hc_doi: { value: () => this.doi, property: 'doi' },
    hc_end_day: { value: () => `00${(new Date(this.inv_date_end)).getDay()}`.substr(-2), property: 'inv_date_end' },
    hc_end_month: { value: () => `00${(new Date(this.inv_date_end)).getMonth() + 1}`.substr(-2), property: 'inv_date_end' },
    hc_end_year: { value: () => `${(new Date(this.inv_date_end)).getFullYear()}`, property: 'inv_date_end' },
    hc_geospatial_scope: { value: () => this.inv_geospatial_scope, property: 'inv_geospatial_scope' },
    hc_inventory_compilation_both: { value: () => this.inv_inventory_compilation_both, property: 'inv_inventory_compilation_both' },
    hc_inventory_type: { value: () => this.inv_inventory_type, property: 'inv_inventory_type' },
    hc_isbn: { value: () => this.isbn, property: 'isbn' },
    hc_keywords: { value: () => this.keywords, property: 'keywords' },
    hc_number_unit: { value: () => this.inv_survey_days_total, property: 'inv_survey_days_total' },
    hc_site_details: { value: () => this.inv_geospatial_site_details, property: 'inv_geospatial_site_details' },
    hc_start_day: { value: () => `00${(new Date(this.inv_date_start)).getDay()}`.substr(-2), property: 'inv_date_start' },
    hc_start_month: { value: () => `00${(new Date(this.inv_date_start)).getMonth() + 1}`.substr(-2), property: 'inv_date_start' },
    hc_start_year: { value: () => `${(new Date(this.inv_date_start)).getFullYear()}`, property: 'inv_date_start' },
    hc_survey_night_day: { value: () => this.inv_survey_night_day, property: 'inv_survey_night_day' },
    hc_survey_protocol_details1: { value: () => this.inv_survey_protocol1, property: 'inv_survey_protocol1' },
    hc_survey_protocol_details2: { value: () => this.inv_survey_protocol_detail1, property: 'inv_survey_protocol_detail1' },
    hc_survey_season: { value: () => this.season, property: 'season' },
    hc_temporal_unit: { value: () => 'days', property: 'hc_temporal_unit' },
    hc_total_area: { value: () => this.inv_geospatial_area_km2, property: 'inv_geospatial_area_km2' },
    inv_compilation_effort: { value: () => this.inv_compilation_effort, property: 'inv_compilation_effort' },
    inv_completeness_optout_reason: { value: () => this.inv_completeness_optout_reason, property: 'inv_completeness_optout_reason' },
    inv_description: { value: () => this.abstract, property: 'abstract' },
    inv_notes: { value: () => this.notes, property: 'notes' },
    inv_taxonomic_class: { value: () => this.class, property: 'class' },
    inv_taxonomic_detail: { value: () => this.specific_taxon_groups, property: 'specific_taxon_groups' },
    product_type: { value: () => this.product_type, property: 'product_type' },
    quality_control: { value: () => this.quality_control, property: 'quality_control' }
  }

  /**
   * Default values
   * - if these are changed, modify reset() to match
   */

  hc_temporal_unit = '';
  inv_compilation_effort = '';
  inv_compilation_type = '';
  inv_completeness_lower = '';
  inv_completeness_optout = false;
  inv_completeness_optout_reason = '';
  inv_completeness_rationale = '';
  inv_completeness_upper = '';
  inv_date_end = '';
  inv_date_start = '';
  inv_geospatial_area_km2 = '';
  inv_geospatial_scope = '';
  inv_geospatial_site_details = '';
  inv_inventory_compilation_both = '';
  inv_inventory_type = '';
  inv_survey_days_total = '';
  inv_survey_night_day = '';
  inv_survey_protocol_detail1 = '';
  inv_survey_protocol1 = '';
  product_type = 'localinv';
  season = '';
  quality_control = '';

  // Inventories will be set to public by default
  allow_download = true;
  mol_modeling = true;
  allow_visualization = true;

  /**
   * End Default Values
   */

  public reset () {
    super.reset();

    // these should match default values of the properties above
    this.hc_temporal_unit = '';
    this.inv_compilation_effort = '';
    this.inv_compilation_type = '';
    this.inv_completeness_lower = '';
    this.inv_completeness_optout = false;
    this.inv_completeness_optout_reason = '';
    this.inv_completeness_rationale = '';
    this.inv_completeness_upper = '';
    this.inv_date_end = '';
    this.inv_date_start = '';
    this.inv_geospatial_area_km2 = '';
    this.inv_geospatial_scope = '';
    this.inv_geospatial_site_details = '';
    this.inv_inventory_compilation_both = '';
    this.inv_inventory_type = '';
    this.inv_survey_days_total = '';
    this.inv_survey_night_day = '';
    this.inv_survey_protocol_detail1 = '';
    this.inv_survey_protocol1 = '';
    this.product_type = 'localinv';
    this.season = '';
    this.quality_control = '';

    this.allow_download = true;
    this.mol_modeling = true;
    this.allow_visualization = true;
  }

  fromOverview(dataset_id, overview: any) {
    super.fromOverview(dataset_id, overview, this.specificJsonMap);
    if (this.inv_completeness_optout_reason) { this.inv_completeness_optout = true; }
  }

  surveyDatesReady() {
    return this.inv_date_start && this.inv_date_end;
  }

  completenessReady() {
    if (this.inv_completeness_optout) {
      return !!this.inv_completeness_optout_reason;
    }
    return this.inv_completeness_lower
      && this.inv_completeness_upper
      && this.inv_completeness_rationale;
  }

  requiredMetadataIsReady () : boolean {
    let requiredFields = [
      this.dataset_title,
      this.license,
      this.abstract
    ];
    // if any of the fields are false, this will be true
    let fieldsMissing = requiredFields.some(element => !element);
    // return true if no fields are missing, false if fields are missing
    return !fieldsMissing;
  }

  isReady() {
    return this.requiredMetadataIsReady()
      && this.completenessReady()
      && this.surveyDatesReady()
      && this.releasePolicyReady();
  }

}



