import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'mol-select-users-modal',
  templateUrl: './select-users-modal.component.html',
  styleUrls: ['./select-users-modal.component.css']
})
export class SelectUsersModalComponent implements OnInit {

  users = '';

  constructor(
    public dialogRef: MatDialogRef<SelectUsersModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  ngOnInit() { }

  onClick(value): void {
    this.dialogRef.close(value);
  }

  public closeDialog(save) {
    this.dialogRef.close(save ? this.users : '');
  }
}



