import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { Jsonp } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { map, toArray } from 'rxjs/operators';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/mergeMap';


export interface Member {
  member_id: string;
  name: string;
  member_type: string;
}

export interface ResourceMember {
  resource_id: string;
  member_id: string;
  name: string;
  permission_id: number;
}

@Injectable()
export class MembersService {

  constructor(
    private api: ApiService,
    private jsonp: Jsonp,
  ) { }

  public fetchAllMembers() {
    return this.jsonp.request(`${this.api.root}/acl/member/groups/list?${this.api.callback}`)
      .map(response => response.json());
  }

  fetchResourceMembers(dataset_id) {
    // return this.jsonp.request(`${this.api.root}/acl/resource/groups?${dataset_id}&${this.api.callback}`)
    //   .map(response => response.json());
    return Observable.of([
      { resource_id: '12345', member_id: '1111', name: 'User one', permission_id: 1 },
      { resource_id: '12345', member_id: '2222', name: 'User two', permission_id: 3 },
      { resource_id: '12345', member_id: '4444', name: 'User four', permission_id: 3 },
      { resource_id: '12345', member_id: '3333', name: 'User three', permission_id: 2 },
      { resource_id: '12345', member_id: '5555', name: 'User five', permission_id: 2 },
    ]);
  }

}



