import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'mol-harmonizer-instructions-modal',
  templateUrl: './harmonizer-instructions-modal.component.html',
  styleUrls: ['./harmonizer-instructions-modal.component.css']
})
export class HarmonizerInstructionsModalComponent implements OnInit {

  constructor(
      public dialogRef: MatDialogRef<HarmonizerInstructionsModalComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  ngOnInit() {
  }

  onCloseClick () {
    this.dialogRef.close();
  }

}



