import { Component, OnInit } from '@angular/core';
import { toggleNgIf } from '../../services/animations.service';

@Component({
  selector: 'mol-range-map-instructions',
  templateUrl: './range-map-instructions.component.html',
  styleUrls: ['./range-map-instructions.component.css'],
  animations: toggleNgIf
})
export class RangeMapInstructionsComponent implements OnInit {

  show = false;

  constructor() { }

  ngOnInit() {
  }

  toggleShow() {
    this.show = !this.show;
  }

}



