import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'contains'
})
export class ContainsPipe implements PipeTransform {

  transform(items: any[], searchText: string, ...fields: string[]): any[] {
    if (!items || !searchText) { return items };

    searchText = searchText.toLowerCase();

    if (!fields.length) {
      return items.filter(item => item.toLowerCase().includes(searchText));
    }

    return items.filter(item => fields.some(field => item[field].toLowerCase().includes(searchText)));
  }

}



