import { Component, OnInit, Input } from '@angular/core';
import { toggleNgIf } from '../../../services/animations.service';


@Component({
  selector: 'mol-license-section',
  templateUrl: './license-section.component.html',
  styleUrls: ['./license-section.component.css', '../section-style.css'],
  animations: toggleNgIf
})
export class LicenseSectionComponent implements OnInit {

  @Input() metadataService;

  Licenses: string[] = ['CC-0', 'CC-BY', 'CC-BY-NC'];
  holder_required = ['CC-BY', 'CC-BY-NC'];
  license_holder_required = false;

  constructor() { }

  ngOnInit() {
  }

  licenseChanged() {
    this.license_holder_required = this.holder_required.indexOf(this.metadataService.license) > -1;

    // if a license holder is not required, set license_holder to an empty string
    // if a license holder is required, set the default license_holder to the user's full name
    if(!this.license_holder_required) {
      this.metadataService.license_holder = '';
    } else {
      this.metadataService.license_holder = this.metadataService.userInfo.fullname;
    }
  }

}



