import { Pipe, PipeTransform } from '@angular/core';
import { SpeciesDataset } from '../services/species-dataset.service';

@Pipe({
  name: 'featureCt'
})
export class FeatureCtPipe implements PipeTransform {

  transform(datasets: SpeciesDataset[], args?: any): any {
   
    return datasets.reduce((p,c) => p+c.no_rows,0);
  
  }

}



