import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { Jsonp, Http } from '@angular/http';
import { map } from 'rxjs/operators';
import { Observable, BehaviorSubject, Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';


export interface DatasetListItem {
  allow_download: boolean;
  created_at: string;
  dataset_id: string;
  dataset_title: string;
  product_type: string;
  public: boolean;
  status: string;
}

export interface DatasetRecordItem {
 family: string | null,
 taxa: string | null,
 uncertainty: number | null,
 scientificname: string | null,
 common: string | null,
 family_common: string | null,
 molid: string | null,
 displaydate: string | null
}


@Injectable()
export class DatasetService {

  constructor(
    private api: ApiService,
    private jsonp: Jsonp,
    private http: Http,
  ) { }

  public fetchList() {
    return this.jsonp.request(`${this.api.root}/acl/member/dataset/list?${this.api.callback}`)
      .map(response => response.json());
  }

  public fetchOverview(dataset_id) {
    return this.jsonp.request(`${this.api.root}/acl/member/dataset/overview?lang=en&dataset_id=${dataset_id}&${this.api.callback}`)
      .map(response => response.json());
  }

  public fetchRecords(dataset_id) {
    return this.jsonp.request(`${this.api.root}/acl/member/dataset/records?dataset_id=${dataset_id}&${this.api.callback}`)
      .map(response => response.json());
  }

  public fetchERMFeature (dataset_id, feature_id) {
    return this.jsonp.request(`${this.api.root}/acl/member/dataset/feature?dataset_id=${dataset_id}&molid=${feature_id}&${this.api.callback}`)
      .map(response => response.json());
  }

  public submitERMFeature (dataset_id, submitData) {
    return this.http.post(`${this.api.root}/acl/member/dataset/addfeature?dataset_id=${dataset_id}`, submitData, { withCredentials: true });
  }

  public done(dataset_id) {
    const url = `${this.api.root}/acl/member/dataset/done?dataset_id=${dataset_id}&${this.api.callback}`;
    return this.jsonp.request(url);
  }

  public delete(dataset_id) {
    const url = `${this.api.root}/datasets/status?status=DELETED&dataset_id=${dataset_id}&${this.api.callback}`;
    return this.jsonp.request(url);
  }

  public updateMetadata(dataset_id, input) {
    // input is either a Metadata service or a raw object
    const jsonStr = input.toJson ? input.toJson() : JSON.stringify(input);

    const url = `${this.api.root}/acl/member/dataset/update?dataset_id=${dataset_id}`;
    return this.http.post(url, jsonStr, { withCredentials: true });
  }

  public fetchEml(dataset_id) {
    const eml = {
        "dataset_id": "f0614e64-4065-4d66-8a93-24dd18b2ed8c",
        "version": "v1",
        "language": "eng",
        "title": "Test_study_plants",
        "creator": {
            "firstname": "MOL Test",
            "lastname": "User",
            "org": "A Wonderful University",
            "position": "Troll"
        },
        "publish_date": null,
        "abstract": "This is a test dataset on a Monday after lunch",
        "license": {
            "link": "http://creativecommons.org/publicdomain/zero/1.0/",
            "text": "Public Domain (CC0)",
            "para": "To the extent possible under law, the publisher has waived all rights to these data and has dedicated them to the <ulink url=\"http://creativecommons.org/publicdomain/zero/1.0/legalcode\"><citetitle>Public Domain (CC0 1.0)</citetitle></ulink>. Users may copy, modify, distribute and use the work, including for commercial purposes, without restriction."
        },
        "geography": {
            "locality": "",
            "bbox": {
                "north": 45.42358,
                "south": -38.47697,
                "east": 148.58718,
                "west": -155.60667
            }
        },
        "temporal": {
            "start": "2006-10-06",
            "end": "2008-01-31"
        },
        "taxonomy": {
            "kingdom": "Plantae"
        }
    };
    return Observable.of(eml);
  }

}



