import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import { ApiService } from "../api.service";
import { InventoryMetadataService } from "../metadata/inventory-metadata.service";
import { PointsMetadataService } from "../metadata/points-metadata.service";
import { ShapeFeatureService } from "../feature/shape-feature.service";
import { PointFeatureService } from "../feature/point-feature.service";
import { PointInventoryFeatureService } from "../feature/point-inventory-feature.service";
import { ValidationService } from "../validation.service";
import { TermsService } from "../terms.service";
import { TaxaService } from "../taxa.service";
import { TranslationService } from "../translation.service";
import { Observable } from "rxjs";

const megaByteCount = function(s) {
  return (encodeURI(s).split(/%..|./).length - 1) / 1024 / 1024;
};

@Injectable()
export abstract class UploadService {
  metadata: PointsMetadataService | InventoryMetadataService;
  features:
    | PointFeatureService
    | ShapeFeatureService
    | PointInventoryFeatureService;

  requiredTerms: string[] = [];
  optionalTerms: string[] = [];

  endPoints = {
    add: "acl/member/dataset/add",
    upload: "acl/member/dataset/upload"
  };

  // number indicating megabyte size of the submitted data
  fileSize: number = null;
  fileSizeError: boolean = false;

  dataEmptyErrors: string[] = [];

  checklistContentErrorsList = [];
  termsErrorsList = [];

  abstract isValid(): boolean;
  abstract filesReady(): boolean;
  abstract checklistContentErrors(): string[];

  translations = {};

  constructor(
    protected api: ApiService,
    protected http: Http,
    public taxaService: TaxaService,
    public valid: ValidationService,
    public terms: TermsService,
    public pointFeatureService: PointFeatureService,
    public shapeFeatureService: ShapeFeatureService,
    public pointsMetadataService: PointsMetadataService,
    public inventoryMetadataService: InventoryMetadataService,
    public translationService: TranslationService
  ) {
    this.translations = translationService.getTranslations();
  }

  reset() {
    this.resetErrors();
    this.resetFeatures();
    this.resetMetadata();
  }

  resetFeatures() {
    if (this.features) {
      this.features.reset();
    }
  }

  resetMetadata() {
    if (this.metadata) {
      this.metadata.reset();
      this.metadata.create(this.taxaService);
    }
  }

  hasErrors() {
    let errors = [
      ...this.checklistContentErrorsList,
      ...this.termsErrorsList,
      ...this.dataEmptyErrors
    ];
    if (this.fileSizeError) {
      errors.push(true);
    }
    return !!errors.length;
  }

  resetErrors() {
    // reset errors
    this.checklistContentErrorsList = [];
    this.termsErrorsList = [];
    this.dataEmptyErrors = [];
    this.fileSize = null;
    this.fileSizeError = false;
  }

  // determines whether all required terms are present in the CSV
  termsErrors(): string[] {
    this.termsErrorsList = this.terms.missingRequired();
    return this.termsErrorsList;
  }

  getUrl(endPoint) {
    return `${this.api.root}/${this.endPoints[endPoint]}?dataset_id=${
      this.metadata.dataset_id
    }`;
  }

  create() {
    return Observable.concat(this.submitMetadata(), this.submitFeatureData());
  }

  submitMetadata() {
    return this.http.post(this.getUrl("add"), this.metadata.toJson(), {
      withCredentials: true
    });
  }

  checkFeatureSize() {
    // the data we send to the server will look approximately like:
    const data = {
      dataset_id: this.metadata.dataset_id,
      product_type: this.metadata.product_type,
      dataFeatures: {
        type: "FeatureCollection",
        features: this.features.dataFeatures
      },
      geomFeatures: {
        type: "FeatureCollection",
        features: this.features.geomFeatures
      },
      attrFeatures: {
        type: "FeatureCollection",
        features: this.features.attrFeatures
      }
    };
    const stringifiedData = JSON.stringify(data);
    this.fileSize = megaByteCount(stringifiedData);
    if (this.fileSize >= 32) {
      this.fileSizeError = true;
    }

    if (!this.features.dataFeatures.length) {
      this.dataEmptyErrors.push("dataFeatures");
    }
    if (!this.features.attrFeatures.length) {
      this.dataEmptyErrors.push("attrFeatures");
    }
    if (!this.features.geomFeatures.length) {
      this.dataEmptyErrors.push("geomFeatures");
    }
  }

  submitFeatureData() {
    const data = {
      dataset_id: this.metadata.dataset_id,
      product_type: this.metadata.product_type,
      dataFeatures: {
        type: "FeatureCollection",
        features: this.features.dataFeatures
      },
      geomFeatures: {
        type: "FeatureCollection",
        features: this.features.geomFeatures
      },
      attrFeatures: {
        type: "FeatureCollection",
        features: this.features.attrFeatures
      }
    };
    return this.http.post(this.getUrl("upload"), JSON.stringify(data), {
      withCredentials: true
    });
  }

  isReady() {
    return this.isValid() && this.metadata.isReady();
  }
}



