import { Component, OnInit, NgZone } from '@angular/core';
import { TitleCasePipe, I18nPluralPipe } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../services/authentication.service';
import { DatasetService } from '../../services/dataset.service';
import { TaxaService, Taxonomy, TaxonName } from '../../services/taxa.service';
import { Observable } from 'rxjs/Observable';
import { forkJoin } from "rxjs/observable/forkJoin";
import 'rxjs/add/observable/concat';
import { map } from 'rxjs/operators';
import { toggleNgIf } from '../../services/animations.service';
import { MatDialog } from '@angular/material';
import { FinalizeDatasetModalComponent } from '../../components/finalize-dataset-modal/finalize-dataset-modal.component';
import { HarmonizerInstructionsModalComponent } from '../../components/harmonizer-instructions-modal/harmonizer-instructions-modal.component';
import { TaxonConcept } from './taxon-concept';
import { normalizeSciName } from '../../services/util.service';


enum State {
  initial,
  exactTcSearch,
  firstTaxonomySelect,
  taxonomySelect,
  useTaxonomy,
  selectNames,
  error,
  saved,
  done,
};

@Component({
  selector: 'mol-taxonomy-update-page',
  templateUrl: './taxonomy-update-page.component.html',
  styleUrls: ['../../components/form.css', './taxonomy-update-page.component.scss'],
  animations: toggleNgIf,
})
export class TaxonomyUpdatePageComponent implements OnInit {

  error = '';
  dataset_id = '';
  title = '';
  overview: any;
  group = '';
  authority: Taxonomy;
  authorities: Taxonomy[] = [];
  authorityCounts: {[taonomyId: number]: number} = {};
  taxonConcepts: TaxonConcept[] = [];
  speciesToLoad: string[] = [];
  speciesLoaded = 0;
  allHarmonized = false;
  allUnharmonized = false;
  prevTaxonomyId = -1;  // Was the dataset saved with a taxonomy previously

  readonly State = State;
  state = State.initial;

  constructor(
    private authentication: AuthenticationService,
    private datasetService: DatasetService,
    private taxaService: TaxaService,
    private route: ActivatedRoute,
    private router: Router,
    private zone: NgZone,
    public dialog: MatDialog,
  ) {}

  get unverifiedCount() {
    return this.taxonConcepts.filter(tc => !tc.isVerified).length;
  }

  get verifiedCount() {
    return this.taxonConcepts.filter(tc => tc.isVerified).length;
  }

  ngOnInit() {
    this.authentication.isLoggedIn();
    this.dataset_id = this.route.snapshot.paramMap.get('dataset_id');
    forkJoin(
      this.datasetService.fetchOverview(this.dataset_id),
      this.taxaService.getTaxonomies(),
      this.taxaService.getTaxaNames(this.dataset_id),
    ).map(result => {
      this.overview = result[0][0];
      this.getAuthorities(result[1], this.overview.summary.taxa);
      this.title = this.overview.summary.dataset_title;

      const speciesList = result[2];
      speciesList.forEach(taxonName => {
        const tc = new TaxonConcept(taxonName);
        this.taxonConcepts.push(tc);
        this.speciesToLoad.push(tc.newSciName);
      });

    })
    .subscribe(
      success => {},
      error => {
        this.error = error;
        this.state = State.error;
      },
      () => {
        this.exactMatchSearchWrapper();
        this.state = State.exactTcSearch;
      },
    );
  }

  exactMatchSearchWrapper() {
    const speciesToLoad = this.speciesToLoad.slice();   // Make a copy for splicing
    const chunkSize = 20;

    const chunks: Observable<void>[] = [];
    while (speciesToLoad.length) {
      chunks.push(this.resolveExactMatches(speciesToLoad.splice(0, chunkSize)));
    }

    Observable.concat(...chunks)
      .subscribe(
        success => {},
        error => {
          this.error = error;
          this.state = State.error;
        },
        () => {
          this.countExactMatchesForAuthority();

          this.authority = this.authorities.find(auth => auth.ismaster === true) || this.authorities[0];
          const prevTcIds = this.taxonConcepts.filter(tc => tc.tcid).map(tc => tc.tcid);
          const tcid = prevTcIds.length ? prevTcIds[0] : null;
          this.taxaService.getTcInfo([tcid])
            .subscribe(
              success => {
                if (success[tcid] && success[tcid].taxonomyid) {
                  this.prevTaxonomyId = +success[tcid].taxonomyid;
                  this.authority = this.authorities.find(
                    auth => auth.taxonomy_id == this.prevTaxonomyId);
                }
                this.taxonConcepts.forEach(tc => {tc.update(this.authority.taxonomy_id)});
                this.state = this.prevTaxonomyId ? State.taxonomySelect : State.firstTaxonomySelect;
              },
              error => {},
            )
        },
      );
  }

  resolveExactMatches(verbatimNames: string[]): Observable<void> {
    return this.taxaService.getTcSearch(verbatimNames)
      .retry(3).map(exactTcSearch => {
        const sciNames = new Set([...Object.keys(exactTcSearch)]);
        sciNames.forEach(sciName => {
          this.findTaxonConcept(sciName).exactTcSearch = exactTcSearch[sciName];
        });
        this.speciesLoaded += verbatimNames.length;
      });
  }

  resolveApproximateMatches(verbatimNames: string[]): Observable<void> {
    return this.taxaService.getApproxTcSearch(verbatimNames)
      .retry(3).map(approxTcSearch => {
        const sciNames = new Set([...Object.keys(approxTcSearch)]);
        sciNames.forEach(sciName => {
          const tc = this.findTaxonConcept(sciName);
          tc.approxTcSearch = approxTcSearch[sciName];
          tc.update(this.authority.taxonomy_id);
        });
        this.speciesLoaded += verbatimNames.length;
      });
  }

  checkName(tc) {
    tc.isBusy = true;
    this.taxaService.getApproxTcSearch([tc.approxSearch()])
      .retry(3).map(approxTcSearch => {
        const sciNames = new Set([...Object.keys(approxTcSearch)]);
        sciNames.forEach(sciName => {
          const tc = this.findTaxonConcept(sciName);
          tc.approxTcSearch = approxTcSearch[sciName];
          tc.update(this.authority.taxonomy_id);
      });
    }).subscribe(
        success => {
          tc.isBusy = false;
        },
        error => {
          this.error = error;
          this.state = State.error;
        },
        () => {},
    );
  }

  approxTcSearchWrapper() {
    this.speciesLoaded = 0;
    if (!this.speciesToLoad.length) { return; }

    const speciesToLoad = this.speciesToLoad.slice();   // Make a copy for splicing
    const chunkSize = 10;

    const chunks: Observable<void>[] = [];
    while (speciesToLoad.length) {
      chunks.push(this.resolveApproximateMatches(speciesToLoad.splice(0, chunkSize)));
    }

    Observable.concat(...chunks)
      .subscribe(
        success => {},
        error => {
          this.error = error;
          this.state = State.error;
        },
        () => {
          this.state = State.selectNames;
        },
      );
  }

  useTaxonomy() {
    this.speciesToLoad = this.taxonConcepts
      .filter(tc => tc.approxSearch())
      .map(tc => tc.approxSearch());

    if (!this.speciesToLoad.length) {
      this.state = State.selectNames;
      return;
    }

    this.state = State.useTaxonomy;
    this.approxTcSearchWrapper();
  }

  authorityChanged() {
    this.taxonConcepts.forEach(tc => {
      tc.update(this.authority.taxonomy_id);
    });
    this.state = State.taxonomySelect;
  }

  finalize($event) {
    $event.stopPropagation();
    const finalDialog = this.dialog.open(FinalizeDatasetModalComponent, {
      height: '300px',
      width: '400px',
      data: {
        dataset_id: this.dataset_id,
        dataset_title: this.title,
      }
    });

    finalDialog.afterClosed().subscribe(reply => {
      if (!reply) { return; }
      const updates = this.getUpdates();
      const percentMatched =(
        100.0 * this.taxonConcepts.filter(tc => tc.tcid).length / this.taxonConcepts.length).toFixed(2);

      forkJoin(
        this.taxaService.updateTaxa(this.dataset_id, updates),
        this.datasetService.updateMetadata(
          this.dataset_id,
          {
            dataset_id: this.dataset_id,
            percent_names_matched: percentMatched
          }),
        this.datasetService.done(this.dataset_id),
      )
      .subscribe(
        success => {
          this.state = State.done;
        },
        error => {
          this.error = error;
          this.state = State.error;
        });
    });
  }

  getUpdates() {
    return this.taxonConcepts.map(tc => {
      if (tc.isVerified) {
        return {
          oldname: tc.startingSciName,
          newname: tc.newSciName,
          tc_id: tc.tcid,
        };
      } else {
        return {
          oldname: tc.startingSciName,
          newname: tc.startingSciName,
          tc_id: '',
        };
      }
    });
  }

  saveSciNames() {
    const updates = this.getUpdates();

    this.taxaService.updateTaxa(this.dataset_id, updates)
      .subscribe(
        success => {
          this.state = State.saved;
        },
        error => {
          this.error = error;
          this.state = State.error;
        });
  }

  countExactMatchesForAuthority() {
    this.authorities.forEach(auth => this.authorityCounts[auth.taxonomy_id] = 0 );
    this.taxonConcepts.forEach(tc => {
      tc.exactTcSearch.forEach(match => {
        this.authorityCounts[+match.taxonomyid] += 1;
      });
    });
  }

  getAuthorities(taxonomies, taxa) {
    const allGroup = 'All';
    const animalGroup = 'Animals';
    const plantGroup = ['Plants', 'Trees', 'Cacti', 'Conifers', 'Palms'];

    this.group = (new TitleCasePipe()).transform(taxa);

    // Add authorities for the dataset's group
    taxonomies.filter(group => group.taxonomy_group == this.group)
      .forEach(group => {
        group.taxonomies[0].forEach(auth => { this.authorities.push(auth) })
      });

    // Add animal-level backbone authority if needed
    taxonomies.filter(group => group.taxonomy_group == animalGroup && plantGroup.indexOf(this.group) === -1)
      .forEach(group => {
        group.taxonomies[0].forEach(auth => { this.authorities.push(auth) })
      });

    // Add the backbone authority
    taxonomies.filter(group => group.taxonomy_group == allGroup)
      .forEach(group => {
        group.taxonomies[0].forEach(auth => { this.authorities.push(auth) })
      });
  }

  sortTaxonConcepts(a:TaxonConcept, b:TaxonConcept): number {
    const aa = a.verbatimSciName.toLowerCase();
    const bb = b.verbatimSciName.toLowerCase();
    if (aa < bb) { return -1; }
    if (aa > bb) { return 1; }
    return 0;
  }

  selectAllUnharmonized() {
    this.taxonConcepts
      .filter(tc => !tc.isVerified)
      .forEach(tc => tc.isSelected = this.allUnharmonized);
  }

  selectAllHarmonized() {
    this.taxonConcepts
      .filter(tc => tc.isVerified)
      .forEach(tc => tc.isSelected = this.allHarmonized);
  }

  harmonize() {
    this.taxonConcepts
      .filter(tc => tc.isSelected && !tc.isVerified)
      .forEach(tc => tc.harmonize());
    this.allHarmonized = false;
    this.allUnharmonized = false;
  }

  unharmonize() {
    this.taxonConcepts
      .filter(tc => tc.isSelected && tc.isVerified)
      .forEach(tc => tc.unharmonize());
    this.allHarmonized = false;
    this.allUnharmonized = false;
  }

  nameSelected(tc) {
    tc.isSelected = true;
    tc.isEdited = false;
  }

  normalizeInput(tc) {
    tc.newSciName = normalizeSciName(tc.newSciName);
    tc.isEdited = true;
  }

  findTaxonConcept(sciName) {
    return this.taxonConcepts.find(tc => tc.newSciName ? tc.newSciName == sciName : tc.verbatimSciName == sciName);
  }

  reload() {
    this.zone.runOutsideAngular(() => {
      location.reload();
    });
  }

  goBack() {
    this.router.navigate(['dataset', this.dataset_id]);
  }

  help () {
    let dialogRef = this.dialog.open(HarmonizerInstructionsModalComponent, {
      height: '80vh',
      width: '80%'
    });
  }
}



