import { Injectable } from "@angular/core";
import { FeatureService } from './feature.service';
import { UUID } from '../util.service';
import Circle from '@turf/circle';
import { featureCollection, point } from '@turf/helpers';
import Destination from '@turf/destination';
import BboxPolygon from '@turf/bbox-polygon';

import { CoordinateTypeSelection }  from '../../components/coordinate-type-modal/coordinate-type-modal.component';


// export interface CoordinateTypeSelection {
//     areaShape: 'circle' | 'square';
//     referenceCoordinate: 'sw'| 'nw' | 'ne' | 'se' | 'center';
//     radius: number;
//     edgeLength: number;
// }

@Injectable()
export class PointInventoryFeatureService extends FeatureService {

  datum(record) {
    const verbatimSciName = this.terms.find("scientificname").mapsTo;
    return {
      geometry: null,
      type: "Feature",
      properties: {
        dataset_id: record.dataset_id,
        molid: record.molid,
        geom_id: record.geom_id,
        product_type: "localinv",
        scientificname: record[verbatimSciName],
        date: null,
        uncertainty: null
      }
    };
  }

  getRadiusFromArea(area) {
    return Math.sqrt(area / Math.PI);
  }

  circle(lat, lng, coordinateType: CoordinateTypeSelection) {
    const coordinates = [parseFloat(lng), parseFloat(lat)];
    const radiusInKm = coordinateType.area
      ? this.getRadiusFromArea(coordinateType.area) / 1000
      : coordinateType.radius / 1000;
    const circle = Circle(coordinates, radiusInKm, {
      units: "kilometers"
    });
    return circle;
  }

  square(lat, lng, coordinateType: CoordinateTypeSelection) {
    const reference = coordinateType.referenceCoordinate;

    const bounds = {
      north: null,
      south: null,
      east: null,
      west: null
    };

    const refPoint = point([lng, lat]);

    let edgeLengthKilometers = coordinateType.area
      ? Math.sqrt(coordinateType.area) / 1000
      : coordinateType.edgeLength / 1000;

    const getNorth = () => {
      const bearing = 0;
      const destination = Destination(refPoint, edgeLengthKilometers, bearing);
      return destination.geometry.coordinates[1];
    };

    const getSouth = () => {
      const bearing = 180;
      const destination = Destination(refPoint, edgeLengthKilometers, bearing);
      return destination.geometry.coordinates[1];
    };

    const getWest = () => {
      const bearing = -90;
      const destination = Destination(refPoint, edgeLengthKilometers, bearing);
      return destination.geometry.coordinates[0];
    };

    const getEast = () => {
      const bearing = 90;
      const destination = Destination(refPoint, edgeLengthKilometers, bearing);
      return destination.geometry.coordinates[0];
    };

    switch (reference) {
      case "sw":
        bounds.south = lat;
        bounds.west = lng;
        // get NW and SE
        bounds.north = getNorth();
        bounds.east = getEast();

        break;
      case "nw":
        bounds.north = lat;
        bounds.west = lng;
        // get NE and SW
        bounds.south = getSouth();
        bounds.east = getEast();
        break;
      case "ne":
        bounds.north = lat;
        bounds.east = lng;
        // get NW and SE
        bounds.south = getSouth();
        bounds.west = getWest();
        break;
      case "se":
        bounds.south = lat;
        bounds.east = lng;
        // get NE and SW
        bounds.north = getNorth();
        bounds.west = getWest();
        break;
      default:
        // center
        edgeLengthKilometers = edgeLengthKilometers / 2;
        bounds.north = getNorth();
        bounds.east = getEast();
        bounds.south = getSouth();
        bounds.west = getWest();
        break;
    }

    const bbox: [number, number, number, number] = [
      bounds.west,
      bounds.south,
      bounds.east,
      bounds.north
    ];
    return BboxPolygon(bbox);
  }

  // TODO
  geometry(record, coordinateType: CoordinateTypeSelection) {
    const lat = +record.decimal_latitude;
    const lng = +record.decimal_longitude;

    const feature =
      coordinateType.areaShape === "circle"
        ? this.circle(lat, lng, coordinateType)
        : this.square(lat, lng, coordinateType);
    feature.properties.geom_id = record.geom_id;
    feature.properties.referenceXY = [lng, lat];
    feature.properties.referenceLat = lat;
    feature.properties.referenceLng = lng;

    return feature;
  }

  addRegion(
    record,
    coordinateTypeSelection: CoordinateTypeSelection,
    isCombinedFile: boolean
  ) {
    let updatedRecord = {
      ...record
    };
    let region_id = "";
    // only need to find mapped region_id if we have one combined file
    if (isCombinedFile) {
      const verbatimRegionId = this.terms.find("region_id").mapsTo;
      region_id = updatedRecord[verbatimRegionId];
    } else {
      region_id = updatedRecord.region_id;
    }

    if (!region_id || typeof region_id === "undefined") {
      console.error("Creating regions: region_id is undefined");
    }

    // Assign a unique geom_id if this is a new region. Create cache entry.
    this.regionIdCache[region_id] = this.regionIdCache[region_id] || UUID();
    updatedRecord.geom_id = this.regionIdCache[region_id];

    // add geom features
    this.geomFeatures.push(
      this.geometry(updatedRecord, coordinateTypeSelection)
    );
  }

  addChecklistRecord(record, datasetId) {
    let updatedRecord = {
      ...record
    };
    const verbatimSciName = this.terms.find("scientificname").mapsTo;
    const verbatimRegionId = this.terms.find("region_id").mapsTo;

    updatedRecord.dataset_id = datasetId;
    updatedRecord.molid = UUID();
    updatedRecord.verbatim_scientificname = updatedRecord[verbatimSciName];
    const region_id = updatedRecord[verbatimRegionId];
    updatedRecord.geom_id = this.regionIdCache[region_id];
    this.dataFeatures.push(this.datum(updatedRecord));
    this.attrFeatures.push(...this.attributes(updatedRecord));
  }

  regionIdsMatch(geometryFile, checklistFile) {
    const self = this;
    const regionIdCache = {};
    const errors = {};

    // Build the region ID cache
    const verbatimRegionId = this.terms.find("region_id").mapsTo;
    geometryFile.forEach(record => {
      regionIdCache[record.region_id] = 1;
    });

    // Get missing region IDs
    checklistFile
      .filter(record => !regionIdCache[record[verbatimRegionId]])
      .forEach(record => {
        // Region ID: {region_id} was not found in the geometry file.
        const err = this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list","regionIdNotFoundGeometryFile"], {
          region_id: record[verbatimRegionId]
        });
        errors[err] = 1;
      });

    // Output errors
    return Object.keys(errors);
  }
}



