import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DatasetListPageComponent } from './pages/dataset-list-page/dataset-list-page.component';
import { FilesPageComponent } from './pages/files-page/files-page.component';
import { PointsSavePageComponent } from './pages/points-save-page/points-save-page.component';
import { InventorySavePageComponent } from './pages/inventory-save-page/inventory-save-page.component';
import { DatasetDetailPageComponent } from './pages/dataset-detail-page/dataset-detail-page.component';
import { TaxonomyUpdatePageComponent } from './pages/taxonomy-update-page/taxonomy-update-page.component';
import { RangeMapLandingPageComponent } from './pages/range-map-landing-page/range-map-landing-page.component';
import { RangeMapMetadataPageComponent } from './pages/range-map-metadata-page/range-map-metadata-page.component';
import { RangeMapDrawPageComponent } from './pages/range-map-draw-page/range-map-draw-page.component';
import { RangeMapFinalizeComponent } from './pages/range-map-finalize/range-map-finalize.component';
import { LandingPageComponent } from './pages/landing-page/landing-page.component';

import { environment } from '../environments/environment';


const routes: Routes = [
  {
    path: '',
    children: [
      { path: 'datasets', component: DatasetListPageComponent },
      { path: 'files', component: FilesPageComponent },
      { path: 'points-save', component: PointsSavePageComponent },
      { path: 'inventory-save', component: InventorySavePageComponent },
      { path: 'dataset/:dataset_id', component: DatasetDetailPageComponent },
      { path: 'taxonomy-update/:dataset_id', component: TaxonomyUpdatePageComponent },
      { path: 'range-map', component: RangeMapLandingPageComponent },
      { path: 'range-map/draw/:dataset_id', component: RangeMapDrawPageComponent },
      { path: 'range-map/draw/:dataset_id/:feature_id', component: RangeMapDrawPageComponent },
      { path: 'range-map/finalize/:dataset_id', component: RangeMapFinalizeComponent },
      { path: 'range-map/metadata/:dataset_id', component: RangeMapMetadataPageComponent },
      { path: 'landing', component: LandingPageComponent },
      { path: '**', redirectTo: 'datasets', pathMatch: 'prefix' }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



