import { Component, OnInit, Input } from '@angular/core';
import { toggleNgIf } from '../../../services/animations.service';


@Component({
  selector: 'mol-release-policy-section',
  templateUrl: './release-policy-section.component.html',
  styleUrls: ['../section-style.css', './release-policy-section.component.css'],
  animations: toggleNgIf,
})
export class ReleasePolicySectionComponent implements OnInit {

  @Input() metadataService;

  constructor() { }

  ngOnInit() { }

  dateChanged() {
    this.metadataService.release_date = (new Date(this.metadataService.release_date)).toISOString();
  }

  updateReleaseDate() {
    if (this.metadataService.release_policy === this.metadataService.no_release_date) {
      this.metadataService.release_date = '';
    }
  }

}



