import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { fromLatLngToPoint } from 'mercator-projection'

// let merc = require('mercator-projection');

@Injectable()
export class OverlayMapTypeService implements google.maps.MapType {

  tiles: any = {};
  private blank_tile = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAQAAAAEAAQMAAABmvDolAAAAA1BMVEUAAACnej3aAAAAAXRSTlMAQObYZgAAAB9JREFUaIHtwQENAAAAwqD3T20ON6AAAAAAAAAAAL4NIQAAAZpg4dUAAAAASUVORK5CYII=';
  onTilesLoaded: Function;
  onTilesAdded: Function;
  onInteraction: Function;
  handlers: {click?: Function, move?: Function};
  tile_url: string;
  grid_url: string;
  utfGrid: any = {};
  opacity: number;
  tileSize: any = new google.maps.Size(256, 256);

  constructor(
    tile_url: string,
    opacity?: number,
    grid_url?: string,
    private http?: HttpClient,
    handlers?: {click?: Function, move?: Function}
  ) {
    this.tile_url = tile_url;
    this.grid_url = grid_url;
    this.opacity = opacity || 0.8;
    this.http = http;
    this.handlers = handlers || {};
  }

  public getTile(coord: google.maps.Point, zoom: number, ownerDocument: Document) {

    let img = ownerDocument.createElement('img');
    let grid = this.utfGrid;
    let self = this;

    try {
      coord = this.getNormalizedCoord(coord, zoom);
      img.style.width = this.tileSize.width + 'px';
      img.style.height = this.tileSize.height + 'px';
      img.src = (this.tile_url || this.blank_tile)
        .replace('{z}', zoom.toString())
        .replace('{x}', coord.x.toString())
        .replace('{y}', coord.y.toString());
      img.style.opacity = this.opacity.toString();
      if (typeof this.onTilesAdded === 'function') { this.onTilesAdded(); }
      this.tiles[img.src] = 'loading';
      img.onload = function() {
        delete self.tiles[img.src];
        if (Object.keys(self.tiles).length == 0 && typeof self.onTilesLoaded !== "undefined") {
          self.onTilesLoaded();
        }
      }
      img.onerror = function() {
        delete self.tiles[img.src];
        if (Object.keys(self.tiles).length == 0 && typeof self.onTilesLoaded !== "undefined") {
          self.onTilesLoaded();
        }
      }

      // setup the UTF Grid
      if (this.grid_url) {
        let gurl = this.grid_url.replace('{z}', zoom.toString())
          .replace('{x}', coord.x.toString())
          .replace('{y}', coord.y.toString());
        if (!grid) { grid = {} }
        if (!grid[zoom]) { grid[zoom] = {} };
        if (!grid[zoom][coord.x]) { grid[zoom][coord.x] = {} };
        if (!grid[zoom][coord.x][coord.y]) {
          let req = this.http.jsonp(gurl, 'callback')
            .subscribe(data => {
              let gridKey: string = zoom.toString() + '-' + coord.x.toString() + '-' + coord.y.toString();
              grid[gridKey] = data;
            }, err => {
              console.log('Error with Grid Data');
              console.log(err);
            });
        }
      }

    } catch (e) { }
    return img;
  }

  public releaseTile(tile: Element | HTMLImageElement) {
    if (tile && tile['src']) {
      delete this.tiles[tile['src']];
      if (Object.keys(this.tiles).length == 0 && typeof this.onTilesLoaded !== "undefined") {
        this.onTilesLoaded();
      }
    }
  }

  public setOpacity(opacity: number) {
    this.opacity = opacity;
  }

  public onMapClickEvent = function(options) {
    let data = this.getGridValue(options.zoom, options.latLng, this.utfGrid);
    if (data) {
      if (this.handlers.click && typeof this.handlers.click === 'function') {
        this.handlers.click({
          coords: {
            lat: options.latLng.lat(),
            lng: options.latLng.lng()
          },
          data: data
        });
      }
    }
  }

  public onMapMouseoverEvent = function (options) {
    let data = this.getGridValue(options.zoom, options.latLng, this.utfGrid);
    if (data) {
      if (this.handlers.move && typeof this.handlers.move === 'function') {
        this.handlers.move({
          coords: {
            lat: options.latLng.lat(),
            lng: options.latLng.lng()
          },
          data: data
        });
      }
    }
    return data;
  }

  private getGridValue = function(zoom: number, latLng: google.maps.LatLng, grids: any) {
    let i, key, value;
    const numTiles = Math.pow(2, zoom),
      worldCoordinate = fromLatLngToPoint({ lat: latLng.lat(), lng: latLng.lng() }),
      pixelCoordinate = new google.maps.Point(
        worldCoordinate.x * numTiles,
        worldCoordinate.y * numTiles),
      tileCoordinate = new google.maps.Point(
        Math.floor(pixelCoordinate.x / 256),
        Math.floor(pixelCoordinate.y / 256)),
      gridCoordinate = new google.maps.Point(
        Math.floor((pixelCoordinate.x - tileCoordinate.x * 256) / 4),
        Math.floor((pixelCoordinate.y - tileCoordinate.y * 256) / 4)),
      gridKey: string = zoom.toString() + '-' + tileCoordinate.x.toString() + '-' + tileCoordinate.y.toString();

    try {
      i = grids[gridKey].grid[gridCoordinate.y].charCodeAt(gridCoordinate.x);
      /*
        decode the UTF code per UTF-grid spec
       https://github.com/mapbox/mbtiles-spec/blob/master/1.1/utfgrid.md
       */
      if (i >= 93) { i--; }
      if (i >= 35) { i--; }
      i -= 32;
      key = grids[gridKey].keys[i];

      value = grids[gridKey].data[String(key)];
    } catch (e) { }
    return (value);
  }

  private getNormalizedCoord = function(coord: google.maps.Point, zoom: number) {
    let y = coord.y, x = coord.x,
      tileRange = 1 << zoom;
    if (y < 0 || y >= tileRange) {
      return null;
    }

    // repeat across x-axis
    if (x < 0 || x >= tileRange) {
      x = (x % tileRange + tileRange) % tileRange;
    }

    return new google.maps.Point(x, y);
  }

}



