import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PointUploadService } from '../../services/upload/point-upload.service';

@Component({
  selector: 'mol-points-save-page',
  templateUrl: './points-save-page.component.html',
  styleUrls: ['./points-save-page.component.scss']
})
export class PointsSavePageComponent implements OnInit {

  public status = 'busy';
  public error = '';

  constructor(
    public uploadService: PointUploadService,
    private router: Router
  ) {
    if (!this.uploadService.isValid()) {
      this.router.navigate(['files']);
    }
  }

  ngOnInit() {
    this.uploadService.create().subscribe(
      success => { this.status = 'success'; },
      error => {
        let obj = error.json();
        this.status = 'error';
        this.error = (obj[0] && obj[0].error) || 'server error';
    });
  }
}



