import {
  Component,
  OnInit,
  Input,
  Output,
  ViewChild,
  ElementRef,
  EventEmitter
} from "@angular/core";
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';


@Component({
  selector: 'mol-include',
  templateUrl: './include.component.html',
  styleUrls: ['./include.component.css']
})
export class IncludeComponent implements OnInit {

  innerHTML: string;

  @Input() set src(src: string) {
    const self = this;
    self.loadHTML(src).then((response) => {
      self.node.nativeElement.innerHTML = response.text();
      this.doneLoading.emit(true);
    });
  }

  @Output() doneLoading = new EventEmitter();

  @ViewChild('node') node: ElementRef;
    constructor(private http: Http) {
  }

  ngOnInit() {
  }

  public loadHTML(src) {
    return this.http.get(src).toPromise();
  }

}



