import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


@Component({
  selector: 'mol-files-form-instructions-modal',
  templateUrl: './files-form-instructions-modal.component.html',
  styleUrls: ['./files-form-instructions-modal.component.css']
})
export class FilesFormInstructionsModalComponent implements OnInit {

  constructor(
      public dialogRef: MatDialogRef<FilesFormInstructionsModalComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  onClick(value): void {
    this.dialogRef.close(value);
  }

  ngOnInit() {
  }

}



