import { Injectable } from '@angular/core';
import * as moment from 'moment';
import { TranslationService } from "./translation.service";


export class Cleaned<T> {
  value: T;
  error: string;
}


@Injectable()
export class ValidationService {

  translations: any;

  constructor(
    public translationService: TranslationService
  ) {
    this.translations = translationService.getTranslations();
  }

  /**
   * File Validation
   */
  noBlankHeaders (file) {
    for(let header of file.fileReaderOutput.meta.fields) {
      if(!header.length) {
        return false;
      }
    }
    return true;
  }

  noWhiteSpace (file) {
    let headersWithWhitespace = [];
    for(let header of file.fileReaderOutput.meta.fields) {
      if(header.length && /\s/g.test(header)) {
        // i18n Translation: Header '${header}' has whitespace. Please remove whitespace and resubmit file.
        let err = this.translationService.getTranslation(["files-form:upload-files:error-text","headerHasWhitespace"], {
          header
        });
        headersWithWhitespace.push(err);
      }
    }
    return headersWithWhitespace;
  }

  /**
   * Individual Record Validation
   */

  completeRecord(record: any, expectedLength: number): boolean {
    // Verify that the record object has at least the expected field count
    return Object.keys(record).length === expectedLength;
  }

  cleanIsoDate(input: string, range?: {min: Date, max?: Date}): Cleaned<string> {
    // Make sure it's a parsable string
    const asMomment = moment(input, 'YYYY-MM-DD');
    if (!asMomment.isValid()) {
      // i18n Translation: "should be formatted as YYYY-MM-DD."
      let err = this.translationService.getTranslation(["files-form:upload-files:error-text","yearFormat"]);
      return {value: null, error: err};
    }

    const asDate = asMomment.toDate();
    const value = asMomment.format('YYYY-MM-DD');

    if (typeof range !== typeof undefined) {
      // Check for given ranges
      if (typeof range.min !== typeof undefined && asDate < range.min) {
        // i18n Translation: "date is out of range"
        let error = this.translationService.getTranslation(["files-form:upload-files:error-text","dateOutOfRange"]);
        return {value: null, error};
      }
      if (typeof range.max !== typeof undefined && asDate > range.max) {
        // i18n Translation: "date is out of range"
        let error = this.translationService.getTranslation(["files-form:upload-files:error-text","dateOutOfRange"]);
        return {value: null, error};
      }
    } else {
      // Exclude obvious nonsense
      if (value < '1300' || value > `${(new Date().getFullYear() + 1)}`) {
        // i18n Translation: "should be formatted as YYYY-MM-DD."
        let err = this.translationService.getTranslation(["files-form:upload-files:error-text","yearFormat"]);
        return { value: null, error: err };
      }
    }
    return {value: value, error: null};
  }

  cleanFloat(value: string, defaultValue?: string): Cleaned<number> {
    if (!value && (typeof defaultValue !== typeof undefined)) {
      value = defaultValue;
    }
    let cleaned = parseFloat(value);
    // i18n Translation: 'has an invalid float'
    let error = this.translationService.getTranslation(["files-form:upload-files:error-text","invalidFloat"]);
    return isNaN(cleaned) ? {value: null, error} : {value: cleaned, error: null};
  }

  cleanLatitude(value: string): Cleaned<number> {
    let cleaned = this.cleanFloat(value);
    if (cleaned.error) return cleaned;
    if (cleaned.value < -90.0 || cleaned.value > 90.0) {
      // i18n Translation: 'latitude is out of range'
      let error = this.translationService.getTranslation(["files-form:upload-files:error-text", "latitudeOutOfRange"], {
        latitude: cleaned.value
      });
      return {value: null, error};
    }
    return cleaned;
  }

  cleanLongitude(value: string): Cleaned<number> {
    let cleaned = this.cleanFloat(value);
    if (cleaned.error) return cleaned;
    if (cleaned.value < -180.0 || cleaned.value > 180.0) {
      // i18n Translation: 'longitude is out of range'
      let error = this.translationService.getTranslation(["files-form:upload-files:error-text","longitudeOutOfRange"], {
        longitude: cleaned.value
      });
      return {value: null, error};
    }
    return cleaned;
  }

}



