import { Injectable } from '@angular/core';
import { FeatureService } from './feature.service';
import { UUID } from '../util.service';


@Injectable()
export class PointFeatureService extends FeatureService {

  datum(record) {
    const oldSciName = this.terms.find('scientificname').mapsTo;
    const oldDate    = this.terms.find('date').mapsTo;
    const oldUncert  = this.terms.find('uncertainty_m').mapsTo;
    return {
      geometry: null,
      type: 'Feature',
      properties: {
        dataset_id: record.dataset_id,
        molid: record.molid,
        geom_id: record.geom_id,
        product_type: 'points',
        scientificname: record[oldSciName],
        date: record[oldDate],
        uncertainty: record[oldUncert]
      }
    };
  }

  geometry(record) {
    const oldLng = this.terms.find('decimal_longitude').mapsTo;
    const oldLat = this.terms.find('decimal_latitude').mapsTo;
    return {
      type: 'Feature',
      properties: {
        geom_id: record.geom_id
      },
      geometry: {
        type: 'Point',
        coordinates: [
          +record[oldLng],
          +record[oldLat]
        ]
      }
    };
  }

  addRecord(record, datasetId) {
    let updatedRecord = {
      ...record
    }
    const verbatimSciName = this.terms.find('scientificname').mapsTo;
    updatedRecord.dataset_id = datasetId;
    updatedRecord.molid = UUID();
    updatedRecord.geom_id = UUID();
    updatedRecord.verbatim_scientificname = updatedRecord[verbatimSciName];

    this.dataFeatures.push(this.datum(updatedRecord));
    this.geomFeatures.push(this.geometry(updatedRecord));
    this.attrFeatures.push(...this.attributes(updatedRecord));
  }

}



