import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FileReaderComponent, FileReaderResults } from '../file-reader.component';
import * as shp from 'shpjs/dist/shp.min.js';


@Component({
  selector: 'mol-shape-file-reader',
  templateUrl: './shape-file-reader.component.html',
  styleUrls: ['./shape-file-reader.component.css', '../file-reader.component.css']
})
export class ShapeFileReaderComponent extends FileReaderComponent implements OnInit {

  parseFile(file) {
    const self = this;
    const reader = new FileReader();

    reader.onload = function(event: any) {
      shp(event.target.result).then(function(output) {
        self.results.emit({
          controlName: self.name,
          isValid: true,
          fileName: self.fileName,
          fileReaderOutput: output,
          errors: [],
        });
      }).catch(function(error: string) {
        self.results.emit({
          controlName: self.name,
          isValid: false,
          fileName: self.fileName,
          fileReaderOutput: error,
          errors: [error],
        });
      });
    };

    reader.readAsArrayBuffer(file);
  }

}



