import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'mol-permissions-section',
  templateUrl: './permissions-section.component.html',
  styleUrls: ['./permissions-section.component.css', '../section-style.css']
})
export class PermissionsSectionComponent implements OnInit {

  @Input() metadataService;

  constructor() { }

  onPublicVizChange () {
      if(!this.metadataService.allow_visualization) {
          this.metadataService.allow_download = false;
      }
  }

  ngOnInit() {
  }

}



