import { Injectable } from '@angular/core';
import { Secrets } from '../secrets';


const googleMapsApiKey = (new Secrets()).googleMapsApiKey;
const googleMapsApiUrl = `https://maps.googleapis.com/maps/api/js?key=${googleMapsApiKey}&libraries=drawing&callback=onGoogleMapsApiLoaded&v=3.33`;


@Injectable()
export class GoogleMapsApiService {

  private promise: Promise<boolean>;

  load(): Promise<boolean> {
    // First time 'load' is called?
    if (!this.promise) {

      // Make promise to load
      this.promise = new Promise(resolve => {

        // Set callback for when google maps is loaded.
        window['onGoogleMapsApiLoaded'] = (ev) => {
          resolve(true);
        };

        let node = document.createElement('script');
        node.src = googleMapsApiUrl;
        node.type = 'text/javascript';
        document.getElementsByTagName('head')[0].appendChild(node);
      });
    }

    // Always return promise. When 'load' is called many times, the promise is already resolved.
    return this.promise;
  }
}



