/**
 * PreviewMap Module
 * - Contains the code that creates the preview map in the upload form.
 */

// Basic Map Modules
import { Map, View } from "ol";
import { getCenter } from "ol/extent";

// Layers
import TileLayer from "ol/layer/Tile";
import VectorLayer from "ol/layer/Vector";
import Group from "ol/layer/Group";

// Sources
import VectorSource from "ol/source/Vector";
import OSM from "ol/source/OSM";
import XYZ from "ol/source/XYZ";

// Styles
import Style from "ol/style/Style";
import Fill from "ol/style/Fill";
import Stroke from "ol/style/Stroke";
import CircleStyle from "ol/style/Circle";

// Geometries
import CircleGeometry from "ol/geom/Circle";
import Geometry from "ol/geom/Geometry";

// Features
import Feature from "ol/Feature";

// Overlay
import Overlay from "ol/Overlay";

import { GeoJSON } from "ol/format";
import { inherits } from "ol/util";
import {
  defaults as DefaultControls,
  ScaleLine,
  Control,
  Attribution
} from "ol/control";
import Collection from "ol/Collection";

import { randomColor } from "randomcolor";

import Select from "ol/interaction/Select";
import {
  click as ClickCondition,
  pointerMove as PointerMoveCondition
} from "ol/events/condition";

type MapType = "point-inventory" | "inventory" | "occurrence";

interface MapLayers {
  layers: VectorLayer[];
  extent: any;
}

const LayerSwitcherControl = function(previewMap, opt_options?) {
  const options = opt_options || {};

  this.previewMap = previewMap;
  this.selected = "map";

  const buttonClick = basemap => {
    this.selected = basemap;
    this.previewMap.setBasemap(basemap);
    if (basemap === "satellite") {
      satelliteButton.className = "layer-switch-control-satellite active";
      mapButton.className = "layer-switch-control-map";
    } else {
      mapButton.className = "layer-switch-control-map active";
      satelliteButton.className = "layer-switch-control-satellite";
    }
  };

  // buttons
  const satelliteButton = document.createElement("button");
  satelliteButton.className = "layer-switch-control-satellite";
  satelliteButton.innerHTML = "Satellite";
  satelliteButton.addEventListener(
    "click",
    () => {
      buttonClick("satellite");
    },
    false
  );

  const mapButton = document.createElement("button");
  mapButton.className = "layer-switch-control-map active";
  mapButton.innerHTML = "Map";
  mapButton.addEventListener(
    "click",
    () => {
      buttonClick("map");
    },
    false
  );

  // container
  const container = document.createElement("div");
  container.className = "mol-layer-switch-control";

  container.appendChild(mapButton);
  container.appendChild(satelliteButton);

  Control.call(this, {
    element: container,
    target: options.target
  });
};

inherits(LayerSwitcherControl, Control);

const DEFAULTS = {
  fill: new Fill({
    color: "rgba(255,255,255,0.4)"
  }),
  stroke: new Stroke({
    color: "#3399CC",
    width: 1.25
  })
};

const DEFAULT_STYLE = [
  new Style({
    image: new CircleStyle({
      fill: DEFAULTS.fill,
      stroke: DEFAULTS.stroke,
      radius: 5
    }),
    fill: DEFAULTS.fill,
    stroke: DEFAULTS.stroke
  })
];

const STYLES = {
  /**
   * Given a feature in an inventory, style the feature based on its geometry type
   * - Shapefile-based inventories will only have polygons, whereas Point-Inventory / coordinate
   *   based inventories will display a reference point in addition to generated polygons
   */
  inventory: (feature, resolution) => {
    let geometryType = feature.getGeometry().getType();
    if (geometryType === "Polygon" || geometryType === "MultiPolygon") {
      return new Style({
        fill: new Fill({
          color: feature.get("color")
        }),
        stroke: new Stroke({
          color: "rgba(0,0,0,0.6)",
          width: 1
        })
      });
    }
    return DEFAULT_STYLE;
  },
  occurrence: (feature, resolution) => {
    return [
      new Style({
        geometry: new CircleGeometry(
          feature.getGeometry().getCoordinates(),
          feature.get("uncertainty")
        ),
        fill: new Fill({
          color: "rgba(0,0,0,0.2)"
        }),
        stroke: new Stroke({
          color: "rgba(0,0,0,0.5)",
          width: 1
        })
      }),
      new Style({
        image: new CircleStyle({
          fill: new Fill({
            color: "#BBBBBB"
          }),
          stroke: new Stroke({
            color: "#000000",
            width: 1
          }),
          radius: 3
        })
      })
    ];
  },
  referencePoint: new Style({
    image: new CircleStyle({
      fill: new Fill({
        color: "rgba(0,0,0,0.5)"
      }),
      radius: 3
    })
  })
};

const HOVER_STYLE = {
  inventory: (feature, resolution) => {
    let geometryType = feature.getGeometry().getType();
    if (geometryType === "Polygon" || geometryType === "MultiPolygon") {
      return new Style({
        fill: new Fill({
          color: feature.get("color")
        }),
        stroke: new Stroke({
          color: "rgba(0,0,0,0.6)",
          width: 2
        })
      });
    }
    return DEFAULT_STYLE;
  },
  occurrence: (feature, resolution) => {
    return [
      new Style({
        geometry: new CircleGeometry(
          feature.getGeometry().getCoordinates(),
          feature.get("uncertainty")
        ),
        fill: new Fill({
          color: "rgba(0,0,0,0.2)"
        }),
        stroke: new Stroke({
          color: "rgba(0,0,0,0.5)",
          width: 1
        })
      }),
      new Style({
        image: new CircleStyle({
          fill: new Fill({
            color: "#BBBBBB"
          }),
          stroke: new Stroke({
            color: "#000000",
            width: 2
          }),
          radius: 3
        })
      })
    ];
  }
};

export default class PreviewMap {
  map: Map;
  geoJsonFormat: GeoJSON = new GeoJSON({
    dataProjection: "EPSG:4326",
    featureProjection: "EPSG:3857"
  });
  baseMaps = {
    map: new TileLayer({
      source: new XYZ({
        url: "https://basemaps.cartocdn.com/light_all/{z}/{x}/{y}@2x.png",
        tilePixelRatio: 2,
        attributions: " CARTO"
      })
    }),
    satellite: new TileLayer({
      source: new XYZ({
        url: "https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
        tilePixelRatio: 2,
        attributions: " CARTO"
      })
    })
  };
  clickSelect = new Select({
    condition: ClickCondition,
    filter: (feature, layer) => {
      if (feature) {
        return true;
      }
      return false;
    },
    style: (feature, resolution) => {
      if (this.datasetType === "inventory") {
        return HOVER_STYLE.inventory(feature, resolution);
      } else if (this.datasetType === "occurrence") {
        return HOVER_STYLE.occurrence(feature, resolution);
      }
    }
  });
  pointerMoveSelect = new Select({
    condition: PointerMoveCondition,
    filter: (feature, layer) => {
      if (feature) {
        return true;
      }
      return false;
    },
    style: (feature, resolution) => {
      if (this.datasetType === "inventory") {
        return HOVER_STYLE.inventory(feature, resolution);
      } else if (this.datasetType === "occurrence") {
        return HOVER_STYLE.occurrence(feature, resolution);
      }
    }
  });
  attribution = new Attribution({
    collapsible: false
  });
  scaleBar: ScaleLine = new ScaleLine();
  mapData = null;
  layerGroup = new Group();
  selectedFeature: Feature = null;
  datasetType: "occurrence" | "inventory" = null;
  popup: Overlay;

  constructor() {}

  setBasemap(basemap) {
    this.layerGroup.setLayers(
      new Collection([this.baseMaps[basemap], ...this.mapData.layers])
    );
  }

  setData(features, type: MapType) {
    // clear any previous map
    this.clearMap();
    switch (type) {
      case "point-inventory":
        this.datasetType = "inventory";
        this.mapData = this.createPointInventoryMap(features);
        break;
      case "inventory":
        this.datasetType = "inventory";
        this.mapData = this.createInventoryMap(features);
        break;
      case "occurrence":
      default:
        this.datasetType = "occurrence";
        this.mapData = this.createOccurrenceMap(features);
        break;
    }
  }

  reRenderMap() {
    if (this.map) {
      this.map.setTarget("map");
    }
  }

  clearMap() {
    if (this.map) {
      this.map.setTarget(null);
      this.map = null;
    }
  }

  assignUniqueColorsToFeatures(features) {
    for (let feature of features) {
      feature.set(
        "color",
        randomColor({
          format: "rgba",
          alpha: 0.6
        })
      );
    }
  }

  createOccurrenceMap(features) {
    let fc = {
      type: "FeatureCollection",
      features: features
    };

    let fcSource = new VectorSource({
      features: this.geoJsonFormat.readFeatures(fc)
    });

    let fcLayer = new VectorLayer({
      source: fcSource,
      style: STYLES.occurrence
    });

    let extent = fcSource.getExtent();

    return {
      layers: [fcLayer],
      extent
    };
  }

  createInventoryMap(features) {
    let fc = {
      type: "FeatureCollection",
      features: features
    };

    let fcSource = new VectorSource({
      features: this.geoJsonFormat.readFeatures(fc)
    });

    this.assignUniqueColorsToFeatures(fcSource.getFeatures());

    let fcLayer = new VectorLayer({
      source: fcSource,
      style: STYLES.inventory
    });

    let extent = fcSource.getExtent();

    return {
      layers: [fcLayer],
      extent
    };
  }

  createPointInventoryMap(features) {
    /**
     * Gather components to build feature preview layer
     * - Feature Collection
     * - Layer Source
     * - Vector Layer
     * - Layer Extent
     */
    let fc = {
      type: "FeatureCollection",
      features: features
    };

    let fcSource = new VectorSource({
      features: this.geoJsonFormat.readFeatures(fc)
    });

    this.assignUniqueColorsToFeatures(fcSource.getFeatures());

    let fcLayer = new VectorLayer({
      source: fcSource,
      style: STYLES.inventory
    });

    let extent = fcSource.getExtent();

    /**
     * Gather components to build layer to show original reference points
     * - Feature Collection
     * - Layer Source
     * - Vector Layer
     */
    let referencePoints = {
      type: "FeatureCollection",
      features: []
    };

    for (let feature of fc.features) {
      referencePoints.features.push({
        type: "Feature",
        geometry: {
          type: "Point",
          coordinates: feature.properties.referenceXY
        },
        properties: {}
      });
    }

    let referencePointsSource = new VectorSource({
      features: this.geoJsonFormat.readFeatures(referencePoints)
    });

    let referencePointsLayer = new VectorLayer({
      source: referencePointsSource,
      style: STYLES.referencePoint
    });

    return {
      layers: [referencePointsLayer, fcLayer],
      extent
    };
  }

  setSelectedFeature(feature: Feature) {
    this.selectedFeature = feature;
  }

  unsetSelectedFeature() {
    this.selectedFeature = null;
  }

  changePopupContents () {
    // get selected feature properties
    let properties = this.selectedFeature.getProperties();
    const newProperties = {
      ...properties
    };

    // remove some properties
    delete newProperties.color;
    delete newProperties.geometry;
    delete newProperties.geom_id;

    // enumerate through properties and create a <dl>
    const keys = Object.keys(newProperties);
    const list = [];
    for(let key of keys) {
      let value = newProperties[key];
      if(key && value) {
        list.push(`
          <li>
            <strong>${key}:</strong>
            ${value}
          </li>
        `)
      }
    }
    const content = `
      <ul>
        ${list.join('')}
      </ul>
    `;

    // set popup content to <dl>
    this.popup.getElement().innerHTML = content;
  }

  hidePopup () {
    this.popup.getElement().classList.add("hidden");
  }

  showPopup (clickCoordinates) {
    const geom: Geometry = this.selectedFeature.getGeometry();
    const geomType: string = geom.getType();
    let coordinates: number[];

    // get location to show popup based on currently selected feature
    switch (geomType) {
      case "Point":
        coordinates = geom.getCoordinates();
        break;
      case "Polygon":
      default:
        coordinates = clickCoordinates;
        break;
    }

    this.popup.setPosition(coordinates);
    this.popup.getElement().classList.remove("hidden");
    this.changePopupContents();
  }

  // create preview map
  createMap() {
    if (!this.map && this.mapData && this.mapData.layers.length) {
      let view = new View({
        center: [0, 0],
        zoom: 0
      });

      this.clickSelect.on("select", e => {
        if (e.selected.length) {
          this.setSelectedFeature(e.selected[0]);
          this.showPopup(e.mapBrowserEvent.coordinate);
        } else {
          this.unsetSelectedFeature();
          this.hidePopup();
        }
      });

      // set map layers
      this.layerGroup.setLayers(
        new Collection([this.baseMaps.map, ...this.mapData.layers])
      );

      // recreate popup div if map is cleared
      if(!document.getElementById("map-popup")) {
        const overlayContainer = document.getElementsByClassName("map-overlays")[0];
        const popupDiv = document.createElement("div");
        popupDiv.id = "map-popup";
        overlayContainer.appendChild(popupDiv);
      }

      this.popup = new Overlay({
        position: [0, 0],
        offset: [0, -13],
        positioning: "bottom-center",
        element: document.getElementById("map-popup")
      });

      this.map = new Map({
        target: "map",
        controls: DefaultControls({
          attribution: false
        }).extend([
          this.scaleBar,
          new LayerSwitcherControl(this),
          this.attribution
        ]),
        layers: this.layerGroup,
        overlays: [this.popup],
        view: view
      });

      this.map.addInteraction(this.clickSelect);
      this.map.addInteraction(this.pointerMoveSelect);

      view.fit(this.mapData.extent);

      if (view.getZoom() > 21) {
        view.setZoom(21);
      }
    }
  }
}



