import {
  enableProdMode,
  TRANSLATIONS,
  TRANSLATIONS_FORMAT,
  LOCALE_ID
} from "@angular/core";
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';

import { AppModule } from './app/app.module';
import { environment } from './environments/environment';

// i18n
import { xlfs as translations } from './locale/translations';
import { LOCALE_LIST } from './locale/localeList';

function setCookie(name, value, days?) {
  var expires = "";
  if (days) {
    var date = new Date();
    date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/";
}

function getCookie(name) {
  var nameEQ = name + "=";
  var ca = document.cookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == ' ') c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}

if (environment.production) {
  enableProdMode();
}

let localeId = "en-US";
if (!getCookie("lang")) {
  setCookie("lang", "en-US");
} else {
  let cookieLocale = getCookie("lang");
  if (LOCALE_LIST.indexOf(cookieLocale) > -1) {
    localeId = cookieLocale;
  }
}

platformBrowserDynamic().bootstrapModule(AppModule, {
  providers: [
    { provide: TRANSLATIONS, useValue: translations[localeId] },
    { provide: TRANSLATIONS_FORMAT, useValue: "xlf" },
    { provide: LOCALE_ID, useValue: localeId }
  ]
});



