/**
 * Expert Range Mapper feature attributes editing modal
 */

import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { RangeMapService } from '../../services/range-map.service';

@Component({
  selector: 'mol-feature-attributes-dialog',
  templateUrl: './feature-attributes-dialog.component.html',
  styleUrls: ['./feature-attributes-dialog.component.scss']
})
export class FeatureAttributesDialogComponent implements OnInit {

  prevFeatureAttributes = null;
  prevCustomAttributes = null;

  constructor(
    public dialogRef: MatDialogRef<FeatureAttributesDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public rangeMapService: RangeMapService
  ) {
    this.prevFeatureAttributes = JSON.parse(JSON.stringify(rangeMapService.feature.featureAttributes));
    this.prevCustomAttributes = JSON.parse(JSON.stringify(rangeMapService.feature.customFeatureAttributes));
  }

  cancel () {
    // this.rangeMapService.feature.featureAttributes = this.prevFeatureAttributes;
    // this.rangeMapService.feature.customFeatureAttributes = this.prevCustomAttributes;
    this.dialogRef.close();
  }

  saveAndClose () {
    this.dialogRef.close();
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  submit () {
    this.dialogRef.close();
     this.rangeMapService.feature.featureBeforeSubmitSubj.next(true);
  }

  checkRequiredValues () {
    return (
      this.rangeMapService.feature.featureAttributes.presence
      && this.rangeMapService.feature.featureAttributes.origin
      && this.rangeMapService.feature.featureAttributes.seasonality
      && this.rangeMapService.feature.featureAttributes.temporal_scope_start
      && this.rangeMapService.feature.featureAttributes.temporal_scope_end
    );
  }

  ngOnInit() {

  }

}



