import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


@Component({
  selector: 'mol-finalize-dataset-modal',
  templateUrl: './finalize-dataset-modal.component.html',
  styleUrls: ['./finalize-dataset-modal.component.css']
})
export class FinalizeDatasetModalComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<FinalizeDatasetModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  onClick(value): void {
    this.dialogRef.close(value);
  }

  ngOnInit() {
  }

}



