import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import { ApiService } from "../api.service";
import { UploadService } from "./upload.service";
import { TaxaService } from "../taxa.service";
import { ValidationService } from "../validation.service";
import { FileReaderResults } from "../../components/file-reader/file-reader.component";
import { InventoryMetadataService } from "../metadata/inventory-metadata.service";
import { PointsMetadataService } from "../metadata/points-metadata.service";
import { ShapeFeatureService } from "../feature/shape-feature.service";
import { PointFeatureService } from "../feature/point-feature.service";
import { TermsService } from "../terms.service";
import { TranslationService } from "../translation.service";

import { Subject } from "rxjs";

@Injectable()
export class InventoryUploadService extends UploadService {
  readonly requiredTerms = ["scientificname", "region_id"];
  readonly optionalTerms = [
    "abundance",
    "catalog_number",
    "class",
    "collection_code",
    "coordinate_precision",
    "country_code",
    "day",
    "depth",
    "depth_accuracy",
    "elevation",
    "elevation_accuracy",
    "establishment_means",
    "family",
    "genus",
    "geodetic_datum",
    "global_unique_identifier",
    "identified_by",
    "infraspecific_epithet",
    "institution_code",
    "kingdom",
    "license",
    "locality",
    "month",
    "order",
    "photographer",
    "phylum",
    "publishing_org_key",
    "record_number",
    "record_source",
    "recorded_by",
    "species",
    "taxon_rank",
    "type_status",
    "uncertainty_m",
    "year"
  ];

  shapeFile;
  checklistFile;

  geomFeaturesCreated = new Subject();

  constructor(
    protected api: ApiService,
    protected http: Http,
    public taxaService: TaxaService,
    public valid: ValidationService,
    public terms: TermsService,
    public pointFeatureService: PointFeatureService,
    public shapeFeatureService: ShapeFeatureService,
    public pointsMetadataService: PointsMetadataService,
    public inventoryMetadataService: InventoryMetadataService,
    public translationService: TranslationService
  ) {
    super(
      api,
      http,
      taxaService,
      valid,
      terms,
      pointFeatureService,
      shapeFeatureService,
      pointsMetadataService,
      inventoryMetadataService,
      translationService
    );
    this.metadata = inventoryMetadataService;
    this.features = this.shapeFeatureService;
  }

  reset() {
    this.terms.setTermLevels(this.requiredTerms, this.optionalTerms);
    this.shapeFile = undefined;
    this.checklistFile = undefined;
    super.reset();
  }

  filesReady(): boolean {
    return this.shapeFile && this.checklistFile;
  }

  isValid(): boolean {
    return this.filesReady();
  }

  initVerbatims() {
    this.terms.initVerbatims(this.checklistFile.fileReaderOutput.meta.fields);
  }

  getChecklistResults(results: FileReaderResults): void {
    this.checklistFile = results;
  }

  getShapeFileResults(results: FileReaderResults): void {
    this.shapeFile = results;
  }

  contentsAreValid(): boolean {
    return true;
  }

  // Checks to see whether region_id in the shapefile match the region_id in the csv
  checklistContentErrors(): string[] {
    const errors = (<ShapeFeatureService>this.features).regionIdsMatch(
      this.shapeFile.fileReaderOutput.features,
      this.checklistFile.fileReaderOutput.data
    );

    let noBlankHeaders = this.valid.noBlankHeaders(this.checklistFile);

    if (!noBlankHeaders) {
      // i18n Translation: "Checklist file has empty column header"
      let err = this.translationService.getTranslation(["files-form:match-columns-step:form:matching-errors-list", "checklistFileEmptyColumnHeader"]);
      errors.push(err);
    }

    this.checklistContentErrorsList = errors;
    return errors;
  }

  createFeatures() {
    const self = this;
    const regionIdCache = {};
    self.features.reset();
    const expectedLength =
      self.checklistFile.fileReaderOutput.meta.fields.length;

    self.shapeFile.fileReaderOutput.features.forEach(feature => {
      (<ShapeFeatureService>self.features).addShapeRecord(feature);
    });

    self.checklistFile.fileReaderOutput.data
      .filter(record => this.valid.completeRecord(record, expectedLength))
      .forEach(record => {
        (<ShapeFeatureService>this.features).addChecklistRecord(
          record,
          self.metadata.dataset_id
        );
      });

    this.geomFeaturesCreated.next(this.shapeFeatureService.geomFeatures);

    this.checkFeatureSize();
  }
}



