import * as moment from 'moment';
import { BehaviorSubject, Subject } from 'rxjs';
import Combine from '@turf/combine';
import Truncate from '@turf/truncate';
import { feature, featureCollection } from '@turf/helpers';
import * as wkx from 'wkx';

import { UUID, getCookie, setCookie, eraseCookie } from './util.service';
import { RangeMapMetadataService } from './metadata/range-map-metadata.service';

interface Species {
  taxa: string;
  tc_id: string;
  vernacular: string;
  scientificname: string;
}

interface DataFeatures {
  dataset_id: string;
  molid: string;
  geom_id: string;
  scientificname: string;
  tc_id: string;
  product_type: 'range';
  uncertainty_m: number;
  date: string;
}

interface GeomFeatures {
  geom_id: string;
  wkt: string;
}

interface AttrFeature {
  molid: string;
  metadata_field: string;
  metadata_value: string;
}

interface CustomAttribute {
  key: string;
  value: string;
}

interface HiddenAttributes {
  lastUserId: string;
  lastEditAt: string;
}

interface RangeMapFeatureError {
  error: string;
  type: 'getFeature' | 'submitFeature' | 'invalidGeoJson';
  routerLink?: string[];
}

interface RangeMapAPIFeature {
  dataFeatures: DataFeatures,
  geomFeatures: GeomFeatures,
  attrFeatures: AttrFeature[]
}

interface ResponseData {
  dataset_id: string,
  feature: RangeMapAPIFeature,
  molid: string
}

export type RangeMapFeature = GeoJSON.FeatureCollection<GeoJSON.Polygon | GeoJSON.MultiPolygon>;

/**
 * Contains functions for sending range map GeoJSON to the API
 */
export class RangeMapFeatureService {

  // Subjects. Subscribed to by various components. For:
  // when stuff goes right
  featureBeforeSubmitSubj = new Subject();
  featureSubmitSubj = new Subject();
  getFeatureSubj = new BehaviorSubject<RangeMapFeature>(null);

  // when stuff goes wrong
  errorSubj = new Subject<RangeMapFeatureError>();

  metadata = null;
  datasetService = null;
  speciesDatasetService = null;
  molid = null;

  loadedFeature = null;

  mode: 'new' | 'edit' = 'new';

  geoJson: GeoJSON.FeatureCollection<GeoJSON.Polygon> | any;
  geoJsonValid = false;
  taxonomy: string;

  scientificname: string;
  tc_id: string = "";
  species: Species | string;
  speciesInfo: any = null;

  // the data structure for each feature submission to the API
  submitData: RangeMapAPIFeature;

  allowedGeometryTypes = ['MultiPolygon', 'Polygon'];

  featureAttributes = {
    // required
    scientificname: null,
    presence: 'extant',
    origin: 'native',
    seasonality: 'resident',
    temporal_scope_start: null,
    temporal_scope_end: null,
    // optional
    geographic_coverage: null,
    subspecies: null,
    population: null,
    source: null,
    notes: null,
    taxonomic_citation: null,
    marine_terrestrial: null
  };

  hiddenFeatureAttributes: HiddenAttributes = {
    lastUserId: null,
    lastEditAt: null,
  }

  customFeatureAttributes: CustomAttribute[] = [];

  constructor(
    speciesDatasetService,
    datasetService,
    metadata: RangeMapMetadataService,
    options?: {
      molid?: string,
      species?: any
    }
  ) {
    this.metadata = metadata;
    this.datasetService = datasetService;
    this.speciesDatasetService = speciesDatasetService;

    if(options && options.molid) {
      this.molid = options.molid;
      this.mode = 'edit';
      this.getFeatureFromServer();
    } else {
      this.molid = UUID();
      this.mode = 'new';
      if(options.species) {
        this.setSpecies(options.species);
      }
    }

    this.speciesDatasetService.info.subscribe(info => {
      this.speciesInfo = info;
    });
  }

  getFeatureFromServer () {
    const self = this;

    // call API for feature info
    this.datasetService.fetchERMFeature(this.metadata.dataset_id, this.molid).subscribe( (response: ResponseData) => {
      let feature = response.feature;
      // setSpecies
      self.setSpecies(feature.dataFeatures.scientificname);
      self.fromAttrFeatures(feature.attrFeatures);

      // setGeoJson
      const geoJSON = self.getGeoJSONFromWKT(feature.geomFeatures.wkt);
      if(self.validGeoJSON(geoJSON)) {
        self.setGeoJson(geoJSON);
        self.getFeatureSubj.next(geoJSON);
      } else {
        console.error("invalid GeoJSON feature");
        self.errorSubj.next({
          error: 'Invalid GeoJSON',
          type: 'invalidGeoJson'
        });
      }

    }, error => {
      let errorMsg = self.getErrorMessage(error) ? self.getErrorMessage(error) : "Error loading feature";
      self.errorSubj.next({
        error: errorMsg,
        type: 'getFeature'
      });
    });
  }

  // transform Google Maps drawing output to MOL data submission format
  createSubmitData (dataset_id) {
    const molid = this.molid;
    const geom_id = UUID();

    // DataFeatures
    const dataFeatures: DataFeatures =  {
      dataset_id,
      molid,
      geom_id,
      tc_id: this.tc_id,
      scientificname: this.scientificname,
      product_type: 'range',
      uncertainty_m: null,
      date: null
    }

    // GeomFeatures
    const geomFeatures: GeomFeatures = {
      geom_id,
      wkt: this.getWKTFromGeoJSON()
    }

    // AttrFeatures
    const attrFeatures: AttrFeature[] = this.getAttrFeatures(molid);

    this.submitData = {
      dataFeatures,
      geomFeatures,
      attrFeatures,
    };

    return this.submitData;
  }

  getGeoJSONFromWKT (wkt): RangeMapFeature {
    const geom = wkx.Geometry.parse(wkt);
    const geoJson: any = geom.toGeoJSON();
    if(geoJson.type === "FeatureCollection") {
      return geoJson;
    } else if (geoJson.type === "MultiPolygon" || geoJson.type === "Polygon") {
      const f = feature(geoJson);
      const fc = featureCollection([f]) as any as RangeMapFeature;
      return fc;
    }
    console.error("incorrect geometry returned from server", wkt, geoJson);
  }

  getWKTFromGeoJSON () {
    const multipolygon = Combine(this.geoJson) as any as GeoJSON.FeatureCollection<GeoJSON.MultiPolygon>;
    const truncated = Truncate(multipolygon as any, {precision: 6, coordinates: 2});
    const wkt = wkx.Geometry.parseGeoJSON(truncated.features[0].geometry).toWkt();
    return wkt;
  }

  // take attr features from this component and convert them to API schema
  getAttrFeatures (molid): AttrFeature[] {

    const attrFeatures: AttrFeature[] = [];

    // assign hidden attributes
    this.hiddenFeatureAttributes.lastEditAt = moment().toISOString();
    if(this.metadata.user_id) {
      this.hiddenFeatureAttributes.lastUserId = this.metadata.user_id;
    }

    // get other values
    for (const key in this.featureAttributes) {
      const value = this.featureAttributes[key];
      if (value) {
        attrFeatures.push({
          molid,
          metadata_field: key,
          metadata_value: value
        });
      }
    }
    for (const key in this.hiddenFeatureAttributes) {
      const value = this.hiddenFeatureAttributes[key];
      if (value) {
        attrFeatures.push({
          molid,
          metadata_field: key,
          metadata_value: value
        });
      }
    }
    for (const customAttribute of this.customFeatureAttributes) {
      if(customAttribute.key && customAttribute.value) {
        attrFeatures.push({
          molid,
          metadata_field: customAttribute.key,
          metadata_value: customAttribute.value
        });
      }
    }
    return attrFeatures;
  }

  // Take attr features from the API and convert them back to the schema used in this component
  fromAttrFeatures (attrFeatures: AttrFeature[]) {
    const featureAttributes = Object.keys(this.featureAttributes);
    const hiddenAttributes = Object.keys(this.hiddenFeatureAttributes);

    for (const attrFeature of attrFeatures) {
      const key = attrFeature.metadata_field;
      const value = attrFeature.metadata_value;
      const isFeatureAttribute = (featureAttributes.indexOf(key) > -1) ? true : false;
      const isHiddenAttribute = (hiddenAttributes.indexOf(key) > -1) ? true : false;
      if(isFeatureAttribute) {
        this.featureAttributes[key] = value;
      } else if (isHiddenAttribute) {
        this.hiddenFeatureAttributes[key] = value;
      } else {
        this.customFeatureAttributes.push({key, value});
      }
    }

  }

  setGeoJson (geoJson) {
    const isValid = this.validGeoJSON(geoJson);
    if(isValid) {
      this.geoJson = geoJson;
      this.geoJsonValid = true;
    } else {
      this.geoJsonValid = false;
      console.error("Can\'t set GeoJSON: Invalid GeoJSON");
    }
  }

  /**
   * When the user selects or types a species in the autocomplete box
   * 1. Set service variables scientificname and tc_id
   * 2. Get species from the dataset service to fill autocomplete
   * 3. If there's a tc_id, get datasets from MOL
   */
  setSpecies (species: any, tcid?: string) {
     this.species = species;
     if (species) {
       // if species is an object, it means the user selected an autocomplete option
       if (typeof species === 'object') {
          this.scientificname = species.scientificname;
          this.tc_id = species.tc_id;
        } else if (tcid) {
          this.scientificname = species;
          this.tc_id = tcid;
        } else {
          this.scientificname = species;
        }
        this.speciesDatasetService.getSpeciesInfo(this.scientificname);
        if(this.tc_id) {
          this.speciesDatasetService.getDatasets(this.scientificname);
        }
        this.metadata.scientific_name = this.scientificname;
        this.featureAttributes.scientificname = this.scientificname;
     } else {
       this.speciesDatasetService.clear();
     }
  }

  resetSpecies () {
    this.species = null;
    this.scientificname = null;
    this.tc_id = null;
    this.speciesInfo = null;
    this.speciesDatasetService.clear();
  }

  validGeoJSON (opt_geojson?: RangeMapFeature) {
    const geoJson = (opt_geojson) ? opt_geojson : this.geoJson;
    if(geoJson && geoJson.type === "FeatureCollection" && geoJson.features.length > 0) {
        return true;
    }
    return false;
  }

  addCustomAttribute() {
    this.customFeatureAttributes.push({
      key: "",
      value: ""
    });
  }

  removeCustomAttribute (i) {
    this.customFeatureAttributes.splice(i, 1);
  }

  getErrorMessage (error) {
    let errorMsg = "";
    if (error.body) {
      errorMsg = JSON.parse(error._body)[0].error;
    } else if (error[0] && error[0].error) {
      errorMsg = error[0].error;
    }
    return errorMsg;
  }

  submitFeature () {
    if(this.scientificname) {
      setCookie('scientificname',this.scientificname, 1);
    }
    if(this.tc_id) {
      setCookie('tc_id',this.tc_id, 1);
    }
    const featureSubmitData = this.createSubmitData(this.metadata.dataset_id);
    this.datasetService.submitERMFeature(this.metadata.dataset_id, featureSubmitData).subscribe(response => {
      this.featureSubmitSubj.next(response);
    }, error => {
      let errorMsg = this.getErrorMessage(error) ? this.getErrorMessage(error) : "Error submitting feature";
      this.errorSubj.next({
        error: errorMsg,
        type: 'submitFeature'
      });
    });
  }

}



