/**
 * Range Map Service
 * Controls data as the user moves through creating a new expert range map, including:
 * 1. Selecting a taxonomic group, dataset type (range-map-landing)
 * 2. Assigning metadata to a range map dataset
 * 3. Creating a new range map feature
 */

import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Subject } from 'rxjs/Subject';

import { ApiService } from './api.service';
import { AuthenticationService } from './authentication.service';

import { DatasetService } from './dataset.service';
import { RangeMapMetadataService } from './metadata/range-map-metadata.service';
import { RangeMapFeatureService } from './range-map-feature.service';
import { SpeciesDatasetService, SpeciesDataset, DatasetType } from './species-dataset.service';
import { TaxaService } from './taxa.service';
import { TranslationService } from './translation.service';

// Roughly, the format in which the API returns information about a dataset
export interface DatasetOverview {
  error: any;
  group_list: {
    count: number;
    taxa: string;
  }[];
  metadata: {
    section: string;
    children: {
      type: string;
      id: string;
      value: string;
      label: string;
    }[];
  }[];
  records_list: {
    common: string;
    displaydate: string;
    family: any;
    family_common: any;
    recid: string;
    scientificname: string;
    taxa: string;
    tc_id: string;
    uncertainty: any;
  }[];
  sites_list: any;
  species_list: {
    common: string;
    count: number;
    scientificname: string;
    taxa: string;
    tc_id: null;
  }[];
  summary: {
    allow_download: boolean;
    counts: {
      records: number;
      sites: number;
      species: number;
      taxa: number;
    };
    dataset_title: string,
    extents: {
      maxx: number;
      maxy: number;
      minx: number;
      miny: number;
    };
    mol_modeling: boolean;
    product_type: 'localinv' | 'points' | 'range';
    public: boolean;
    release_date: any;
    status: string;
    taxa: string;
  };
}

// Interface for sending errors to the Error Subject (which essentially is just used
// to create the error modal from varying components)
interface RangeMapError {
  error: string;
  type: 'getDataset' | 'newMetadata' | 'updateMetadata' | 'datasetFinalized' | 'getUser';
  routerLink?: string[];
}

/**
 * Contains functions for sending range map GeoJSON, metadata to the API
 */
@Injectable()
export class RangeMapService {

  user_id: number = null;
  dataset_id: string = null;
  taxa: string = null;
  datasetOverview: DatasetOverview = null;
  error = false;
  species: any;

  endPoints = {
    add: 'acl/member/dataset/add',
    update: 'acl/member/dataset/update',
    upload: 'acl/member/dataset/upload',
    done: 'acl/member/dataset/done'
  };

  // stores the feature currently being drawn on range-map-draw. Re-instantiated every time the
  // user creates a new feature.
  feature: RangeMapFeatureService = null;

  // Subjects. Subscribed to by various components. For:
  // when stuff goes right
  metadataSubmitSubj = new Subject();
  metadataCreateSubj = new Subject();

  // when stuff goes wrong
  errorSubj = new Subject<RangeMapError>();

  translations: any;

  constructor(
    protected api: ApiService,
    protected auth: AuthenticationService,
    protected http: Http,
    public metadata: RangeMapMetadataService,
    public taxaService: TaxaService,
    private datasetService: DatasetService,
    public speciesDatasetService: SpeciesDatasetService,
    public translationService: TranslationService
  ) {
    this.translations = this.translationService.getTranslations();
      this.auth.getUserInfo();
      this.auth.userInfoObs.subscribe(response => {
        this.metadata.user_id = response.id.toString();
      });
      // i18n Translation: 'Get User Info Failed';
      let err = this.translationService.getTranslation(["range-map-info-modal:text","getUserInfoFailed"]);
      this.auth.userInfoErrorObs.subscribe(error => {
        this.errorSubj.next({
          error: err,
          type: 'getUser'
        });
      })
  }

  reset () {
    this.dataset_id = null;
    if (this.feature) {
      this.feature = null;
    }
  }

  /**
   * When the user navigates to the draw-map page without having gone through the metadata page,
   * grab the dataset information based on the current dataset id.
   */
  getDataset (dataset_id) {
    this.dataset_id = dataset_id;
    this.metadata.dataset_id = dataset_id;
    this.datasetService.fetchOverview(dataset_id).subscribe(overview => {
      this.datasetOverview = overview[0];
      if (this.datasetOverview && !this.datasetOverview.error) {
        this.taxa = this.datasetOverview.summary.taxa;
        this.metadata.fromOverview(this.dataset_id, this.datasetOverview);
        if (this.datasetOverview.summary.status === 'COMPLETED') {
          // i18n Translation: 'Dataset finalzied';
          let err = this.translationService.getTranslation(["range-map-info-modal:text","datasetFinalized"]);
          this.errorSubj.next({
            error: err,
            type: 'datasetFinalized',
            routerLink: ['dataset', dataset_id]
          });
          this.error = true;
        }
      } else {
        if (this.datasetOverview && this.datasetOverview.error) {
          this.errorSubj.next({
            error: this.datasetOverview.error,
            type: 'getDataset'
          });
          this.error = true;
        } else {
          // i18n Translation: 'No data returned';
          let err = this.translationService.getTranslation(["range-map-info-modal:text","noDataReturned"]);
          this.errorSubj.next({
            error: err,
            type: 'getDataset'
          });
          this.error = true;
        }
      }
    });
  }

  setSpecies (species: any) {
    if(typeof species === 'string' && species.length) {
      this.species = species;
      this.feature.setSpecies(species);
    } else if(typeof species === 'object') {
      this.species = species;
      this.feature.setSpecies(species);
    } else {
      this.species = null;
      this.feature.setSpecies(null);
    }
  }

  // On range-map-landing, when user selects a taxonomy, store changes here and in taxonomy service
  selectTaxon (taxon) {
    this.taxa = taxon.taxa;
    this.taxaService.selectTaxon(taxon.taxa);
  }

  // when drawing a new feature, instantiate a new range map feature service class
  startFeature (featureId?: string) {
    if(featureId) {
      this.feature = new RangeMapFeatureService(this.speciesDatasetService, this.datasetService, this.metadata, {
        molid: featureId
      });
    } else {
      let options = {};
      if(this.species) {
        options = {
          species: this.species
        };
      }
      this.feature = new RangeMapFeatureService(this.speciesDatasetService, this.datasetService, this.metadata, options);
    }
  }

  // check if taxa set (range-map-metadata)
  hasTaxa () {
    if (this.taxa) {
      return true;
    }
    return false;
  }

  // check if dataset_id set (range-map-draw)
  hasDatasetId () {
    if (this.dataset_id) {
      return true;
    }
    return false;
  }

  getUrl(endPoint) {
    return `${this.api.root}/${this.endPoints[endPoint]}?dataset_id=${this.dataset_id}`;
  }

  getErrorMessage (error) {
    let errorMsg = "";
    if (error.body) {
      errorMsg = JSON.parse(error._body)[0].error;
    } else if (error[0] && error[0].error) {
      errorMsg = error[0].error;
    }
    return errorMsg;
  }

  createNewDataset (dataset_title) {
    this.metadata.createEmpty(this.taxaService, dataset_title);
    this.dataset_id = this.metadata.dataset_id;

    const errorSubj = this.errorSubj;
    this.http.post(this.getUrl('add'), this.metadata.toJson(), { withCredentials: true }).subscribe(response => {
      this.datasetOverview = response[0];
      if (this.datasetOverview && this.datasetOverview.error) {
        errorSubj.next({
          error: this.datasetOverview.error,
          type: 'newMetadata'
        });
      } else {
        this.metadataCreateSubj.next(response);
      }
    }, error => {
      // i18n Translation: 'Error creating metadata';
      let err = this.translationService.getTranslation(["range-map-info-modal:text","errorCreatingMetadata"]);
      let errorMsg = this.getErrorMessage(error) ? this.getErrorMessage(error) : err;
      errorSubj.next({
        error: errorMsg,
        type: 'newMetadata'
      });
    });
    return this.metadataCreateSubj;
  }

  updateMetadata() {
    const errorSubj = this.errorSubj;
    this.http.post(this.getUrl('update'), this.metadata.toJson(), { withCredentials: true }).subscribe(response => {
      this.datasetOverview = response[0];
      if (this.datasetOverview && this.datasetOverview.error) {
        errorSubj.next({
          error: this.datasetOverview.error,
          type: 'updateMetadata'
        });
      } else {
        this.metadataSubmitSubj.next(response);
      }
    }, error => {
      // i18n Translation: 'Error saving metadata';
      let err = this.translationService.getTranslation(["range-map-info-modal:text","errorSavingMetadata"]);
      let errorMsg = this.getErrorMessage(error) ? this.getErrorMessage(error) : "Error saving metadata";
      errorSubj.next({
        error: errorMsg,
        type: 'updateMetadata'
      });
    });
    return this.metadataSubmitSubj;
  }

  // finalize dataset
  done () {
    return this.http.post(this.getUrl('done'), null, { withCredentials: true });
  }

}



