import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { InventoryMetadataService } from '../../services/metadata/inventory-metadata.service';
import { InventoryUploadService } from '../../services/upload/inventory-upload.service';
import { PointInventoryUploadService } from '../../services/upload/point-inventory-upload.service';

@Component({
  selector: 'mol-inventory-save-page',
  templateUrl: './inventory-save-page.component.html',
  styleUrls: ['./inventory-save-page.component.scss']
})
export class InventorySavePageComponent implements OnInit {

  uploadService: InventoryUploadService | PointInventoryUploadService;
  status = 'busy';
  error = '';

  constructor(
    public metadataService: InventoryMetadataService,
    public inventoryUploadService: InventoryUploadService,
    public pointInventoryUploadService: PointInventoryUploadService,
    private router: Router
  ) {
    let validPointInventory = (this.metadataService.geometryType === 'coordinates' && this.pointInventoryUploadService.isValid());
    let validInventory = (this.metadataService.geometryType === 'shapefile' && this.inventoryUploadService.isValid());

    if(this.metadataService.geometryType === 'coordinates' && validPointInventory) {
      this.uploadService = this.pointInventoryUploadService;
    } else if (this.metadataService.geometryType === 'shapefile' && validInventory) {
      this.uploadService = this.inventoryUploadService;
    }

    if (!(validPointInventory || validInventory)) {
      this.router.navigate(['files']);
    }
  }

  ngOnInit() {
    this.uploadService.create().subscribe(
      success => { this.status = 'success'; },
      error => {
        let obj = error.json();
        this.status = 'error';
        this.error = (obj[0] && obj[0].error) || 'server error';
    });
  }
}



